import { Component, OnInit, ViewContainerRef } from '@angular/core';

import { ToastsManager } from 'ng2-toastr/ng2-toastr';

import { CompanyService } from '../../company/company.service';
import { ModelsService } from '../models.service';
import { RatingModel, RatingSubmodel, RatingCalcRefPd } from '../entities';
import { UserService } from '../../user/user.service';
import { MsgService } from 'app/core/msg.service';

@Component({
  selector: 'app-model-config',
  templateUrl: './model-config.component.html',
  styleUrls: ['./model-config.component.css']
})
export class ModelConfigComponent implements OnInit {

  allModels;
  exposures;
  selectedExposures;

  private mainScale;
  private previousModel;
  model: RatingModel;
  submodel: RatingSubmodel;
  showingSub = false;
  canDelete = false;

  private isModelNew = false;
  disableSave = false;
  // private isSubmodelNew = false;
  // private isCalcRefPdNew = false;

  hasEditPrivilege = false;

  constructor(private modelsService: ModelsService, private companyService: CompanyService, private toastr: ToastsManager,
    private msg: MsgService, private userService: UserService) {
    this.hasEditPrivilege = userService.hasPrivilege(userService.modelEdit);
  }

  ngOnInit() {
    this.getIndexModels();
    this.getExposures();
    this.getScale();
  }

  private getIndexModels() {
    this.modelsService.getRatingModels().subscribe(
      data => {
        this.allModels = data[0].clientModel;
        this.allModels.forEach(model => {
          model.activeModel = model.models.find(v => v.isActive === 1);
          model.isCollapsed = true;
        });
      },
      error => console.log('getIndexModels error:', error)
    );
  }

  private getExposures() {
    this.companyService.getExposures().subscribe(
      data => {
        this.exposures = data.map(it => ({ id: it.id, text: it.exposure }));
      },
      error => console.log('getExposures error:', error)
    );
  }

  private getScale() {
    this.modelsService.getScale().subscribe(
      data => {
        this.mainScale = data;
      },
      error => console.log('getScale error:', error)
    );
  }

  scaleChanged() {
    this.model.ratingCalcRefPd.length = 0;
    if (this.model.msType === 'DIRECT_MAPPING') {
      this.model.ratingCalcRefPd = this.mainScale.map(it => {
        let r = new RatingCalcRefPd();
        r.rating = it.rating;
        r.ratingMasterScaleId = it.id;
        r.midPd = it.midPd;
        return r;
      });
    }
  }

  toggleCollapsed(model) {
    model.isCollapsed = !model.isCollapsed;
    if (this.previousModel && this.previousModel !== model) { this.previousModel.isCollapsed = true; }
    if (model.isCollapsed === false) {
      this.previousModel = model;
    }
  }
  getTypeDisplay(code) {
    if (code === 'CONTINUOUS') { return '逻辑转换'; }
    if (code === 'DISCRETE_QUAN') { return '分档转换'; }
    if (code === 'DISCRETE') { return '分档转换'; }
  }

  getModel(model) {
    this.model = null;
    this.canDelete = false;
    this.modelsService.getModel(model.id).subscribe(data => {
      this.model = data;
      this.getSelectedExposures(this.model);
      this.isModelNew = false;
      this.showingSub = false;
      this.model.ratingCalcRefPd = this.model.ratingCalcRefPd.sort(this.model.msType === 'DIRECT_MAPPING' ? (a, b) => {
        if (+a.small < +b.small) { return 1; }
        if (+a.small > +b.small) { return -1; }
        return 0;
      } : (a, b) => {
        if (a.small == null || a.small as any === '') {
          return -1;
        }
        if (b.small == null || b.small as any === '') {
          return 1;
        }
        if (+a.small < +b.small) { return -1; }
        if (+a.small > +b.small) { return 1; }
        return 0;
      });
    });
    this.modelsService.getHasUsed(model.id).subscribe(used => this.canDelete = !used);
  }

  newModel() {
    this.model = new RatingModel();
    this.isModelNew = true;
    this.showingSub = false;
    this.canDelete = false;
    this.selectedExposures = null;
  }

  newSubmodel() {
    if (this.model.exposures.length === 0) {
      this.toastr.error('请先选择适用敞口');
      return;
    }
    this.showingSub = true;
    this.submodel = new RatingSubmodel();
  }

  getSelectedExposures(model) {
    this.selectedExposures = model.exposures && model.exposures.map(it => ({ id: it.exposureSid, text: it.exposure }));
  }

  exposureSelected(data) {
    this.model.exposures.push({ exposureSid: data.id, exposure: data.text, modelId: this.model.id });
    this.getSelectedExposures(this.model);
  }

  exposureRemoved(data) {
    this.model.exposures.splice(this.model.exposures.findIndex(it => it.exposureSid === data.id), 1);
    this.getSelectedExposures(this.model);
  }

  editSubmodel(item) {
    this.showingSub = true;
    this.submodel = item;
  }

  removeSubmodel(item) {
    this.model.ratingSubModels.splice(this.model.ratingSubModels.indexOf(item), 1);
  }

  addCalcRefPd() {
    this.model.ratingCalcRefPd.push(new RatingCalcRefPd());
  }

  removeCalcRefPd(item) {
    this.model.ratingCalcRefPd.splice(this.model.ratingCalcRefPd.indexOf(item), 1);
  }

  private valid(m: RatingModel) {
    let msg = null;
    let empty = (v) => v == null || v.length === 0;
    let scaleMapInvalid = () => {
      let scales = m.ratingCalcRefPd;
      if (m.msType === 'DIRECT_MAPPING') {
        let preMin = null;
        for (let i = 0; i < scales.length; i++) {
          if (empty(scales[i].small) || empty(scales[i].large)) {
            msg = '最大值和最小值为必填项';
            return;
          }
          if ((!empty(scales[i].small) && /^[+-]?\d{0,16}(\.\d{1,16})?$/.test(scales[i].small.toString()) === false) ||
            (!empty(scales[i].large) && /^[+-]?\d{0,16}(\.\d{1,16})?$/.test(scales[i].large.toString()) === false)) {
            msg = '最大值和最小值只能输入数值，且长度控制在16（小数点前）+16（小数点后）个数值内，可以为负；';
            return;
          }
          if (i !== 0 && scales[i].large !== preMin) {
            return true;
          }
          if (empty(scales[i].small) || empty(scales[i].large) || Number(scales[i].small) >= Number(scales[i].large)) {
            return true;
          }
          preMin = scales[i].small;
        }
      } else {
        let preMax = null;
        for (let i = 0; i < scales.length; i++) {
          if (empty(scales[i].parameterA) || empty(scales[i].parameterB)) {
            msg = '转换参数a和转换参数b为必填项';
            return;
          }
          if ((!empty(scales[i].small) && /^[+-]?\d{0,16}(\.\d{1,16})?$/.test(scales[i].small.toString()) === false)
            || (!empty(scales[i].large) && /^[+-]?\d{0,16}(\.\d{1,16})?$/.test(scales[i].large.toString()) === false) ||
            (/^[+-]?\d{0,16}(\.\d{1,16})?$/.test(scales[i].parameterA.toString()) === false) ||
            (/^[+-]?\d{0,16}(\.\d{1,16})?$/.test(scales[i].parameterB.toString()) === false)) {
            msg = '主标尺映射所有字段只能输入数值，且长度控制在16（小数点前）+16（小数点后）个数值内，可以为负；';
            return;
          }
          if (i !== 0 && scales[i].small !== preMax) {
            return true;
          }
          if (!empty(scales[i].small) && !empty(scales[i].large) && +scales[i].small >= +scales[i].large) {
            return true;
          }
          preMax = scales[i].large;
        }
      }
    };
    if (empty(m.code)) {
      msg = '模型代码为必填';
    } else if (/^[0-9a-zA-Z_]{0,20}$/.test(m.code) === false) {
      msg = '模型代码只能输入数字和字母；不能有空格；限制在20个字符之内；';
    } else if (empty(m.name)) {
      msg = '模型名称为必填';
    } else if (/^\S{0,30}$/.test(m.name) === false) {
      msg = '模型名称不能有空格，限制在30个字符之内；';
    } else if (empty(m.type)) {
      msg = '请选择模型类型';
    } else if (!m.exposures.length) {
      msg = '请选择敞口';
    } else if (!m.ratingSubModels.length) {
      msg = '请至少添加一个子模型';
    } else if (!m.ratingCalcRefPd.length) {
      msg = '请添加主标尺映射策略';
    } else {
      let ratioSum = m.ratingSubModels.reduce((a, b) => a + (+b.ratio), 0);
      if (ratioSum < 0.99 || ratioSum > 1.01) {
        msg = '子模型权重加和必需约等于1';
      } else if (scaleMapInvalid()) {
        msg = '主标尺映射配置错误';
      } else {
        if (m.ratingSubModels.some(it => it.base === 1) &&
        m.ratingSubModels.some(it => empty(it.meanValue) || empty(it.sdValue))) {
          msg = '有基准子模型时，均值和标准差为必填';
        }
      }
    }
    if (msg === null) { return true; }
    this.toastr.error(msg, '验证失败');
    return false;
  }

  saveModel() {
    this.model.ratingCalcRefPd.forEach(it => {
      let s = it as any;
      it.formula = null;
      // tslint:disable-next-line:no-unused-expression
      s.small && s.small.trim && (s.small = s.small.trim());
      // tslint:disable-next-line:no-unused-expression
      s.large && s.large.trim && (s.large = s.large.trim());
      // tslint:disable-next-line:no-unused-expression
      s.parameterA && s.parameterA.trim && (s.parameterA = s.parameterA.trim());
      // tslint:disable-next-line:no-unused-expression
      s.parameterB && s.parameterB.trim && (s.parameterB = s.parameterB.trim());
    });
    if (!this.valid(this.model)) { return; }
    this.disableSave = true;
    this.model.ratingSubModels.forEach((it, index) => { it.priority = index + 1; });
    this.model.isActive = 0; // this.model.id == null ? 1 : 0;
    this.modelsService.saveModel(this.model, this.model.id == null).subscribe(data => {
      this.getModel(data);
      this.toastr.success('保存成功').then(it => this.disableSave = false);
      this.getIndexModels();
    },
      error => {
        this.toastr.error('保存失败').then(it => this.disableSave = false);
      });
  }

  publishModel() {
    if (this.model.isActive === 1) {
      this.msg.confirm('请确认是否停用该版本？').result
        .then(result => {
          this.modelsService.revoke(this.model).subscribe(data => {
            this.model.isActive = 0;
            this.allModels.find(it => it.modelName === this.model.name).activeModel = null;
            this.toastr.success('成功停用该版本');
          }, error => {
            this.toastr.error('停用该版本失败');
          });
        });
    } else {
      let errMsg: string;
      const exposureUsed = this.model.exposures.some((exp) => {
        return this.allModels.some(m => {
          errMsg = `敞口：${exp.exposure}已在模型：${m.modelName}中使用`;
          return m.modelName !== this.model.name && m.activeModel && m.activeModel.exposures.some(e => e.exposureSid === exp.exposureSid);
        });
      });
      if (exposureUsed) {
        this.toastr.error(errMsg);
        return;
      }
      this.msg.confirm('启用该版本会停用正在使用中的版本，请确认是否启用该版本？').result
        .then(result => {
          this.modelsService.publish(this.model).subscribe(data => {
            this.model.isActive = 1;
            this.allModels.find(it => it.modelName === this.model.name).activeModel = this.model;
            this.toastr.success('成功启用该版本');
          }, error => {
            this.toastr.error('启用该版本失败');
          });
        });
    }
  }

  deleteModel() {
    let msg = this.model.isActive === 1 ? '此模型版本是启用状态，请确认是否删除？' : '请确认是否删除此版本?';
    this.msg.confirm(msg).result
      .then(result => {
        this.modelsService.deleteModel(this.model).subscribe(data => {
          this.getIndexModels();
          this.model = null;
          this.toastr.success('成功删除该版本');
        }, error => {
          this.toastr.error('删除该版本失败');
        });
      });
  }

}




