import { BaseClass } from '../base-class';
import { Exposure } from './exposure';
import { Security } from './security';
import { InnerRating } from './inner-rating';
import { BaseRating, RatingBrief } from './base-rating';

/**
 * @author taohui
 *
 * 企业基本信息实体
 */
export class Company extends BaseClass {
  id: number; // 企业标识符
  name: string; // 企业名称
  foundDate: string; // 成立时间
  regDate: string; // 注册时间
  regAddress: string; // 注册地址
  officeAddress: string; // 办公地址
  officePostCode: number; // 公司所在地邮编
  regCapital: number; // 注册资本
  currency: string; // 币种
  legRepresent: string; // 法人
  chairman: string; // 董事长
  gmanager: string; // 总经理
  bsecretary: string; // 董事长秘书
  businScope: string; // 经营范围
  mainBusion: string; // 主营产品
  exposure: Exposure; // 行业
  orgFormId: number; // 企业性质id
  orgFormName: string; // 企业性质名称
  securityList: Security[]; // 企业股票
  innerRating: InnerRating; // 企业内评
  outerRating: BaseRating; // 企业外评
  web: string; // 网站地址
  country: string; // 国家
  region: number; // 地区
  city: number; // 城市
  regionCode: number; // 所属地方政府级别
  companyPhone: number; // 公司电话
  companyFax: number; // 公司传真
  comanyEmail: string; // 公司电子邮箱
  blnumb: number; // 营业执照号码
  ntrnum: number; // 国税登记号码
  ltrnum: number; // 地税登记号码
  orgnum: number; // 信用代码
  srcCode: string; // 源系统
  accountingFirm: string; // 会计师事务所
  legaladvisor: string; // 法律顾问
  companyState: number; // 公司状态
  employNum: string; // 职工人数
  affilparties: AffilParty[]; // 实际控制人/第一大股东
  ratingBriefVO: RatingBrief;

  generateByRaw(raw: any) {
    super.generateByRaw(raw);
    if (raw.exposure) {
      this.exposure = new Exposure();
      this.exposure.generateByRaw(raw.exposure);
    }
    if (raw.innerRating) {
      this.innerRating = new InnerRating();
      this.innerRating.generateByRaw(raw.innerRating);
    }
    if (raw.outerRating) {
      this.outerRating = new BaseRating();
      this.outerRating.generateByRaw(raw.outerRating);
    }
    this.securityList = [];
    if (raw.securityList && Array.isArray(raw.securityList) && raw.securityList.length > 0) {
      for (let item of raw.securityList) {
        let security = new Security();
        security.generateByRaw(item);
        this.securityList.push(security);
      }
    }
  }
}


export class CompanyRegion extends BaseClass {
  regionType: number;  // 个数
  regionCodeLevel1: number;
  regionNameLevel1: string; // 省
  regionCodeLevel2: number;
  regionNameLevel2: string; // 市
  regionCodeLevel3: number;
  regionNameLevel3: string; // 区
}

export class ShareHolder extends BaseClass {
  endDate: string;
  endYear: string;
  endMonth: string;
  endDay: string;
  items: ShareHolderItem[];
  isPlus: boolean;
}
export class ShareHolderItem extends BaseClass {
  shareholderName: string; // 股东名称
  shareholderType: string; // 股东性质
  shareholderNum: number; // 直接持股数量
  shareholderRatio: number; // 占股比例
  shareType: string; // 股份性质
  changeAmount: string;
  status: number;
  changeRatio: number;
}

export class AffilParty extends BaseClass {
  affilParty: string; // 实际控制人/第一大股东
  relationType: string; // 标记affilParty是实际控制人还是第一大股东
  inoShaRatio: number; // 第一大股东持股比例
}



