import {Component, OnInit, Output, Input, EventEmitter} from '@angular/core';

import {ToastsManager} from 'ng2-toastr/ng2-toastr';

import {RatingModel, RatingSubmodel, ModelFactor} from '../../../entities';
import {ModelsService} from '../../../models.service';
import {UserService} from '../../../../user/user.service';

@Component({
  selector: 'app-submodel-config',
  templateUrl: './submodel-config.component.html',
  styleUrls: ['./submodel-config.component.css']
})
export class SubmodelConfigComponent implements OnInit {

  private origSubmodel;
  private submodelCopy;

  private origFactor;
  private factorCopy;

  allFactors;
  factorMeta;
  hasEditPrivilege = false;
  @Output() finish = new EventEmitter<boolean>();
  @Input() model: RatingModel;

  @Input()
  set submodel(value: RatingSubmodel) {
    this.origSubmodel = value;
    this.submodelCopy = JSON.parse(JSON.stringify(value));
  }

  get submodel() {
    return this.submodelCopy;
  }

  isCollapsed = true;

  set factor(value: ModelFactor) {
    this.origFactor = value;
    this.factorCopy = JSON.parse(JSON.stringify(value));
  }

  get factor() {
    return this.factorCopy;
  }

  constructor(private modelsService: ModelsService, private toastr: ToastsManager,
              private userService: UserService) {
    this.hasEditPrivilege = userService.hasPrivilege(userService.modelEdit);
  }

  ngOnInit() {
    this.getFactorsMeta();
  }

  subModelTypeChanged() {
    this.getFactorsMeta();
  }

  private getFactorsMeta() {
    let ob;
    if (this.submodel.type === 'CONTINUOUS') {
      ob = this.modelsService.getFactorsQuan(this.model.exposures.map(it => it.exposureSid));
      this.submodel.parameter1 = 'X=1/(1 + exp (logit_a + logit_b * _values)) ';
      this.submodel.parameter2 = 'Y=(X-avg) / std_dev';
    } else if (this.submodel.type === 'DISCRETE_QUAN') {
      ob = this.modelsService.getFactorsDiscreteQuan(this.model.exposures.map(it => it.exposureSid));
      this.submodel.parameter1 = '';
      this.submodel.parameter2 = '';
    } else {
      ob = this.modelsService.getFactorsDiscrete(this.model.exposures.map(it => it.exposureSid));
      this.submodel.parameter1 = '';
      this.submodel.parameter2 = '';
    }
    ob.subscribe(
      data => {
        this.allFactors = data;
        this.allFactors.forEach(this.transferItem);
      },
      error => console.log('getFactorsMeta error:', error)
    );
  }

  private transferItem(it) {
    if (it == null) {
      return {id: '', text: ''};
    }
    it.id = it.factorCode;
    it.text = `<span style="color:#49b6d6">类别：</span><span style="display:inline-block; width:8rem">${it.factorType}</span>
          <span style="color:#49b6d6">名称：</span><span style="display:inline-block; width:16rem">${it.factorName}</span>
          <span style="color:#49b6d6">代码：</span>${it.factorCode}`;
    if (it.formulaCh) {
      it.text += `<div style="white-space: normal;"><span style="color:#49b6d6">描述公式：</span>${it.formulaCh}</div><hr>`;
    }
    if (it.description) {
      it.text += `<div style="white-space: normal;"><span style="color:#49b6d6">描述公式：</span>${it.description}</div><hr>`;
    }
    return it;
  }

  cancel() {
    this.finish.emit(false);
  }

  private valid(m: RatingSubmodel) {
    let msg = null;
    let empty = (v) => v == null || v.length === 0;
    if (empty(m.name)) {
      msg = '子模型名称为必填';
    } else if (/^\S{0,30}$/.test(m.name) === false) {
      msg = '子模型名称不能有空格，限制在30个字符之内；';
    } else if (empty(m.ratio)) {
      msg = '子模型权重为必填';
    } else if (/^[+-]?((0\.\d+)|1)$/.test(m.ratio.toString()) === false) {
      msg = '子模型权重必须是0-1之间的数字；';
    } else if (empty(m.type)) {
      msg = '子模型类型为必填';
    } else if (empty(m.intercept)) {
      msg = '参数截距为必填';
    } else if (/^[+-]?\d+(\.\d+)*$/.test(m.intercept.toString()) === false) {
      msg = '截距只能为数字；';
    } else if (m.ratingModelFactors.length === 0) {
      msg = '请至少要设置一个指标';
    }

    if (msg === null) {
      return true;
    }
    this.toastr.error(msg, '验证失败');
    return false;
  }

  save() {
    if (!this.valid(this.submodel)) {
      return;
    }

    let index = this.model.ratingSubModels.indexOf(this.origSubmodel);
    this.submodel.ratingModelId = this.model.id;
    if (index === -1) {
      this.model.ratingSubModels.push(this.submodel);
    } else {
      if (this.submodel.base) { // 设为基准 需要把其它取消基准
        this.model.ratingSubModels.forEach(it => it.base = 0);
        this.submodel.base = 1;
      } else {
        this.submodel.base = 0;
      }
      this.model.ratingSubModels[index] = this.submodel;
    }
    this.finish.emit(true);
  }

  newFactor() {
    if (!this.submodel.type) {
      this.toastr.error('请先选择子模型类型');
      return;
    }
    this.isCollapsed = false;
    this.factor = new ModelFactor();
  }

  removeFactor(item) {
    this.submodel.ratingModelFactors.splice(this.submodel.ratingModelFactors.indexOf(item), 1);
  }

  editFactor(item) {
    this.isCollapsed = false;
    this.factor = item;
    this.factorMeta = this.allFactors.find(it => it.factorCode === item.factorCode);
  }

  private indexValid(n: ModelFactor) {
    let msg = null;
    let empty = (v) => v == null || v.length === 0;
    if (this.submodel.type !== 'CONTINUOUS') {
      if (typeof this.factorMeta === 'undefined' || empty(n.param1) ||
        (this.factorMeta.options[0] && empty(n.param2)) || (this.factorMeta.options[1] && empty(n.param3)) ||
        (this.factorMeta.options[2] && empty(n.param4)) || (this.factorMeta.options[3] && empty(n.param5)) ||
        (this.factorMeta.options[4] && empty(n.param6)) || (this.factorMeta.options[5] && empty(n.param7))
      ) {
        msg = '指标信息所有选项均为必填项';
      } else if (/^[+-]?\d{0,16}(\.\d{1,16})?$/.test(n.param1.toString()) === false) {
        msg = '指标系数只能输入数值，且长度控制在16（小数点前）+16（小数点后）个数值内，可以为负；';
      } else {
        let numberControl = '指标下的所有字段只能输入数值，且长度控制在16（小数点前）+16（小数点后）个数值内，可以为负；';
        if (this.factorMeta.options[0] && /^[+-]?\d{0,16}(\.\d{1,16})?$/.test(n.param2.toString()) === false) {
          msg = numberControl;
        } else if (this.factorMeta.options[1] && /^[+-]?\d{0,16}(\.\d{1,16})?$/.test(n.param3.toString()) === false) {
          msg = numberControl;
        } else if (this.factorMeta.options[2] && /^[+-]?\d{0,16}(\.\d{1,16})?$/.test(n.param4.toString()) === false) {
          msg = numberControl;
        } else if (this.factorMeta.options[3] && /^[+-]?\d{0,16}(\.\d{1,16})?$/.test(n.param5.toString()) === false) {
          msg = numberControl;
        } else if (this.factorMeta.options[4] && /^[+-]?\d{0,16}(\.\d{1,16})?$/.test(n.param6.toString()) === false) {
          msg = numberControl;
        } else if (this.factorMeta.options[5] && /^[+-]?\d{0,16}(\.\d{1,16})?$/.test(n.param7.toString()) === false) {
          msg = numberControl;
        }

      }
      if (msg === null) {
        return true;
      }
      this.toastr.error(msg, '验证失败');
      return false;
    } else if (this.submodel.type === 'CONTINUOUS') {
      if (typeof this.factorMeta === 'undefined' || empty(n.param1) || empty(n.param2)
        || empty(n.param3) || empty(n.param4) || empty(n.param5) || empty(n.param6) || empty(n.param7)
      ) {
        msg = '指标信息所有选项均为必填项';
      } else if ((/^[+-]?\d{0,16}(\.\d{1,16})?$/.test(n.param1.toString()) === false) ||
        (/^[+-]?\d{0,16}(\.\d{1,16})?$/.test(n.param2.toString()) === false) ||
        (/^[+-]?\d{0,16}(\.\d{1,16})?$/.test(n.param3.toString()) === false) ||
        (/^[+-]?\d{0,16}(\.\d{1,16})?$/.test(n.param4.toString()) === false) ||
        (/^[+-]?\d{0,16}(\.\d{1,16})?$/.test(n.param5.toString()) === false) ||
        (/^[+-]?\d{0,16}(\.\d{1,16})?$/.test(n.param6.toString()) === false) ||
        (/^[+-]?\d{0,16}(\.\d{1,16})?$/.test(n.param7.toString()) === false)) {
        msg = '指标下的所有字段只能输入数值，且长度控制在16（小数点前）+16（小数点后）个数值内，可以为负；';
      }
      if (msg === null) {
        return true;
      }
      this.toastr.error(msg, '验证失败');
      return false;
    }

  }

  saveFactor() {
    if (!this.indexValid(this.factor)) {
      return;
    }

    let index = this.submodel.ratingModelFactors.indexOf(this.origFactor);
    this.factor.factorCode = this.factorMeta.factorCode;
    this.factor.factorName = this.factorMeta.factorName;
    this.factor.subModelId = this.submodel.id;
    this.factor.methodType = this.submodel.type;
    if (index === -1) {
      this.submodel.ratingModelFactors.push(this.factor);
    } else {
      this.submodel.ratingModelFactors[index] = this.factor;
    }
    this.isCollapsed = true;
  }

  factorSelected(item) {
    this.factorMeta = this.allFactors.find(it => it.factorCode === item.id);
  }
}



