import { Injectable } from '@angular/core';
import { Modal, DropInPreset } from 'ngx-modialog/plugins/vex';
import { DialogRef } from 'ngx-modialog';

@Injectable()
export class MsgService {
  constructor(public modal: Modal) {}

  alert(message: string, timeout: number = 1500): DialogRef<DropInPreset> {
    const ref = this.modal
      .alert()
      .className('plain')
      .showCloseButton(timeout <= 0)
      .message(message)
      .okBtn(null)
      .open();
    if (timeout > 0) setTimeout(() => ref.close(), timeout);
    return ref;
  }

  confirm(
    message: string,
    okBtn = '是',
    cancelBtn = '否',
    showCloseButton = true
  ): DialogRef<DropInPreset> {
    return this.modal
      .confirm()
      .className('plain')
      .showCloseButton(showCloseButton)
      .message(message)
      .okBtn(okBtn)
      .cancelBtn(cancelBtn)
      .open();
  }
}



