import { InnerRating } from './inner-rating';
import { BaseClass } from '../base-class';
import { Ultility } from '../../ultility';
export class InnerRatingHistory extends BaseClass {
  reportDate: string;
  innerRatingVos: InnerRating[];
  generateByRaw(raw: any) {
    super.generateByRaw(raw);
    this.innerRatingVos = [];
    if (Ultility.arrayIsNotEmpty(raw.innerRatingVos)) {
      for (let item of raw.innerRatingVos) {
        let innerRating = new InnerRating();
        innerRating.generateByRaw(item);
        this.innerRatingVos.push(innerRating);
      }
    }
  }
}



