import { Injectable } from '@angular/core';
import { Portfolio } from '../shared';
import { UserService } from 'app/user/user.service';
import { HttpClient } from '@angular/common/http';
import { DefaultPortfolioRating } from 'app/overview/rate-comparition';
@Injectable()
export class PortfolioService {
  private portfolioApiPrefix = '/portfolios';
  constructor(private http: HttpClient, private userService: UserService) { }
  /**
   * 删除投资组合
   */
  delPortfolio(portfolioId: number) {
    return this.http.get(this.portfolioApiPrefix + `/${portfolioId}`);
  }

  /**
   * 获取组合列表
   */
  getPortfolios(portfolioType: number) {
    return this.http.get<any>(this.portfolioApiPrefix + `/user/${this.userService.CurrentUser.userName}/type/${portfolioType}`);
  }

  /**
   * 获取组合里的所有公司信息
   */
  getPortfolioItems(portfolioId: number) {
    return this.http.get<any>(this.portfolioApiPrefix + `/${portfolioId}`);
  }

  /**
   * 获取默认组合内外评对比
   */
  getDefaultPofolioRatingComparition() {
    return this.http.get<DefaultPortfolioRating>(this.portfolioApiPrefix + '/defaultPortfolioRating');
  }

  /**
   * 检查组合名称是否已经存在
   */
  checkIsNameExist(portfolioName: string, type: string) {
    return this.http.get(this.portfolioApiPrefix + `/${portfolioName}/${type}/exist`);
  }

  /**
   * 新增投资组合
   */
  addPortfolio(portfolio: Portfolio) {
    return this.http.post(this.portfolioApiPrefix + '/', portfolio);
  }

  /**
   * 更新组合里企业
   */
  updatePortfolioItems(portfolioId: number, data: Portfolio) {
    return this.http.put(this.portfolioApiPrefix + `/${portfolioId}`, data);
  }

  /**
   * 导出
   */
  exportPortfolio(portfolioId: number) {
    return this.http.get(this.portfolioApiPrefix + `/${portfolioId}/excel`, {observe: 'response', responseType: 'blob'});
  }
  /**
   * 根据用户信息查询用户创建的静态组合
   */
  getPortfoliolist(type: number) {
    return this.http.get(this.portfolioApiPrefix + `/portfolioType/${type}`);
  }
}



