import { HttpClient } from '@angular/common/http';
import { Injectable, Optional } from '@angular/core';
import { Http, RequestMethod, Response } from '@angular/http';
import { Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { User, UserPassword, SystemConfig } from '../shared/entities/user';

export type CheckUserField = 'userName' | 'phone' | 'displayName' | 'email';

@Injectable()
export class AdminService {
  constructor(private http: HttpClient) {}

  // 获取所有的用户
  searchUsers(page: number, size: number) {
    return this.http.get('/users', {
      params: { page: page.toString(), size: size.toString() }
    });
  }

  // 新增用户
  addUser(newUser: User) {
    return this.http
      .post('/user', newUser);
  }

  // 获取当前用户
  getCurrentUser(userName: string) {
    return this.http
      .get(`/user/${userName}`);
  }

  // 编辑
  editUser(userName: string, newUser: User) {
    return this.http
      .put(`/user/${userName}`, newUser);
  }

  // 删除
  delUser(userName: string) {
    return this.http.delete(`/user/${userName}`, {observe: 'response'});
  }

  checkUser(
    userName: string,
    field: CheckUserField,
    fieldVale: string
  ): Observable<boolean> {
    let search: any = { [field]: fieldVale };
    if (userName != null && userName.length > 0) {
      search.userName = userName;
    }
    return this.http
      .get<boolean>(`/user/${field}/exist`, { params: search });
  }

  // 查询用户拥有的角色
  getUserRoles() {
    return this.http.get<any>('/roles');
  }

  // 添加用户角色
  addUserRoles(roleName: string) {
    return this.http
      .post(`/roles/${roleName}`, roleName);
  }

  // 编辑用户角色'/{roleName}/to/{newRoleName}'
  editUserRoles(roleName: string, newRoleName: string) {
    return this.http
      .put(`/roles/${roleName}/to/${newRoleName}`, null);
  }

  // 获取current用户所有权限
  getPrivilegesAssign() {
    return this.http
      .get<any>('/privilegesAssigned');
  }

  // get privileges asignable for role
  getRolePrivilegesAssign(roleName: string) {
    return this.http
      .get<any>(`/roles/${roleName}/privilegesAssigned`);
  }

  // 查看相应的角色拥有的权限
  getUserPrivileges(roleName: string) {
    return this.http
      .get<any>(`/roles/${roleName}/privileges`);
  }

  // 删除用户角色
  delUserRoles(roleName: string) {
    return this.http
      .delete(`/roles/${roleName}`);
  }

  // 改变角色权限
  toggleRolePrivilege(roleName: string, data: any, add: boolean) {
    let type = add ? 1 : 0;
    return this.http
      .put(`/roles/${roleName}/privileges/${type}`, data);
  }

  // 改变角色权限
  toggleRolePrivilegeAssign(roleName: string, data: any, add: boolean) {
    let type = add ? 1 : 0;
    return this.http
      .put(`/roles/${roleName}/privilegesAssigned/${type}`, data);
  }

  // 获取发起评级时的认定人列表
  getHasPrivilegeUsers(privilegeType: string, privilege: string) {
    return this.http
      .get<User[]>(`/users/privilegeTypes/${privilegeType}/privileges/${privilege}`);
  }

  // 修改用户密码
  editUserPassword(userPassword: UserPassword) {
    return this.http
      .put('/users/currentUser/password', userPassword);
  }

  // 重置用户密码
  resetUserPassword(userName: string) {
    return this.http
      .put(`/users/${userName}/password`, null);
  }
  // 获取系统配置
  getSystemConfig(name: string) {
    return this.http
      .get<SystemConfig>(`/systemConfig/?name=${name}`);
  }
  // 设置配置
  setSystemConfig(systemConfig: SystemConfig[]) {
    return this.http
      .post<any>('/systemConfig/', systemConfig);
  }

  getExposureWarningOwners() {
    return this.http
      .get<any>('/exposureOwners/warings');
  }

  getExposureRatingOwners() {
    return this.http
      .get<any>('/exposureOwners/ratings');
  }

  updateExposureWarningOwners(owners) {
    return this.http.post('/exposureOwners/warings', owners);
  }

  updateExposureRatingOwners(owners) {
    return this.http.post('/exposureOwners/ratings', owners);
  }
}



