import {
  Component,
  OnInit,
  OnDestroy,
  ViewChild,
  TemplateRef,
  trigger,
  state,
  style,
  transition,
  animate
} from '@angular/core';
import { NgClass } from '@angular/common';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { Observer } from 'rxjs/Observer';
import { Subscription } from 'rxjs/Subscription';
import { Subject } from 'rxjs/Subject';

import { AdminService } from './admin.service';
import { User, PagedResult } from '../shared';

import { UserService } from '../user/user.service';
import { MsgService } from 'app/core/msg.service';

@Component({
  selector: 'app-admin',
  templateUrl: 'admin.component.html',
  styleUrls: ['admin.component.css']
})
export class AdminComponent implements OnInit {
  loadingStatus: boolean;
  isCollapsed = true; // 弹出框的状态
  allUsersList: User[];
  currentUser: User;
  userName: string;
  email: string;
  phone: string;
  userSearchResult: PagedResult<User>;
  isSearchUser: boolean;
  itemsPerPage = 10;
  currentPage = 1;
  rolesList;
  newPwd: string;
  addNewFlag: boolean;
  isSaving: boolean;

  constructor(
    private router: Router,
    private activeRoute: ActivatedRoute,
    private adminService: AdminService,
    public userService: UserService,
    private msg: MsgService
  ) {}

  ngOnInit() {
    this.allUsersList = [];
    this.rolesList = [];
    this.searchUser();
    this.currentUser = new User();
    // 初始化分页绑定实体对象
    this.userSearchResult = new PagedResult<User>();
    this.userSearchResult.total = 0;
    this.userSearchResult.items = [];
    this.isCollapsed = true; // 弹出框的默认状态
    this.getUserRoles();
  }

  // 新增按钮点击事件
  private addUsser() {
    this.addNewFlag = true;
    this.isCollapsed = false;
    this.currentUser = new User(); // 初始化对象 清空对象数据
    this.currentUser.userName = '';
  }

  // 编辑用户
  editUser(userName) {
    this.addNewFlag = false;
    this.isCollapsed = false;
    this.userName = userName;
    // 根据记录标示 调用api获取对应数据
    this.adminService
      .getCurrentUser(userName)
      .subscribe(
        data => this.generateUser(data),
        error => console.log('error', error)
      );
  }

  // 取消保存按钮点击事件
  cancelSave() {
    this.isCollapsed = true;
  }

  // 保存用户按钮点击事件
  saveUser() {
    if (this.currentUser.clientId > 0) {
      // 编辑保存事件
      this.adminService.editUser(this.userName, this.currentUser).subscribe(
        data => {
          this.msg.alert('保存成功');
          this.isCollapsed = true; // 关闭录入框
          this.searchUser(); // 刷新列表
        },
        error => console.log('error', error)
      );
    } else {
      // 新增保存事件
      this.isSaving = true;
      this.adminService.addUser(this.currentUser).subscribe(
        data => {
          this.isCollapsed = true; // 关闭录入框
          this.msg.alert(`成功创建新用户${this.currentUser.userName}，初始登录密码为1234`, 0);
          this.searchUser(); // 刷新列表
        },
        error => {
          console.log('error', error);
          this.msg.alert('保存失败');
        },
        () => this.isSaving = false
      );
    }
  }

  // 分页页码改变事件
  pageChanged($event) {
    this.searchUser($event.page);
  }

  // 客户查询方法
  searchUser(page: number = 1) {
    this.isSearchUser = true;
    this.currentPage = page;
    this.adminService
      .searchUsers(this.currentPage - 1, this.itemsPerPage)
      .subscribe(
        data => {
          this.generateAllUsers(data);
        },
        error => console.log('error:', error),
        () => {
          this.loadingStatus = false;
        }
      );
  }

  // 赋值对象
  generateUser(data) {
    this.currentUser = new User();
    if (data) {
      this.currentUser.generateByRaw(data);
    }
  }

  // 赋值列表
  generateAllUsers(userSearchResult) {
    this.userSearchResult = new PagedResult<User>();
    this.userSearchResult.total = userSearchResult.total;
    let userBasicinfos: User[] = [];
    if (userSearchResult.items == null) {
      userSearchResult.items = [];
    }
    for (let item of userSearchResult.items) {
      let userBasicinfoItem = new User();
      userBasicinfoItem.generateByRaw(item);
      userBasicinfos.push(userBasicinfoItem);
    }
    this.userSearchResult.items = userBasicinfos;
  }

  // 删除用户
  delUser(userName) {
    this.userName = userName;
    const res = this.msg.confirm('确定删除吗?').result;
    res.then(result => {
      this.adminService.delUser(this.userName).subscribe(
        data => {
          if (data.status === 422 && !data.ok) {
            this.msg.alert('不能删非当前登录创建的用户！');
          } else {
            this.msg.alert('用户删除成功');
            this.searchUser(); // 刷新列表
          }
        },
        error => {
          console.log('delete user error:', error);
          if (error === '该账号下面有创建的用户') {
            this.msg.alert('该账号下面有创建的用户， 无法删除');
          } else {
            this.msg.alert('用户删除失败，请重试！');
          }
        }
      );
    });
  }

  // 获取用户角色
  getUserRoles() {
    this.adminService.getUserRoles().subscribe(
      data => {
        for (let item of data) {
          this.rolesList.push(item);
        }
      },
      error => console.log('error', error)
    );
  }

  // 确认是否重置密码
  confirmResetPwd(userName) {
    this.userName = userName;
    this.msg.confirm('请确认是否重置密码').result.then(result => {
      this.adminService.resetUserPassword(this.userName).subscribe(
        data => {
          this.msg.alert(`重置密码成功! 新密码为：${data}`, 0);
        },
        error => {
          this.msg.alert('重置密码失败，请重试！');
        }
      );
    });
  }
}



