import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { SharedModule } from '../shared/shared.module';
import { TaskService } from './task.service';
import { TaskListComponent } from './task-list/task-list.component';
import { CompanyRatingTaskComponent } from './company-rating-task/company-rating-task.component';
import { NgUploaderModule } from 'ngx-uploader';
import { WarningTaskComponent } from './warning-task/warning-task.component';
import { BondRatingTaskComponent } from './bond-rating-task/bond-rating-task.component';
import { RatingService } from './rating.service';

@NgModule({
  imports: [
    CommonModule,
    RouterModule,
    FormsModule,
    NgUploaderModule,
    SharedModule
  ],
  declarations: [
    TaskListComponent, CompanyRatingTaskComponent, WarningTaskComponent, BondRatingTaskComponent
  ],
  providers: [TaskService, RatingService]
})
export class TaskModule { }



