import { BaseClass } from '../shared/base-class';
export class RateComparitionResult {
  level: string;
  valueList: number[];
  contrastList: ContrastItems[];
}

export class ContrastItems {
  contrastCompanyList: ContrastCompany[];
}

export class ContrastCompany {
  id: number;
  name: string;
  outRating: string;
  innerRating: string;
  get outRating_level() {
    if (this.outRating) {
      if (this.outRating.substring(0, 1) === 'A') return 1;
      if (this.outRating.substring(0, 2) === 'BB') return 2;
      return 3;
    } else {
      return 0;
    }
  }
  get innerRating_level() {
    if (this.innerRating) {
      if (this.innerRating.substring(0, 1) === 'A') return 1;
      if (this.innerRating.substring(0, 2) === 'BB') return 2;
      return 3;
    } else {
      return 0;
    }
  }
}

export class DefaultPortfolioRating extends BaseClass {
  id: number;
  portFolioName: string;
  ratingCurrent: string;
  creditOrgNm: string;
  ratingList: RatingItem[];
}

export class RatingItem extends BaseClass {
  companyId: number;
  companyName: string;
  innerDate: number;
  innerRating: string;
  outRating: string;
  outRatingDate: number;
}



