import { element } from 'protractor';
import { UserPassword } from './../../shared/entities/user';
import { MasterScaleResolver } from './../../root/master-scale-resolver';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';

import { FeedService } from '../../core/feedAPI/feedAPI.service';
import { UserService } from '../user.service';
import { NgForm } from '@angular/forms';
import { setTimeout } from 'timers';
import { LoginService } from '../login.service';

@Component({
  selector: 'app-login',
  templateUrl: 'login.component.html',
  styleUrls: ['login.component.css']
})

export class LoginComponent implements OnInit {
  error: string;
  errorHidden: boolean;
  submitDisabled: boolean;
  user: any = { 'name': '', 'password': '' };
  token: any;
  kaptchaUrl = '';
  kaptcha: any = '';
  loading = false;
  @ViewChild('loginForm') loginForm: NgForm;
  constructor(private feedService: FeedService, private router: Router,
    private userService: UserService, private loginService: LoginService) {
    this.refreshKaptcha();
  }

  ngOnInit() {
    this.errorHidden = true;
    this.submitDisabled = true;
    this.refreshKaptcha();
    setTimeout(() => {
      this.loginForm.controls.name.markAsPristine();
      this.loginForm.controls.kaptcha.markAsPristine();
      this.loginForm.controls.password.markAsPristine();
    }, 0 );
  }

  change() {
    this.errorHidden = true;
    this.error = '';
  }

  login(loginForm: NgForm) {
    if (loginForm.valid) {
      this.loading = true;
      this.loginService.getAccess(this.user.name, this.user.password, this.kaptcha).subscribe(
        token => {
          console.log(token);
          this.extractData(token);
          this.loginService.getUser().subscribe(
            res => {
              console.log(res);
              window.localStorage.setItem('userInfo', JSON.stringify(res));
              this.userService.CurrentUser = res;
            }
          );
          this.token = token;
          this.router.navigate(['/']);
          this.loading = false;
        },
        error => {
          console.log(error);
          this.error = error;
          this.errorHidden = false;
          this.refreshKaptcha();
          this.loading = false;
        });
    } else {
      loginForm.controls.kaptcha.markAsTouched();
      loginForm.controls.name.markAsTouched();
      loginForm.controls.password.markAsTouched();
    }
  }

  extractData(res) {
    if (res.access_token) {
      window.localStorage.setItem('login_cm_token', JSON.stringify(res));
    }
    return res || {};
  }


  refreshKaptcha() {
    this.kaptcha = '';
    this.kaptchaUrl = this.feedService.getEndpoint() + '/kaptcha?' + new Date().getTime();
  }
}



