import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';

import { AnalysisComponent } from './analysis.component';
import { AnalysisCompanyComponent } from './analysis-company.component';
import { AnalysisIndustryComponent } from './analysis-industry.component';
import { AnalysisArchivesComponent } from './analysis-archives.component';

import { SharedModule } from '../shared/shared.module';
import { AnalysisService } from './analysis.service';
import { PortfolioService } from '../portfolio/portfolio.service';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    SharedModule,
    RouterModule
  ],
  declarations: [
    AnalysisComponent, AnalysisCompanyComponent,
    AnalysisIndustryComponent, AnalysisArchivesComponent
  ],
  providers: [AnalysisService, PortfolioService],
})
export class AnalysisModule { }



