import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';

import { SharedModule } from '../shared/shared.module';
import { PortfolioAdminComponent } from './portfolio-admin/portfolio-admin.component';
import { PortfolioService } from './portfolio.service';
import { RuleService } from './rule.service';
import { RuleSelectComponent } from './rule-select/rule-select.component';
import { SelfSelectComponent } from './self-select/self-select.component';
import { TagInputComponent } from './tag-input/tag-input.component';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    SharedModule,
  ],
  declarations: [PortfolioAdminComponent, SelfSelectComponent, RuleSelectComponent, TagInputComponent],
  providers: [PortfolioService, RuleService]
})
export class PortfolioModule { }



