import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';

import { SharedModule } from '../shared/shared.module';
import { CompanySearchComponent } from './company-search/company-search.component';
import { CompanyDetailComponent } from './company-detail/company-detail.component';
import { CompanyAnalysisComponent } from './company-analysis/company-analysis.component';
import { FactorSelectComponent } from './factor-select/factor-select.component';
import { CompanyEditComponent } from './company-edit/company-edit.component';
import { FinanceEditComponent } from './finance-edit/finance-edit.component';
import { BondEditComponent } from './bond-edit/bond-edit.component';
import { FinanceService } from './finance.service';
import { NgUploaderModule } from 'ngx-uploader';

@NgModule({
  imports: [
    SharedModule,
    RouterModule,
    NgUploaderModule
  ],
  declarations: [
    CompanySearchComponent, CompanyDetailComponent, FactorSelectComponent, CompanyEditComponent,
    FinanceEditComponent, CompanyAnalysisComponent, BondEditComponent
  ],
  providers: [FinanceService]
})
export class CompanyModule { }



