import { Component, OnInit, OnDestroy, Input, Output, EventEmitter, NgZone } from '@angular/core';
import { Subject } from 'rxjs/Subject';
import { Observable } from 'rxjs/Observable';
import { Observer } from 'rxjs/Observer';
import { Subscription } from 'rxjs/Subscription';

import { FinanceService } from '../finance.service';
import { Company } from '../../shared';
import { FinanceSubjctItem, FinanceSet } from '../entities/finance';
import { UploadOutput, UploadInput } from 'ngx-uploader';

@Component({
  selector: 'app-finance-edit',
  styleUrls: ['finance-edit.component.css'],
  templateUrl: 'finance-edit.component.html'
})


export class FinanceEditComponent implements OnInit, OnDestroy {
  @Input()
  companyId: number;
  @Input()
  addStatus: boolean; // true为录入，false为编辑
  @Output()
  loadingEvent: EventEmitter<any> = new EventEmitter();
  @Output()
  finish: EventEmitter<any> = new EventEmitter();
  @Output()
  errorEvent: EventEmitter<any> = new EventEmitter();

  financeTemplateTypeList: any[];
  financeTemplateTypeSelected: any;
  zone: NgZone;
  options: any;
  financeSets: FinanceSet[];
  showFinanceSet: FinanceSet;
  financeDataTypeList: any[];
  financeDataTypeSelected: any;
  showDefaultTemplateTypeAnncement = new Subject();
  sub: Subscription;
  defaultTemplateType: string;
  uploadInput = new EventEmitter<UploadInput>();

  constructor(private financeService: FinanceService) {
  }

  ngOnInit() {
    this.zone = new NgZone({ enableLongStackTrace: false });
    this.financeTemplateTypeSelected = { code: 0, value: '未知' };
    this.financeSets = [];
    this.financeDataTypeList = [
      { code: 'balancesheets', value: '资产负债表' },
      { code: 'cashflows', value: '现金流量表' },
      { code: 'incomestates', value: '利润表' },
    ];
    this.financeDataTypeSelected = this.financeDataTypeList[0];
    this.showFinanceSet = new FinanceSet();
    this.showFinanceSet.financeData = [];
    this.showFinanceSet.dateList = [];
    this.getFinanceTemplateTypeList();
    this.getDefaultTemplateType();
    this.initUploadOption();
    this.sub = this.showDefaultTemplateTypeAnncement.subscribe(
      data => this.defaultTemplateType && (this.financeTemplateTypeList.length > 0) && this.showDefaultTemplateType()
    );
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

  getDefaultTemplateType() {
    this.financeService.getDefaultTemplateType(this.companyId).subscribe(
      data => data && (this.defaultTemplateType = data.name) && this.showDefaultTemplateTypeAnncement.next()
    );
  }

  getFinanceTemplateTypeList() {
    this.financeService.getFinanceTemplateTypeList().subscribe(
      data => {
        this.financeTemplateTypeList = [];
        this.financeTemplateTypeSelected = {
          code: 0,
          value: '未知'
        };
        if (data && data.length > 0) {
          let idx = 0;
          for (let item of data) {
            if (item === '未知') {
              continue;
            }
            idx = idx + 1;
            this.financeTemplateTypeList.push({ code: idx, value: item });
          }
          this.showDefaultTemplateTypeAnncement.next();
          this.initUploadOption();
        }
      },
      error => console.log('getFinanceTemplateTypeList error:', error)
    );
  }

  getFinanceTemplate() {
    this.loadingEvent.emit(true);
    this.financeService.getFinanceTemplate(this.financeTemplateTypeSelected.value).subscribe(
      data => {
        this.loadingEvent.emit(false);
        const fileName = `${this.financeTemplateTypeSelected.value}财务模板.xls`;
        if (window.navigator.msSaveBlob) {
          window.navigator.msSaveBlob(data.body, fileName);
        } else {
          let downloadUrl = window.URL.createObjectURL(data.body);
          let downloadLink = document.createElement('a');
          downloadLink.href = downloadUrl;
          downloadLink.download = fileName;
          document.body.appendChild(downloadLink);
          downloadLink.click();
          document.body.removeChild(downloadLink);
        }
      },
      error => { this.loadingEvent.emit(false); console.log('getFinanceTemplate error:', error); this.errorEvent.emit('模板下载失败'); }
    );
  }

  getFinanceData() {
    this.financeService.getFinanceData(this.companyId, this.financeDataTypeSelected.code, this.financeTemplateTypeSelected.value).subscribe(
      data => this.generateFinanceData(data, this.financeDataTypeSelected.code),
      error => console.log('getFinanceData error:', error)
    );
  }

  generateFinanceData(data, type: string) {
    if (!data || (Array.isArray(data) && data.length < 1)) {
      this.showFinanceSet.financeData = [];
      this.showFinanceSet.dateList = [];
      this.showFinanceSet.financeType = this.financeTemplateTypeSelected.code;
      return;
    }
    let sheets: Array<FinanceSubjctItem>[] = [];
    let dateList: number[] = [];
    for (let unit of data) {
      let unitList: FinanceSubjctItem[] = [];
      for (let item of unit) {
        let subjectItem = new FinanceSubjctItem();
        subjectItem.generateByRaw(item);
        if (subjectItem.subjectEN === 'COMPANY_TYPE' || subjectItem.subjectEN === 'COMBINE_TYPE_CD'
          || subjectItem.subjectEN === 'CURRENCY') {
          continue;
        }
        if (subjectItem.subjectEN === 'RPT_DT') {
          dateList.push(subjectItem.value);
          continue;
        }
        unitList.push(subjectItem);
      }
      sheets.push(unitList);
    }
    let financeSet: FinanceSet;
    if (this.financeSets && this.financeSets.length > 0) {
      let index = this.financeSets.findIndex(item => item.financeType === type);
      if (index >= 0) {
        financeSet = this.financeSets[index];
      }
    }
    if (financeSet) {
      financeSet.financeData = sheets;
      financeSet.dateList = dateList;
    } else {
      financeSet = new FinanceSet();
      financeSet.financeData = sheets;
      financeSet.financeType = type;
      financeSet.dateList = dateList;
      this.financeSets.push(financeSet);
    }
    this.showFinanceSet = financeSet;

    this.financeTemplateTypeSelected.companyId = this.companyId;
    window.localStorage.setItem('financeTemplateTypeSelected', JSON.stringify(this.financeTemplateTypeSelected));
  }

  initUploadOption() {
    let params = this.financeService.getFinanceUploaderOption(this.companyId, this.financeTemplateTypeSelected.value);
    this.options = {
      url: params.url,
      calculateSpeed: true,
      authToken: params.authToken,
      authTokenPrefix: params.authTokenPrefix,
      headers: {Authorization: `${params.authTokenPrefix} ${params.authToken}`},
    };
  }

  onUploadOutput(output: UploadOutput): void {
    if (output.type === 'allAddedToQueue') {
      const event: UploadInput = {
        type: 'uploadAll',
        url: this.options.url,
        method: 'POST',
        headers: this.options.headers,
      };
      this.uploadInput.emit(event);
      this.loadingEvent.emit(true);
    } else if (output.type === 'addedToQueue'  && typeof output.file !== 'undefined') {
    } else if (output.type === 'done' && output.file != null) {
      this.loadingEvent.emit(false);
      if (output.file.responseStatus !== 200) {
        this.errorEvent.emit('财务数据导入失败!');
      } else if (output.file.response) {
        let resp = output.file.response;
        for (let item of resp) {
          if (item.name === 'audits') continue;
          this.generateFinanceData(item.lists, item.name);
        }
        if (this.financeSets && this.financeSets.length > 0) {
          this.showFinanceSet = this.financeSets[0];
        }
        this.financeTemplateTypeSelected.companyId = this.companyId;
        window.localStorage.setItem('financeTemplateTypeSelected', JSON.stringify(this.financeTemplateTypeSelected));
        this.loadingEvent.emit(false);
        this.finish.emit();
      }
    }
  }

  selFinanceDataType(financeDataType) {
    this.financeDataTypeSelected = financeDataType;
    let index = this.financeSets.findIndex(item => item.financeType === financeDataType.code);
    if (index < 0) {
      this.getFinanceData();
    } else {
      this.showFinanceSet = this.financeSets[index];
    }
  }

  selFinanceTemplateType(financeTemplateType) {
    this.financeTemplateTypeSelected = financeTemplateType;
    this.initUploadOption();
    this.financeSets = [];
    this.getFinanceData();
  }

  showDefaultTemplateType() {
    let index = this.financeTemplateTypeList.findIndex(item => item.value === this.defaultTemplateType);
    if (index >= 0) {
      this.financeTemplateTypeSelected = this.financeTemplateTypeList[index];
    } else {
      if (this.financeTemplateTypeList && this.financeTemplateTypeList.length < 1) {
        this.financeTemplateTypeSelected = this.financeTemplateTypeList[0];
      }
    }
    const tempSelected = window.localStorage.getItem('financeTemplateTypeSelected');
    if (tempSelected) {
      const tempSelectedOb = JSON.parse(tempSelected);
      if (tempSelectedOb.companyId === this.companyId) {
        this.financeTemplateTypeSelected = tempSelectedOb;
      }
    }
    this.getFinanceData();
    this.initUploadOption();
  }
}



