import { Component, OnInit, ViewContainerRef } from '@angular/core';

import { ToastsManager } from 'ng2-toastr/ng2-toastr';

import { ModelsService } from '../../models.service';
import { ElementService } from '../../element.service';
import { Element, ElementClassify } from '../../entities/element';
import { Ultility } from '../../../shared';
import { ModelFactor } from '../../entities';
import { MsgService } from 'app/core/msg.service';
@Component({
  selector: 'app-company-config',
  templateUrl: './company-config.component.html',
  styleUrls: ['./company-config.component.css']
})
export class CompanyConfigComponent implements OnInit {
  currentClassify: string;
  isCollapsed = true;
  elementList;
  currentElement: Element;
  private elementsList: ElementClassify[];
  elementDetailList: Element[];
  elementEditing: boolean;
  private dataTypeList: any[];

  constructor(private modelsService: ModelsService, private toastr: ToastsManager,
    private msg: MsgService, private elementService: ElementService) {
  }

  ngOnInit() {
    this.currentClassify = '';
    this.currentElement = new Element();
    this.elementDetailList = [];
    this.elementEditing = false;
    this.dataTypeList = ['请选择', '0', '1'];
    this.getElement();
  }
  // 获取分类列表
  private getElement() {
    this.elementService.getElement().subscribe(
      data => {
        this.elementList = data;
      },
      error => console.log('error:', error)
    );
  }
  // 重命名分类
  save(item, item1, item2) {
    item.editing = false;
    this.elementService.updateElement(item1, item2).subscribe(
      data => {
        this.getElement();
        this.getDetail(item2);
        this.currentClassify = item2;
      },
      error => console.log('error:', error)
    );
  }

  private newFactor() {
    this.isCollapsed = false;
    this.currentElement = new Element();
    this.elementEditing = false;
  }

  editing(item) {
    item.editing = true;
    item.nameCopy = item.name;
  }

  valid() {
    const empty = (v) => v == null || v.length === 0;
    let reg = /^[a-zA-Z][a-zA-Z0-9_]*$/;
    if (empty(this.currentElement.type) || empty(this.currentElement.code)
      || empty(this.currentElement.name) || empty(this.currentElement.dataType)) {
      this.toastr.error('*号为必填');
      return false;
    } else if (!reg.test(this.currentElement.code)) {
      this.toastr.error('代码需为英文字母开头，可以有数字和"_"');
      return false;
    } else {
      return true;
    }
  }

  // 新增元素
  add() {
    if (!this.valid()) { return; }
    if (this.elementEditing === true) {
      this.elementService.editElement(this.currentElement.code, this.currentElement).subscribe(
        data => {
          this.toastr.success('编辑成功');
          this.isCollapsed = true;
          this.getElement();
          this.getDetail(this.currentElement.type);
          this.currentClassify = this.currentElement.type;
        },
        error => this.toastr.error(error, '编辑失败')
      );

    } else {
      this.elementService.createElement(this.currentElement).subscribe(
        data => {
          this.toastr.success('添加成功');
          this.isCollapsed = true;
          this.getElement();
          this.getDetail(this.currentElement.type);
          this.currentClassify = this.currentElement.type;
        },
        error => this.toastr.error(error, '添加失败')
      );
    }
  }

  // 获取列表详细信息
  getDetail(item) {
    this.isCollapsed = true;
    this.elementService.getElementList(item).subscribe(
      data => this.generateElementDetail(data),
      error => console.log('error', error)
    );
  }
  generateElementDetail(data) {
    this.elementDetailList = [];
    if (Ultility.arrayIsNotEmpty(data)) {
      for (let item of data) {
        let elementDetail = new Element();
        elementDetail.generateByRaw(item);
        this.elementDetailList.push(elementDetail);
      }
    }
  }
  // 删除元素
  deleteElement(type, code) {
    if (this.elementDetailList.length < 2) {
      this.msg.confirm('字段分类将被删除,确定删除吗?').result
        .then(result => {
          this.elementService.deleteElement(code).subscribe(
            data => {
              this.toastr.success('删除成功');
              this.getElement();
              this.getDetail(type);
            },
            error => {
              this.toastr.error('删除失败');
            }
          );
        });
    } else {
      this.msg.confirm('确定删除吗?').result
        .then(result => {
          this.elementService.deleteElement(code).subscribe(
            data => {
              this.toastr.success('删除成功');
              this.getElement();
              this.getDetail(type);
            },
            error => {
              this.toastr.error('删除失败');
            }
          );
        });

    }

  }

  // 编辑元素
  editElement(a, b, c, d, e) {
    this.isCollapsed = false;
    this.elementEditing = true;
    this.currentElement = new Element();
    this.currentElement.type = a;
    this.currentElement.code = b;
    this.currentElement.name = c;
    this.currentElement.description = d;
    this.currentElement.dataType = e;
  }
}



