import { BaseClass } from '../base-class';
export class FinanceSubjctItem extends BaseClass {
  subject_en: string;
  subject_cn: string;
  is_bold: boolean;
  value: number;
  child_nodes: FinanceSubjctItem[];
  label: boolean;

  generateByRaw(object: any) {
    super.generateByRaw(object);
    let subjectItemList: FinanceSubjctItem[] = [];
    if (this.child_nodes && this.child_nodes.length > 1) {
      for (let item of this.child_nodes) {
        let subjectItem = new FinanceSubjctItem();
        subjectItem.generateByRaw(item);
        subjectItemList.push(subjectItem);
      }
      this.child_nodes = subjectItemList;
    }
  }
}

export class FinanceSet {
  financeType: string;
  financeData: Array<FinanceSubjctItem>[];
  dateList: number[];
}



