import { Injectable, Optional } from '@angular/core';
import { Http, RequestMethod, Response } from '@angular/http';
import { Headers, RequestOptions, ResponseContentType, URLSearchParams } from '@angular/http';
import { LogSearchParams, PagedResult, Loglist } from '../shared';
import { HttpClient } from '@angular/common/http';

@Injectable()
export class LogService {
  constructor(private http: HttpClient) { }

  // 获取日志列表
  getlogList(logSearchParams: LogSearchParams) {
    return this.http.post<PagedResult<Loglist>>('/log/search', logSearchParams);
  }

  exportLog(logSearchParams: LogSearchParams) {
    return this.http.get(`/log/excel`, {params: {
      keyword: logSearchParams.keyword.toString(),
      ipAddress: logSearchParams.ipAddress.toString(),
      userName: logSearchParams.userName.toString(),
      fromDate: logSearchParams.fromDate.toString(),
      toDate: logSearchParams.toDate.toString()
    }, observe: 'response', responseType: 'blob'});
  }
}



