import { BaseClass } from '../base-class';

export class BondWarrantor extends BaseClass {
  bondWarrantorSid: number; // 不填自动生成：债券担保人标识符
  endDate: string; // 选填：担保结束日期
  guaranteeType: string; // 必填：担保类型
  noticeDate: Date; 	// 必填：公告日期
  secinnerId: number; // 必填：证券内码标识符
  startDate: Date; // 	选填：担保起始日期
  warrantorId: number; // 必填：担保人标识符
  warrantorName: string; // 必填：担保人名称
  warrantorType: string; // 必填： 保证人类型
  warrantyAmout: number; // 必填：
  warrantyAmt: number; // 必填：TODO:which property?
  warrantyFee: number; // 必填：担保费用
  warrantyStrengthType: string; // 选填：担保强度
  warrantyType = '信用担保';	// 必填：担保方式  ID:6438 - 信用担保
  company: any;
}



