import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Element } from './entities/element';
@Injectable()
export class ElementService {
  constructor(private http: HttpClient) { }

  // 获取分类列表
  getElement() {
    return this.http.get('/elements/elementTypes');
  }
  // 更新字段分类
  updateElement(oldType: string, newType: string) {
    return this.http.put(`/elements/elementType/${oldType}/to/${newType}`, null);
  }
  // 创建字段
  createElement(element: Element) {
    return this.http.post('/elements/element', element);
  }
  // 获取字段列表
  getElementList(elementType: string) {
    return this.http.get('/elements/elements', {params: {type: elementType}});
  }
  // 删除字段
  deleteElement(code: string) {
    return this.http.delete(`/elements/element/${code}`);
  }
  // 修改字段
  editElement(code: string, element: Element) {
    return this.http.put(`/elements/element/${code}`, element);
  }
}



