import { Component, OnInit, OnDestroy, ViewChild, TemplateRef, ElementRef, ViewContainerRef } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';

import { LogService } from './log.service';
import { PagedResult, Loglist, LogSearchParams } from '../shared';
import * as moment from 'moment';
import { UserService } from '../user/user.service';


@Component({
  selector: 'app-log',
  styleUrls: ['log.component.css'],
  templateUrl: 'log.component.html'
})

export class LogManageComponent implements OnInit {

  fromDt: Date = null;
  fromDate;
  toDt: Date = null;
  toDate;
  datePickerHidden: boolean;
  datePickerEndHidden: boolean;
  currentPage: number;
  lastPage: number;
  itemsPerPage: number;
  logSearchParams: LogSearchParams;
  operateLogList: PagedResult<Loglist>;
  loading: boolean;

  constructor(private router: Router, private activeRoute: ActivatedRoute, public userService: UserService,
    private logService: LogService) {
  }

  ngOnInit() {
    this.logSearchParams = new LogSearchParams();
    this.logSearchParams.keyword = '';
    this.logSearchParams.ipAddress = '';
    this.logSearchParams.userName = '';
    this.logSearchParams.fromDate = '';
    this.logSearchParams.toDate = '';

    this.datePickerHidden = true;
    this.datePickerEndHidden = true;
    this.lastPage = 1;
    this.currentPage = 1;
    this.itemsPerPage = 10;
    this.getOperateLogList();
  }


  gettoDate() {
    this.toDate = this.toDt && moment(this.toDt).format('YYYY-MM-DD');
    return this.toDate;
  }

  getfromDate() {
    this.fromDate = this.fromDt && moment(this.fromDt).format('YYYY-MM-DD');
    return this.fromDate;
  }

  dateChange() {
    this.datePickerHidden = true;
  }

  dateChangeEnd() {
    this.datePickerEndHidden = true;
  }

  searchLog() {
    if (this.fromDate) {
      this.logSearchParams.fromDate = this.fromDate;
    }
    if (this.toDate) {
      this.logSearchParams.toDate = this.toDate;
    }
    this.currentPage = 1;
    this.getOperateLogList(0, 10);
  }

  clearSearchParams() {
    this.logSearchParams = new LogSearchParams();
    this.fromDate = null;
    this.toDate = null;
    this.fromDt = void 0;
    this.toDt = void 0;
    this.getOperateLogList();
  }

  exportLog() {
    this.logService.exportLog(this.logSearchParams).subscribe(
      data => {
        let date = new Date();
        const fileName = this.userService.CurrentUser.userName + '_' + date.getFullYear() +
          '-' + (date.getMonth() + 1) + '-' + date.getDate() + '_导出日志' + `.xls`;
        if (window.navigator.msSaveBlob) {
          window.navigator.msSaveBlob(data.body, fileName);
        } else {
          let downloadUrl = window.URL.createObjectURL(data.body);
          let downloadLink = document.createElement('a');
          downloadLink.href = downloadUrl;
          downloadLink.download = fileName;
          document.body.appendChild(downloadLink);
          downloadLink.click();
          document.body.removeChild(downloadLink);
        }
      },
      error => {
        this.loading = false;
        console.log('exportlog error:', error);
      }
    );
  }

  pageChanged($event) {
    if (this.lastPage === $event.page) {
      return;
    }else {
      this.lastPage = $event.page;
      this.getOperateLogList($event.page - 1);
    }
  }

  getOperateLogList(page: number = 0, size: number = this.itemsPerPage) {
    this.loading = true;
    this.logSearchParams.page = page;
    this.logSearchParams.size = size;
    this.logService.getlogList(this.logSearchParams).subscribe(
      data => this.generateOperateLogList(data),
      error => {
        console.log('getlogList error:', error);
        this.loading = false;
      }
    );
  }

  generateOperateLogList(data: PagedResult<Loglist>) {
    console.log(data);
    this.loading = false;
    this.operateLogList = new PagedResult<Loglist>();
    this.operateLogList.total = 0;
    this.operateLogList.items = [];
    if (data) {
      this.operateLogList.total = data.total;
      this.operateLogList.page = data.page;
      this.operateLogList.size = data.size;
    }
    if (data && data.items && Array.isArray(data.items) && data.items.length > 0) {
      this.operateLogList.items = data.items;
    }
  }
}



