import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { FormsModule } from '@angular/forms';

import { SharedModule } from '../shared/shared.module';
import { ProductComponent } from './product.component';
import { BondComponent } from './bond/bond.component';
import { BondNewComponent } from './bond/bond-new.component';
import { BondService } from './bond/bond.service';
import { BondDetailComponent } from './bond/bond-detail/bond-detail.component';
import { BondRatingComponent } from './bond/bond-rating/bond-rating.component';

@NgModule({
  imports: [
    SharedModule,
    RouterModule,
    FormsModule,
  ],
  exports: [],
  declarations: [
    ProductComponent,
    BondComponent,
    BondNewComponent,
    BondDetailComponent,
    BondRatingComponent
  ],
  providers: [BondService]
})
export class ProductModule {}



