import { Component, OnInit, Input } from '@angular/core';
import { ModelsService } from '../../models/models.service';

@Component({
  selector: 'app-rating-fluid',
  templateUrl: 'rating-fluid.component.html',
  styleUrls: ['rating-fluid.component.css']
})
export class RatingFluidComponent implements OnInit {
  initialRating: string;
  ratingLevel: number;
  initialRatingType: string;
  ratingTypeLevel: number;
  initialRatingStatus: string;
  initialIdentifiedType: string;
  private _basicRating: string;
  constructor(private modelService: ModelsService) {}

  ngOnInit() {}

  @Input()
  set rating1(r: string) {
    this.initialRating = r;
    this.ratingLevel = 0;
    if (this.initialRating) {
      this.ratingLevel = this.modelService.getLevel(this.initialRating);
      if (this.ratingLevel === 0) {
        this.initialRatingStatus = '已生效';
      }
    }
  }

  get rating1() {
    return this.initialRating;
  }

  @Input()
  set ratingType(ratingType: string) {
    this.initialRatingType = ratingType;
    this.update();
  }

  @Input()
  set identifiedType(identifiedType: string) {
    this.initialIdentifiedType = identifiedType;
    this.update();
  }

  @Input()
  set basicRating(basicRating: string) {
    this._basicRating = basicRating;
    this.update();
  }

  get basicRating() {
    return this._basicRating;
  }

  @Input()
  set ratingStatus(ratingStatus: string) {
    this.initialRatingStatus = ratingStatus;
    if (
      this.initialRatingStatus === '待生效' ||
      this.initialRatingStatus === '未认定'
    ) {
      this.initialRatingStatus = '待';
    } else if (
      this.initialRatingStatus == null ||
      this.initialRatingStatus.length === 0
    ) {
      this.initialRatingStatus = '已生效';
    }
  }

  update() {
    if (this.initialRatingType) {
      this.initialRatingType = this.initialRatingType.substring(0, 1);
      if (
        this.initialRatingType === '认' &&
        `${this.initialIdentifiedType}` === '1'
      ) {
        this.initialRatingType = '机';
        return;
      } else if (
        this.initialRatingType === '认' &&
        `${this.initialIdentifiedType}` === '0'
      ) {
        this.initialRatingType = '人';
        return;
      } else if (this.initialRatingType === '认' && this.basicRating != null) {
        this.ratingTypeLevel = 1;
        return;
      }
    }
  }
}



