import { Component, Input, OnDestroy } from '@angular/core';

@Component({
  selector: 'app-circle',
  styleUrls: ['circle.css'],
  templateUrl: 'circle.html'
})

export class CircleComponent implements OnDestroy {
  visible = true;
  private timeout: any;
  test = true;

  @Input()
  public delay = 0;

  @Input()
  public message: string;

  @Input()
  public set isRunning(value: boolean) {
    if (!value) {
      this.cancel();
      this.visible = false;
    } else {
      this.timeout = setTimeout(() => {
        this.visible = true;
        this.cancel();
      }, this.delay);
    }
  }

  private cancel(): void {
    if (this.timeout) {
      clearTimeout(this.timeout);
      this.timeout = null;
    }
  }

  ngOnDestroy(): any {
    this.cancel();
  }
}



