import { NgModule } from '@angular/core';
import { SharedModule } from '../shared/shared.module';
import { FormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { DndModule } from 'ng2-dnd';

import { ModelsComponent } from './models.component';
import { ModelsService } from './models.service';
import { ExposureService } from './exposure.service';
import { ElementService } from './element.service';
import { FactorService } from './factor.service';
import { ModelConfigComponent } from './model-config/model-config.component';
import { SubmodelConfigComponent } from './model-config/components/submodel-config/submodel-config.component';
import { ScaleConfigComponent } from './scale-config/scale-config.component';
import { ExposureConfigComponent } from './exposure-config/exposure-config.component';
import { FactorConfigComponent } from './factor-config/factor-config.component';
import { FormulaEditorComponent } from './factor-config/formula-editor/formula-editor.component';
import { CompanyConfigComponent } from './data-config/company-config/company-config.component';
import { WarningFinanceComponent } from './warning-finance/warning-finance.component';
import { WarningExtraComponent } from './warning-extra/warning-extra.component';
@NgModule({
  imports: [
    SharedModule,
    FormsModule,
    RouterModule,
    DndModule.forRoot()
  ],
  declarations: [
    ModelsComponent, ModelConfigComponent, SubmodelConfigComponent,
    ScaleConfigComponent, ExposureConfigComponent, FactorConfigComponent, FormulaEditorComponent,
    CompanyConfigComponent, WarningFinanceComponent, WarningExtraComponent
  ],
  providers: [ModelsService, ExposureService, FactorService, ElementService],
})
export class ModelsModule { }



