import { Directive, Input, ElementRef } from '@angular/core';

import { UserService } from '../../user/user.service';

@Directive({
  selector: '[appVisiblePrivilege]'
})
export class VisiblePrivilegeDirective {

  constructor(private ele: ElementRef, private userService: UserService) { }


  @Input()
  set appVisiblePrivilege(privilege: string) {
    if (this.userService.hasPrivilege(privilege)) {
      this.ele.nativeElement.style.visibility = 'visible';
    } else {
      this.ele.nativeElement.style.visibility = 'hidden';
    }
  }
}



