import { BaseClass } from '../../shared/base-class';
import { BondPledge, BondWarrantor } from '../../shared';
import { Company } from '../../shared';
export class BondQueryParams {
  keyword: string;
  size: number;
  page: number;
  rating: string[];
  period: string[];
  couponRate: string[];
  bussion: number[];
  bondType: string[];
  ratingType: string[];
  creditRating: string[];
  issueType: string[];
  issueVolum: string[];
  tradeMarket: string[];
}

export class BondIssuer extends Company {
  bondPartySid: number;
  secinnerId: number;
  noticeDate: string;
  partyId: number;
  partyName: string;
  partyTypeId: number;
  startDate: string;
  endDate: string;
  rating: string;
  ratingLevel: number;
  ratingDate: string;
  sharesList: any[];
  company: Company;
}

export class BondCreditStepItem extends BaseClass {
  sid: number;
  mitigation_value: number;
}

export class BondCreditStepResult extends BaseClass {
  stepName: string;
  stepValue: number;
  itemResults: BondCreditStepItem[];

  generateByRaw(raw) {
    super.generateByRaw(raw);
    if (raw.itemResults) {
      this.itemResults = [];
      for (let item of raw.itemResults) {
        let bondCreditStepItem = new BondCreditStepItem();
        bondCreditStepItem.generateByRaw(item);
        this.itemResults.push(bondCreditStepItem);
      }
    }
  }
}

export class BondCredit extends BaseClass {
  uuid: number;
  secInnerId: number;
  pledgeResult: BondCreditStepResult;
  warrantorResult: BondCreditStepResult;
  rawLGDScore: number;
  rawLGDGrade: string;
  rawRating: string;
  rating_level: number;
  generateByRaw(raw) {
    super.generateByRaw(raw);
    this.rating_level = 0;
    if (raw.pledgeResult) {
      this.pledgeResult = new BondCreditStepResult();
      this.pledgeResult.generateByRaw(raw.pledgeResult);
    }
    if (raw.warrantorResult) {
      this.warrantorResult = new BondCreditStepResult();
      this.warrantorResult.generateByRaw(raw.warrantorResult);
    }
  }
}


export class BondCreditPledge extends BondPledge {
  valid_value: number;
}

export class BondCreditWarrantor extends BondWarrantor {
  valid_value: number;
}

export class BondCreditAdjustSaveParam {
  uuid: number; // 必填：之前调用返回的唯一uuid值
  secInnerId: number; // 必填：证券内码标识符
  modelId: number; // 必填：模型代码，后续支持多模型。现在值是1，指的是当前仅有的一套LGD模型
  rawLgdScore: number; // 必填：原始LGD计算值
  rawLgdGrade: string; // 必填：原始LGD级别
  adjustLgdScore: number; // 选填：调整后LGD计算值
  adjustLgdGrade: string; // 选填：调整后LGD级别
  adjustLgdReason: string; // 选填：调整LGD原因
  rawRating: string; // 必填：原始债券评级
  adjustRating: string; // 必填：调整后债券评级
  adjustRatingReason: string; // 必填：债券评级调整原因
}




