import { Component, OnInit, OnDestroy, ViewChild, TemplateRef } from '@angular/core';
import { Router } from '@angular/router';

import {
  AnalysisIndexGroup,
  AnalysisFactor, AnalysisFactorData,
  AnalysisResult, AnalysisItemCompany,
  AnalysisIndex
} from './analysis';
import { AnalysisService } from './analysis.service';
import { Portfolio, PortfolioItem } from '../shared';
import { PortfolioService } from '../portfolio/portfolio.service';
import { RuleService } from '../portfolio/rule.service';
import { Rule } from '../portfolio/rule-select/rule';


const enum AnalysisMode { PERIOD = 1, INDEX }
declare let d3: any;

@Component({
  selector: 'app-analysis-company',
  styleUrls: ['analysis.component.css'],
  templateUrl: 'analysis-company.component.html',
})

export class AnalysisCompanyComponent implements OnInit, OnDestroy {

  // ###START Fields
  // ui status
  limitVisible = false;
  resultIsReady = false;
  factorsIsReady = false;
  loading: boolean;
  // table options
  analysisColumnsFix = [];
  analysisColumnsAll = [];
  analysisColumns = [];
  // analysis items
  analysisItems: any;
  analysisItemsTemp: Portfolio[] = [];
  analysisFactors: AnalysisFactor[] = [];
  analysisFactorsTemp: AnalysisFactor[] = [];
  // analysis benchmark
  analysisBenchmarks: Rule[] = [];
  analysisPortfolios: Portfolio[] = [];
  curSelectedBenchMark: Portfolio = new Portfolio();

  curSelectedIndexGroupName: string;
  curSelectedIndexName: string;
  // analysis factors
  indexGroups = [];
  indexItems = [];
  indexfactors = [];
  // analysis result
  analysisPeriods = [];
  analysisBaselines = [
    { 'id': 0, 'name': '组合均值', 'selected': false },
    { 'id': 1, 'name': '组合中值', 'selected': false },
    { 'id': 2, 'name': '组合最大值', 'selected': false },
    { 'id': 3, 'name': '组合最小值', 'selected': false },
    { 'id': 4, 'name': '基准均值', 'selected': true },
    { 'id': 5, 'name': '基准中值', 'selected': true },
    { 'id': 6, 'name': '基准最大值', 'selected': true },
    { 'id': 7, 'name': '基准最小值', 'selected': true },
  ];
  analysisData: AnalysisFactorData;
  analysisResult: AnalysisResult;
  analysisResultPeriods = [];
  analysisResultIndex = [];

  analysisModes = [{ 'id': AnalysisMode.PERIOD, 'text': '单一周期' }, { 'id': AnalysisMode.INDEX, 'text': '单一指标' }];
  curMode: AnalysisMode = AnalysisMode.PERIOD;
  curAnalysisPeriod = '2015';
  curAnalysisIndex = '';
  curAnalysisResultTemp = [];
  curAnalysisResultTopFix = [];
  curAnalysisResult = [];
  // analysis chart
  resultChartOptions: any;
  resultChartData = [];

  periodChartOptions: any;
  periodChartData: any;
  periodOptionList = [];
  indexChartOptions: any;
  indexChartData: any;
  indexOptionList = [];

  // elements
  @ViewChild('curBenchMark') curBenchmark;
  @ViewChild('companySelModal') companySelModal;
  @ViewChild('indexSelModal') indexSelModal;
  @ViewChild('cellCBTemplate') cellCBTemplate: TemplateRef<any>;
  @ViewChild('cellRatingTemplate') cellRatingTemplate: TemplateRef<any>;
  @ViewChild('cellRemoveTemplate') cellRemoveTemplate: TemplateRef<any>;
  @ViewChild('cellAlignRTemplate') cellAlignRTemplate: TemplateRef<any>;
  @ViewChild('headerCBTemplate') headerCBTemplate: TemplateRef<any>;
  @ViewChild('cellRatingDateTemplate') cellRatingDateTemplate: TemplateRef<any>;
  // ###END Fields

  constructor(private router: Router,
    private portfolioService: PortfolioService,
    private analysisService: AnalysisService,
    private ruleService: RuleService) {
  }


  // ###START Inits
  ngOnInit() {
    this.loading = false;
    this.analysisService.AnalysisResultAnnouncement.subscribe(
      value => {
        this.analysisItems = value;
        this.getAnalysisFactors();
      }
    );

    this.tableInit();
    // this.getBenchmarks();
    this.getPortfolioList();
  }
  ngOnDestroy() {
    this.analysisService.AnalysisResultAnnouncement.next(false);
  }
  tableInit() {
    this.analysisColumnsFix = [
      { name: '企业名称', prop: 'companyName', width: 300, frozenLeft: true, sortable: false, draggable: false },
      { name: '内部评级', width: 100, cellTemplate: this.cellRatingTemplate, sortable: false, draggable: false },
      { name: '评级日期', cellTemplate: this.cellRatingDateTemplate, width: 100, sortable: false, draggable: false }
    ];

    this.tableColumnsReset();
  }
  tableColumnsReset() {
    console.log('reset tb columns: ', this.analysisColumns);
    this.analysisColumns = [...this.analysisColumnsFix];
    for (let f of this.analysisFactors) {
      this.analysisColumns.push({
        name: f.name, prop: f.name, width: 160, resizeable: false, sortable: false,
        draggable: false, headerTemplate: this.headerCBTemplate, cellTemplate: this.cellAlignRTemplate
      });
    }
    this.analysisColumnsAll = [...this.analysisColumns];
    console.log('reset-done tb columns: ', this.analysisColumns);
  }
  resetPeriodColumns() {
    this.analysisColumns = [...this.analysisColumnsFix];
    for (let f of this.analysisFactors) {
      this.analysisColumns.push({
        name: f.name, prop: f.name, width: 160, resizeable: false, sortable: false,
        draggable: false, headerTemplate: this.headerCBTemplate, cellTemplate: this.cellAlignRTemplate
      });
    }
  }
  resetIndexColumns() {
    this.analysisColumns = [...this.analysisColumnsFix];
    for (let p of this.analysisPeriods) {
      this.analysisColumns.push({
        name: p + '年', prop: p, width: 160, resizeable: false, sortable: false, draggable: false,
        headerTemplate: this.headerCBTemplate, cellTemplate: this.cellAlignRTemplate
      });
    }
    console.log('update index columns:::', this.analysisColumns);
  }
  // ###END Inits


  // ###START Event
  // index selection
  indexGroupChanged(idxGroup: any) {
    console.log('select indexGroup:::', idxGroup.text);
    if (this.curSelectedIndexGroupName === idxGroup.text) return;

    if (this.indexGroups[0].index.length < 1) {
      let tempObj1 = new AnalysisIndex();
      this.indexGroups[0].index.push(tempObj1);
      tempObj1.name = '不存在可比数据';
    } else if (this.indexGroups[1].index.length < 1) {
      let tempObj1 = new AnalysisIndex();
      this.indexGroups[1].index.push(tempObj1);
      tempObj1.name = '不存在可比数据';
    } else if (this.indexGroups[2].index.length < 1) {
      let tempObj1 = new AnalysisIndex();
      this.indexGroups[2].index.push(tempObj1);
      tempObj1.name = '不存在可比数据';
    }
    this.curSelectedIndexGroupName = idxGroup.text;

    this.indexItems = [];
    this.indexfactors = [];
    console.log('select indexGroup - Names:::', this.indexItems);
    this.indexItems = this.indexGroups.filter(g => g.name === idxGroup.text)[0].index;
    console.log('select indexGroup - Names:::', this.indexItems);
    // console.log('select indexGroup - factors:::', this.indexItems[0].factors);
    if (this.indexItems[0].factors && this.indexItems[0].factors.length > 0) {
      this.indexfactors = this.indexItems[0].factors;
      this.curSelectedIndexName = this.indexItems[0].name;
      this.indexSelected(this.curSelectedIndexName);
    }
    console.log('select indexName:::', this.curSelectedIndexName);
  }
  indexSelected(index: any) {
    this.curSelectedIndexName = index;
    this.indexfactors = this.indexItems.filter(value => value.name === index)[0].factors;
  }
  // factors selection
  factorOnChanged(isSelected: any, item: any) {
    if (isSelected.checked) {
      if (this.analysisFactorsTemp.length >= 20) {
        console.log('ggg', isSelected);
        this.limitVisible = true; item.selected = false; isSelected.checked = false; isSelected.unchecked = true;
      } else {
        console.log('ddd', isSelected);
        item.selected = true;
        item.indexGroup = this.curSelectedIndexGroupName;
        item.index = this.curSelectedIndexName;
        this.analysisFactorsTemp.push(item);
      }
    } else {
      item.selected = false;
      this.factorRemoved(item, false);
      this.limitVisible = false;
    }
    console.log('factors changed', this.analysisFactorsTemp);
  }
  factorRemoved(factor: AnalysisFactor, synch: boolean) {
    console.log('factors sync', synch);
    this.analysisFactorsTemp = this.analysisFactorsTemp.filter(f => !(f.name === factor.name && f.index === factor.index));
    this.limitVisible = false;

    for (let ig of this.indexGroups) {
      console.log('removing g', ig);
      for (let idx of ig.index) {
        console.log('removing idx', idx);
        if (idx.name === factor.index) {
          for (let f of idx.factors) {
            if (f.name === factor.name) {
              f.selected = false;

              if (synch) this.analysisFactors = this.analysisFactorsTemp.slice();
              return;
            }
          }
        }
      }
    }
  }
  // benchmark selection
  benchMarkSelected(target: any) {
    console.log('cur select benmark: ', target.value);
    let option = target.options[target.selectedIndex];
    this.curSelectedBenchMark.id = option.value;
    this.curSelectedBenchMark.name = option.text;
    console.log('cur select benmark: ', this.curSelectedBenchMark);
  }
  baseToggle(opt) {
    console.log('base change:::', opt);
    opt.hided = !opt.hided;
    let temp = this.curAnalysisResultTopFix.filter(item => !item.hided);
    this.curAnalysisResult = [...temp, ...this.curAnalysisResultTemp];
    if (this.curMode === AnalysisMode.INDEX) this.refreshIndexChart();
    if (this.curMode === AnalysisMode.PERIOD) this.refreshPeriodChart();
  }
  // factor filtered
  periodFiltered(p: string) {
    console.log('year change:::', p);
    this.curAnalysisPeriod = p;
    this.refreshPeriodData();
    this.refreshPeriodChart();
  }
  // factor filtered
  factorFiltered(f: string) {
    this.curAnalysisIndex = f;
    this.refreshIndexData();
    this.refreshIndexChart();
  }
  //
  modeSelected(target: any) {
    console.log('select analysis mode drop:', target);
    let mode = target.options[target.selectedIndex];
    if (mode.value === AnalysisMode.PERIOD.toString()) {
      if (this.curMode !== AnalysisMode.PERIOD) {
        this.curMode = AnalysisMode.PERIOD;
        this.refreshPeriodMode();
      }
    }

    if (mode.value === AnalysisMode.INDEX.toString()) {
      if (this.curMode !== AnalysisMode.INDEX) {
        this.curMode = AnalysisMode.INDEX;
        this.refreshIndexMode();
      }
    }
    console.log('select analysis mode:', this.curMode);
  }

  analysisIndexSelected(index: any): void {
    console.log('select index:', index);
    this.curAnalysisIndex = index.name;

    if (this.curMode === AnalysisMode.INDEX) this.refreshIndexChart();
    if (this.curMode === AnalysisMode.PERIOD) this.refreshPeriodChart();
  }
  // ###END Event


  // ###START Modal
  factorSelectorShow() {
    this.analysisFactorsTemp = this.analysisFactors.slice();
    this.updateCheckbox();
    this.indexSelModal.config.backdrop = false; // workaround
    this.indexSelModal.show();
  }
  updateCheckbox() {
    for (let ig of this.indexGroups) {
      for (let idx of ig.index) {
        if (idx.factors) {
          for (let f of idx.factors) {
            if (this.analysisFactorsTemp.some(it => it.index === idx.name && it.name === f.name)) {
              f.selected = true;
            } else {
              f.selected = false;
            }
          }
        }
      }
    }
  }
  factorSelectorClose() {
    // this.loading=true;

    this.analysisFactors = this.analysisFactorsTemp.slice();

    this.factorsIsReady = this.analysisFactors.length > 0;

    this.indexSelModal.hide();
  }
  // ###END Modal

  // ###START Analysis Results

  // Refresh
  refreshPeriodMode() {

    this.curAnalysisIndex = this.analysisFactors[0].name;
    this.resetPeriodColumns();

    // this.analysisResultPeriods = this.analysisRaw.items;
    // this.curAnalysisResult = this.analysisResultPeriods;
    this.refreshPeriodData();

    this.refreshPeriodChart();
  }

  refreshIndexMode() {

    this.curAnalysisIndex = this.analysisFactors[0].name;

    this.resetIndexColumns();

    this.refreshIndexData();

    this.refreshIndexChart();
  }

  refreshPeriodData() {
    this.curAnalysisResultTemp = [];
    this.analysisPeriods = [];
    this.curAnalysisResultTopFix = [];

    let factList = [];
    if (this.analysisResult.summaries.length > 0 && this.analysisResult.summaries[0].factorList.length > 0) {
      console.log('summaries[0] factorlist:::', this.analysisResult.summaries[0].factorList);
      for (let fl of this.analysisResult.summaries[0].factorList) {
        let i = fl.date.indexOf('-');
        this.analysisPeriods.push(fl.date.slice(0, i));
      }
    }
    this.curAnalysisPeriod = this.curAnalysisPeriod ? this.curAnalysisPeriod : this.analysisPeriods[0];

    let idx = 0;
    for (let base of this.analysisResult.baselines) {
      console.log('this.analysisResult.baselines', this.analysisResult.baselines);
      let item = new AnalysisItemCompany();
      item.optId = 'jz' + idx++;
      item.companyName = '基准' + base.name;
      item.rating = 'NaN';
      console.log('baselines factorlist:::', base.factorList);
      let fList = base.factorList.filter(fl => fl.date.indexOf(this.curAnalysisPeriod) > -1);

      for (let f of this.analysisFactors) item[f.name] = '-';

      if (fList && fList.length > 0) {
        for (let fdata of fList[0].factors) {
          item[fdata.name] = Number(fdata.value).toFixed(5);
        }
      }

      this.curAnalysisResultTopFix.push(item);
    }
    for (let sum of this.analysisResult.summaries) {
      let item = new AnalysisItemCompany();
      item.optId = 'jz' + idx++;
      item.companyName = '组合' + sum.name;
      item.rating = 'NaN';
      console.log('summaries factorlist:::', sum.factorList);
      factList = sum.factorList;

      let fList = sum.factorList.filter(fl => fl.date.indexOf(this.curAnalysisPeriod) > -1);

      for (let f of this.analysisFactors) item[f.name] = '-';

      if (fList && fList.length > 0) {
        for (let fdata of fList[0].factors) {
          item[fdata.name] = Number(fdata.value).toFixed(5);
        }
      }
      this.curAnalysisResultTopFix.push(item);
    }
    console.log('analysis items:::', this.analysisResult.items);
    for (let item of this.analysisResult.items) {
      this.periodOptionList.push(item.companyName);
      let target = this.analysisResult.raw.filter(c => c.company.id === item.companyId)[0];
      let flist = [];

      console.log('targert :::', item.companyName);
      if (target && target.factorList && target.factorList.length > 0) {
        flist = target.factorList.filter(fl => fl.date.indexOf(this.curAnalysisPeriod) > -1);
      }

      console.log('target factorlist:::', flist);

      for (let f of this.analysisFactors) item[f.name] = '-';

      if (flist && flist.length > 0) {
        console.log('target item:::', item);
        for (let f of flist[0].factors) {
          item[f.name] = Number(f.value).toFixed(5);
        }
      }
      this.curAnalysisResultTemp.push(item);
    }
    this.curAnalysisResult = [];
    this.curAnalysisResult = [...this.curAnalysisResultTopFix, ...this.curAnalysisResultTemp];

    console.log('current analysis result:::', this.curAnalysisResult);
  }

  refreshIndexData() {
    this.curAnalysisResultTemp = [];
    this.analysisResultIndex = [];
    this.curAnalysisResultTopFix = [];
    let factList = [];

    console.log('refresh baseline for index', this.analysisResult.baselines);
    let idx = 0;
    for (let base of this.analysisResult.baselines) {
      let item = new AnalysisItemCompany();
      item.optId = 'jz' + idx++;
      item.companyName = '基准' + base.name;
      item.rating = 'NaN';
      console.log('raw raw base', base);
      for (let fl of base.factorList) {
        let factor = fl.factors.filter(f => {
          console.log('refreshIndexData - find factor name', this.curAnalysisIndex);
          return f.name === this.curAnalysisIndex;
        })[0];

        if (factor && factor.value) {
          item[fl.date.slice(0, 4)] = Number(factor.value).toFixed(5);
        } else {
          item[fl.date.slice(0, 4)] = '-';
        }
      }
      this.curAnalysisResultTopFix.push(item);
    }
    for (let sum of this.analysisResult.summaries) {
      let item = new AnalysisItemCompany();
      item.optId = 'jz' + idx++;
      item.companyName = '组合' + sum.name;
      item.rating = 'NaN';
      for (let fl of sum.factorList) {
        let factor = fl.factors.filter(f => {
          console.log('refreshIndexData - find factor name', this.curAnalysisIndex);
          return f.name === this.curAnalysisIndex;
        })[0];

        if (factor && factor.value) {
          item[fl.date.slice(0, 4)] = Number(factor.value).toFixed(5);
        } else {
          item[fl.date.slice(0, 4)] = '-';
        }
      }
      this.curAnalysisResultTopFix.push(item);
    }

    console.log('refreshing index data', this.analysisResult.items);
    for (let item of this.analysisResult.items) {

      let result = new AnalysisItemCompany();
      result.companyId = item.companyId;
      result.companyName = item.companyName;
      result.innerRating = item.innerRating;
      result.rating = item.rating;
      result.ratingDate = item.ratingDate;
      let target = this.analysisResult.raw.filter(c => c.company.id === item.companyId)[0];
      if (!target) continue;

      for (let fl of target.factorList) {
        let factor = fl.factors.filter(f => f.name === this.curAnalysisIndex)[0];

        if (factor && factor.value) {
          result[fl.date.slice(0, 4)] = Number(factor.value).toFixed(5);
        } else {
          result[fl.date.slice(0, 4)] = '-';
        }
      }
      this.curAnalysisResultTemp.push(result);
    }
    this.curAnalysisResult = [];
    this.curAnalysisResult = [...this.curAnalysisResultTopFix, ...this.curAnalysisResultTemp];

  }

  // ###END Analysis Results

  // ###START Analysis Charts
  refreshPeriodChart() {
    this.periodChartOptions = {
      chart: {
        type: 'discreteBarChart',
        noData: '无数据',
        height: 250,
        margin: {
          top: 20,
          right: 40,
          bottom: 100,
          left: 85
        },
        color: function (d, i) {
          //   if (i < 7) return "#D0E8E7";
          //   if (i < 13) return "#FCE7A0";
          //   if (i < 20) return "#F1AEA5";
          return '#C9CACB';
        },
        x: function (d) { return d.label; },
        y: function (d) { return d.value; },
        showValues: true,
        valueFormat: d3.format('.2f'),
        duration: 500,
        yAxis: {
          tickFormat: d3.format('.2f')
        },
        xAxis: {
          rotateLabels: 30
        },
        // dispatch: {
        //     renderEnd: addReferenceLine
        // }
      }
    };

    let map: any = {};
    const companyNames = [];
    for (let item of this.curAnalysisResult) {
      console.log('item data::::', this.curAnalysisIndex);
      map[item.companyName] = item[this.curAnalysisIndex] === '-' ? '' : item[this.curAnalysisIndex];
      companyNames.push(item.companyName);
    }
    console.log('map resss::::', map);
    this.periodChartData = [
      {
        key: 'indvidual period',
        values: companyNames.map(it => {
          return { label: it, value: map[it] || 0 };
        })
      }
    ];

    this.resultChartOptions = this.periodChartOptions;
    this.resultChartData = this.periodChartData;
    console.log(this.periodChartData);
  }
  refreshIndexChart() {
    this.indexChartData = [];
    this.indexChartOptions = {
      chart: {
        type: 'lineChart',
        noData: '无数据',
        height: 260,
        color: d3.scale.category20().range(),
        margin: {
          left: 120
        },
        x: function (d) { return d.x; },
        y: function (d) { return d.y; },
        valueFormat: d3.format('.2f'),
        duration: 500,
        yAxis: {
          tickFormat: d3.format(',.2f')
        },
        useInteractiveGuideline: true,
        interactiveLayer: {
          tooltip: {
            contentGenerator: function (d) {
              if (d === null) {
                return '';
              }
              d.series = d.series.filter(item => {
                if (item.data && item.data.x !== d.value) {
                  return false;
                } else {
                  return true;
                }
              });
              let table = d3.select(document.createElement('table'));
              let theadEnter = table.selectAll('thead')
                .data([d])
                .enter().append('thead');
              theadEnter.append('tr')
                .append('td')
                .attr('colspan', 3)
                .append('strong')
                .classed('x-value', true)
                .html(d.value);

              let tbodyEnter = table.selectAll('tbody')
                .data([d])
                .enter().append('tbody');

              let trowEnter = tbodyEnter.selectAll('tr')
                .data(function (p) { return p.series; })
                .enter()
                .append('tr');

              trowEnter.append('td')
                .classed('legend-color-guide', true)
                .append('div')
                .style('background-color', function (p) { return p.color; });

              trowEnter.append('td')
                .classed('key', true)
                .classed('total', function (p) { return !!p.total; })
                .html(function (p, i) { return p.key; });

              trowEnter.append('td')
                .classed('value', true)
                .html(function (p, i) { return p.value; });

              trowEnter.selectAll('td').each(function (p) {
                if (p.highlight) {
                  let opacityScale = d3.scale.linear().domain([0, 1]).range(['#fff', p.color]);
                  let opacity = 0.6;
                  d3.select(this)
                    .style('border-bottom-color', opacityScale(opacity))
                    .style('border-top-color', opacityScale(opacity))
                    ;
                }
              });
              let html = table.node().outerHTML;
              return html;
            }
          }
        }
      }
    };

    let meta: any;
    console.log('item data::::', this.curAnalysisResultTemp);
    let max;
    let min;
    for (let item of this.curAnalysisResult) {
      meta = {};
      meta['key'] = item.companyName;
      meta['values'] = [];
      for (let period of this.analysisPeriods) {
        if (!item[period] || item[period] === '-' || item[period].trim() === '') continue;
        meta.values.push({ x: period, y: item[period] === '-' ? '' : +item[period] });
        if (!max || (+item[period]) > max) {
          max = +item[period];
        }
        if (!min || (+item[period]) < min) {
          min = +item[period];
        }
      }
      this.indexChartData.push(meta);
    }
    console.log('done with indexChartData:::', this.indexChartData);
    let ytickValues = [];
    let step = (max - min) / 5;
    for (let it = 0; it < 5; it++) {
      if (it === 4) {
        ytickValues.push(+max);
      } else {
        ytickValues.push(min + step * it);
      }
    }
    this.indexChartOptions.chart.yAxis.tickValues = ytickValues;
    this.resultChartOptions = this.indexChartOptions;
    this.resultChartData = this.indexChartData;

  }
  // ###END Analysis Charts


  // ###START DataFeed
  // Get Analsis Factors
  getAnalysisFactors() {
    let companyIds = [];

    for (let c of this.analysisItems) {
      companyIds.push(c.companyId);
    }
    if (companyIds.length > 0) {
      this.analysisService.getAnalysisFactors(companyIds).subscribe(
        data => this.generateAnalysisFactors(data),
        error => console.log('error:', error)
      );
    }
  }
  generateAnalysisFactors(data: any[]) {
    this.indexGroups = [];
    for (let item of data) {
      let idxGroup = new AnalysisIndexGroup();
      idxGroup.generateByRaw(item);
      this.indexGroups.push(idxGroup);
    }
    console.log('index groups: ', this.indexGroups);
  }
  isSameExposure() {
    if (this.analysisItems && this.analysisItems.length === 1) {
      return true;
    } else {
      for (let i = 1; i < this.analysisItems.length; i++) {
        let tempId = this.analysisItems[0].exposureId;
        if (this.analysisItems[i].exposureId !== tempId) {
          return false;
        } else {
          return true;
        }
      }
    }
  }

  // Get Benchmarks
  getPortfolioList() {
    this.portfolioService.getPortfolios(0).subscribe(
      data => this.generatePortfolioList(data),
      error => console.log('getPortfolioList error:', error)
    );
  }
  generatePortfolioList(data: any[]) {
    this.analysisPortfolios = [];
    this.analysisPortfolios.push(new Portfolio());
    for (let item of data) {
      let portfolio = new Portfolio();
      portfolio.generateByRaw(item);
      portfolio.type = '企业';

      this.analysisPortfolios.push(portfolio);
    }
  }
  getBenchmarks() {
    this.ruleService.getRuleList().subscribe(
      data => this.generateBenchmarks(data),
      error => console.log('error:', error)
    );
  }
  generateBenchmarks(data) {
    this.analysisBenchmarks = [];
    this.analysisBenchmarks.push(new Rule());
    for (let item of data) {
      let rule = new Rule();
      rule.generateByRaw(item);
      this.analysisBenchmarks.push(rule);
    }
    console.log('benchmarks: ', this.analysisBenchmarks);
  }

  // Get Analysis Results
  getAnalysisResult() {
    this.resultIsReady = true;
    this.loading = true;
    let companyIds = [];
    let factors = [];

    for (let c of this.analysisItems) {
      companyIds.push(c.companyId);
    }
    console.log('factors:::', this.analysisFactors);
    for (let f of this.analysisFactors) {

    }
    console.log('curSelectedBenchMark:::', this.curSelectedBenchMark);
    this.analysisService.getAnalysisFactorsData(companyIds, this.analysisFactors, this.curSelectedBenchMark.id).subscribe(
      data => { this.generateAnalysisResult(data); },
      error => console.log('error:', error)
    );
  }
  generateAnalysisResult(data: any) {

    this.analysisData = new AnalysisFactorData();
    this.analysisData.generateByRaw(data);

    console.log(this.analysisFactors);
    console.log(this.analysisItems);
    console.log(this.analysisData);
    this.tableColumnsReset();
    this.analysisResult = new AnalysisResult();
    this.analysisResult.factors = this.analysisFactors;
    this.analysisResult.items = this.analysisItems;
    console.log('items:::', this.analysisResult.items);
    console.log('companys:::', this.analysisResult.items);
    this.analysisResult.raw = this.analysisData.companies.filter(c =>
      this.analysisResult.items.filter(i => i.companyId === c.company.id).length > 0
    );
    this.analysisResult.benchmark = this.curSelectedBenchMark.name;
    this.analysisResult.baselines = this.analysisData.baselines;
    this.analysisResult.summaries = this.analysisData.summaries;
    console.log('raw:::', this.analysisResult.raw);
    if (this.curMode === AnalysisMode.INDEX) {
      this.refreshIndexMode();
    }
    if (this.curMode === AnalysisMode.PERIOD) {
      this.refreshPeriodMode();
    }

    this.loading = false;
  }

  // ###END DataFeed

}




