import { BaseClass } from '../base-class';
export class Rating extends BaseClass {
  companyId: number; // 企业id
  companyName: string; // 企业名称
  ratingResult: string; // 评级结果
  createdTime: string; // 创建时间
  exposureCode: string; // 行业代码
  exposureName: string; // 行业名称
  adjustComment: string; // 评级备注
  modelName: string; // 评级模型
  initiator: string; // 评级人
  checkstatus: boolean;

  secinnerId: number;
  changeDate: number;
  rating: string;
  creditName: string;
  noticeDate: number;
  ratingType: string;
  ratingStyle: string;
  changeWay: string;
  ratingForward: string;
  remark: string;
  updateDate: number;
  ratingDate: string;
  adjustRating: string;
  isCollapse: boolean;

  get ratingLevel() {
    if (this.ratingResult) {
      if (this.ratingResult.substring(0, 1) === 'A') return { result: this.ratingResult, level: 1 };
      if (this.ratingResult.substring(0, 2) === 'BB') return { result: this.ratingResult, level: 2 };

      return { result: this.ratingResult, level: 3 };
    } else {
      return { result: '', level: 0 };
    }
  }
}

export class QualIndex extends BaseClass {
  priority: number;
  submodelId: number;
  submodelName: string;
  type: string;
  companyFactor: QualFactor[];

}
export class QualFactor {
  companyFactorId: number;
  description: string;
  factorCode: string;
  factorType: string;
  factorName: string;
  formularChName: string;
  ratingModelFactorid: number;
  remark: string;
  selectedOption: number;
  values: any;
  factorOperationOption: FactorOption[];
}

export class FactorOption extends BaseClass {
  factorCode: string;
  formularChName: string;
  id: number;
  optionNumble: number;
  systemCode: number;
  remark: string;
  get text() {
    return this.formularChName;
  }
  set text(value) {

  }
}



