import { Injectable } from '@angular/core';
import { WarningQueryParam, PagedWarning } from '../shared';
import { HttpClient } from '@angular/common/http';
@Injectable()
export class WarningService {
  private warningsApiPrefix = '/warnings';
  constructor(private http: HttpClient) { }

  /**
   * 根据各种查询条件获取预警列表
   */
  getWarnings(params: WarningQueryParam) {
    return this.http.post<PagedWarning>(this.warningsApiPrefix + '/search', params);
  }

  getWarningDetail(warningId: number, typeId: number) {
    return this.http.get<any>(`/warnings/${warningId}/${typeId}/sole`);
  }

  getWarning(id) {
    return this.http.get('/warnings/' + id);
  }

  /**
   * 获取预警类型列表
   */
  getWarningTypes(importance: number) {
    return this.http.get(this.warningsApiPrefix + `/types/importance/${importance}`);
  }

  /**
   * 根据关键字获取相似词
   */
  getSimilarWords(keyword: string) {
    return this.http.get<any>(this.warningsApiPrefix + `/complete?keyword=${keyword}`)
      .map(el => {
        return el ? el.map(data => ({ id: data.id, name: data.subjectName })) : null;
      });
  }
}



