import { Injectable, Optional } from '@angular/core';
import { Http, RequestMethod, Response } from '@angular/http';
import {
  Headers,
  RequestOptions,
  ResponseContentType,
  URLSearchParams
} from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import 'rxjs/add/operator/catch';

import { FeedService } from '../core/feedAPI/feedAPI.service';
import { StringFormatter } from '../shared/string-formater';
import { HttpClient } from '@angular/common/http';
@Injectable()
export class FinanceService {
  private endpoint: string;
  private header: Headers;

  private financeApiPrefix = '/finance';
  private getFinanceTemplateTypeListApi = '/lookup/finance/companyTypes';
  private uploadFinanceApiParam: StringFormatter = new StringFormatter(
    this.financeApiPrefix + '/{companyId}/companyTypes/{companyType}/excel'
  );

  constructor(private httpd: Http, private feedService: FeedService, private http: HttpClient ) {}

  /**
   * 获取公司默认财务模板类型
   */
  getDefaultTemplateType(companyId: number) {
    return this.http.get<any>(this.financeApiPrefix + `/${companyId}/companyType`);
  }

  /**
   * 获取财务模板集合
   */
  getFinanceTemplateTypeList() {
    return this.http.get<any>(this.getFinanceTemplateTypeListApi);
  }

  getFinanceTemplate(type: string) {
    return this.http.get(this.financeApiPrefix + `/templates/companyTypes/${type}`, {observe: 'response', responseType: 'blob'});
  }

  getFinanceData(companyId: number, dataType: string, companyType: string) {
    return this.http.get(this.financeApiPrefix + `/${companyId}/${dataType}/companyTypes/${companyType}`);
  }

  getFinanceUploaderOption(companyId: number, companyType: string) {
    let token = this.feedService.getToken();
    return {
      url:
        this.feedService.getEndpoint() +
        this.uploadFinanceApiParam.get({
          companyId: companyId,
          companyType: companyType
        }),
      authToken: token ? token.access_token : null,
      authTokenPrefix: token ? token.token_type : null
    };
  }

  getFinanceDetails(companyId: number, reportType: string, range: number) {
    return this.http.get<any>(this.financeApiPrefix + `/${companyId}/details`,
    {params: {'reportType': reportType, 'range': range.toString()}});
  }

  exportFinanceReports(companyId: number, reportType: string, range: number) {
    return this.http.get(this.financeApiPrefix + `/${companyId}/details/excel`,
    {params: {'reportType': reportType, 'range': range.toString()}, observe: 'response', responseType: 'blob'});
  }
}



