import { BaseClass } from '../shared/base-class';
export class AnalysisIndexGroup extends BaseClass {
  name: string;
  factorTypes: AnalysisIndex[];

  get id() {
    return this.name;
  }
  get text() {
    return this.name;
  }
}

export class AnalysisItemCompany {
  hided: boolean;
  companyId: number;
  companyName: string;

  innerRating: any;
  rating: string;
  ratingDate: any;
  exposureId: number;
  exposure: string;

  optId: string;


  get ratingLevel() {
    // tslint:disable-next-line:triple-equals
    if (this.rating == 'NaN') return { result: '', level: -1 };
    if (this.rating) {
      if (this.rating.substring(0, 1) === 'A') return { result: this.rating, level: 1 };
      if (this.rating.substring(0, 2) === 'BB') return { result: this.rating, level: 2 };

      return { result: this.rating, level: 3 };
    } else {
      return { result: '', level: 0 };
    }
  }

}
export class AnlysisItemBond {

}

export class AnalysisIndex extends BaseClass {
  name: string;
  factors: AnalysisFactor[];
}

export class AnalysisFactor extends BaseClass {
  name: string;
  selected: boolean;
  index: string;
  type: string;
}

export class AnalysisFactorData extends BaseClass {
  companies: any[];
  summaries: any[];
  baselines: any[];
}

export class AnalysisResult {
  items: any[];
  raw: any[];
  factors: AnalysisFactor[];
  baselines: any[];
  summaries: any[];
  benchmark: string;
}



