import {
  Component,
  ViewChild,
  EventEmitter,
  Input,
  Output,
  OnInit,
  TemplateRef
} from '@angular/core';
import { FormControl } from '@angular/forms';

import { CompanyService } from '../../company/company.service';
import { BondService } from '../../product/bond/bond.service';
import { ValidStatus } from '../tag-input/valid-status';
import { SelfSelectTypeEnum } from './self-select-type';
import { ModalDirective } from 'app/shared/ngx-bootstrap';

// import { CompanyCheckExistResponse } from '../../company/entities/company-check-exist';

@Component({
  selector: 'app-selfselect',
  templateUrl: 'self-select.component.html',
  styleUrls: ['self-select.component.css']
})
export class SelfSelectComponent implements OnInit {
  @Output() selfSelectFinish: EventEmitter<any> = new EventEmitter<any>();
  @Output() loadingStart: EventEmitter<any> = new EventEmitter<any>();
  @Output() okBtnValue: EventEmitter<any> = new EventEmitter<any>();
  @Output() okDisabledFlag: EventEmitter<any> = new EventEmitter<any>();
  @Input()
  set regulationType(regulationType: number) {
    this.regulationId = regulationType;
  }
  @ViewChild('selSelectModal') public selSelectModal: ModalDirective;
  @ViewChild('hdrTpl') hdrTpl: TemplateRef<any>;
  @ViewChild('checkboxTmpl') checkboxTmpl: TemplateRef<any>;
  count: number;
  itemsList: any[];
  portfolioItems: any[];
  okDisabled: boolean;
  warningHidden: boolean;
  selectType: string;
  selectTypeId: number;
  errorMessage: string;
  max: number;
  okValue: string;
  regulationId: number;

  constructor(
    private companyService: CompanyService,
    private bondService: BondService
  ) {}

  ngOnInit() {
    this.itemsList = [];
    this.portfolioItems = [];
    this.max = 200;
    this.okValue = '确定';
  }

  refresh($event) {
    if ($event && $event.type) {
      return;
    }
    this.count = 0;
    this.itemsList = [];
    this.portfolioItems = [];
    this.okValue = '确定';
    this.okBtnValue.emit(this.okValue);
    this.okDisabled = true;
    this.okDisabledFlag.emit(this.okDisabled);
    this.warningHidden = true;
  }

  show() {
    this.refresh(null);
    if (this.regulationId === 0) {
      this.selectType = '企业名称';
    } else {
      this.selectType = '债券简称';
      this.selectTypeId = 2;
    }
    this.selSelectModal.config.backdrop = false;
    this.selSelectModal.show();
  }
  hide() {
    this.selSelectModal.hide();
  }
  okClick() {
    this.loadingStart.emit(true);
    this.selSelectModal.hide();
    this.selfSelectFinish.emit(this.portfolioItems);
  }

  errorHandler($event) {
    this.warningHidden = false;
    if (!$event) {
      switch (this.selectType) {
        case '企业名称':
          $event = '存在不合法公司名称或者公司名称不存在，请检查';
          break;
        case '企业工商登记号':
          $event = '存在不合法工商登记号或者工商登记号不存在，请检查';
          break;
        case '债券简称':
          $event = '存在不合法债券简称或者债券简称不存在，请检查';
          break;
        case '债券全称':
          $event = '存在不合法债券全称或者债券全称不存在，请检查';
          break;
        case '债券代码':
          $event = '存在不合法债券代码或者债券代码不存在，请检查';
          break;
      }
    } else {
      $event = '';
    }
    this.errorMessage = $event;
  }

  itemsChange($event) {
    this.count = this.itemsList.length;
    // let reg= /[u4E00-u9FA5]+/;
    let checkItemList = [];
    let errorNumber = 0;
    this.itemsList.map(item => {
      item.status = ValidStatus.UNCHECK; // 不再过滤掉已检查过的企业，而是每次所有的都进行检查，否则会有bug
      item.text = item.text.trim();

      if (this.selectType === '企业名称' && item.text.length < 1) {
        item.status = ValidStatus.FAIURE;
        errorNumber = errorNumber + 1;
      } else if (
        this.selectType === '债券简称' &&
        (item.text.length < 1 || item.text.length > 30)
      ) {
        item.status = ValidStatus.FAIURE;
        errorNumber = errorNumber + 1;
        this.selectTypeId = 2;
      } else if (
        this.selectType === '债券全称' &&
        (item.text.length < 1 || item.text.length > 100)
      ) {
        item.status = ValidStatus.FAIURE;
        errorNumber = errorNumber + 1;
        this.selectTypeId = 1;
      } else if (
        this.selectType === '债券代码' &&
        (item.text.length < 1 || item.text.length > 30)
      ) {
        item.status = ValidStatus.FAIURE;
        errorNumber = errorNumber + 1;
        this.selectTypeId = 3;
      } else {
        checkItemList.push(item.text);
      }
    });
    if (errorNumber > 0) {
      this.errorHandler(null);
      this.okDisabled = true;
      this.okDisabledFlag.emit(this.okDisabled);
      return;
    }
    if (checkItemList.length > 0) {
      this.okValue = '查询中...';
      this.okBtnValue.emit(this.okValue);
      switch (this.selectType) {
        case '企业名称':
          this.companyService
            .getCompaniesByCompanyNames(checkItemList)
            .subscribe(
              data =>
                this.updateItemsListStatus(
                  data,
                  SelfSelectTypeEnum.COMPANYNAME
                ),
              error => console.log('getCompaniesByCompanyNames error:', error)
            );
          break;
        case '企业工商登记号':
          this.companyService
            .getCompaniesByBlnumbCodes(checkItemList)
            .subscribe(
              data =>
                this.updateItemsListStatus(data, SelfSelectTypeEnum.BLNUMBCODE),
              error => console.log('getCompaniesByBlnumbCodes error:', error)
            );
          break;
        case '债券简称':
          this.bondService
            .getBondsByTypes(2, checkItemList)
            .subscribe(
              data =>
                this.updateItemsListStatus(
                  data,
                  SelfSelectTypeEnum.BONDSHORTNAME
                ),
              error => console.log('getCompaniesByBlnumbCodes error:', error)
            );
          break;
        case '债券全称':
          this.bondService
            .getBondsByTypes(1, checkItemList)
            .subscribe(
              data =>
                this.updateItemsListStatus(data, SelfSelectTypeEnum.BONDNAME),
              error => console.log('getCompaniesByBlnumbCodes error:', error)
            );
          break;
        case '债券代码':
          this.bondService
            .getBondsByTypes(3, checkItemList)
            .subscribe(
              data =>
                this.updateItemsListStatus(data, SelfSelectTypeEnum.BONDCD),
              error => console.log('getCompaniesByBlnumbCodes error:', error)
            );
          break;
      }
    } else {
      this.okDisabled = false;
      this.okDisabledFlag.emit(this.okDisabled);
    }
  }

  updateItemsListStatus(data: any[], companyCheckType: SelfSelectTypeEnum) {
    this.okValue = '确定';
    this.okBtnValue.emit(this.okValue);
    let errorStatus = false;
    this.portfolioItems.length = 0;
    if (data && Array.isArray(data) && data.length > 0) {
      for (let rawItem of data) {
        if (!rawItem) {
          continue;
        }
        let text;
        switch (companyCheckType) {
          case SelfSelectTypeEnum.COMPANYID:
            text = rawItem.companyId;
            break;
          case SelfSelectTypeEnum.COMPANYCD:
            text = rawItem.companyCd;
            break;
          case SelfSelectTypeEnum.COMPANYNAME:
            text = rawItem.companyName;
            break;
          case SelfSelectTypeEnum.BLNUMBCODE:
            text = rawItem.blnumb;
            break;
          case SelfSelectTypeEnum.BONDSHORTNAME:
            text = rawItem.securityShortName;
            break;
          case SelfSelectTypeEnum.BONDNAME:
            text = rawItem.securityName;
            break;
          case SelfSelectTypeEnum.BONDCD:
            text = rawItem.securityCode.substr(
              0,
              rawItem.securityCode.indexOf('.')
            );
            break;
        }
        this.itemsList.map(item => {
          const codeIndex = item.text.indexOf('.');
          if (
            item.text === text ||
            (codeIndex !== -1 && item.text.substr(0, codeIndex) === text)
          ) {
            item.status = ValidStatus.SUCCESS;
            this.portfolioItems.push(rawItem);
          }
        });
      }
    }
    this.itemsList.forEach(item => {
      if (item.status === ValidStatus.UNCHECK) {
        item.status = ValidStatus.FAIURE;
        errorStatus = true;
      }
    });
    if (errorStatus) {
      this.errorHandler(null);
      this.okDisabled = true;
      this.okDisabledFlag.emit(this.okDisabled);
    } else {
      this.errorHandler(true);
      this.okDisabled = false;
      this.okDisabledFlag.emit(this.okDisabled);
    }
  }

  removeItem($event) {
    this.itemsList = this.itemsList.filter(item => item.text !== $event.text);
    this.portfolioItems = this.portfolioItems.filter(
      item => item.companyName !== $event.text
    ); // 本行代码解决：自选添加企业，删掉某一条，该条仍然会被添加的问题
    this.count = this.itemsList.length;
    if (
      !this.itemsList.some(
        item =>
          item.status === ValidStatus.FAIURE ||
          item.status === ValidStatus.UNCHECK
      )
    ) {
      this.warningHidden = true;
      this.okDisabled = false;
      this.okDisabledFlag.emit(this.okDisabled);
    }
    if (this.itemsList.some(item => item.status === ValidStatus.UNCHECK)) {
      this.itemsChange(null);
    }
    if (this.itemsList.length < 1) this.okDisabled = true;
    this.okDisabledFlag.emit(this.okDisabled);
  }
}



