import { Component, ViewChild, EventEmitter, Input, Output, OnInit, ElementRef, TemplateRef, ViewContainerRef } from '@angular/core';

import { PortfolioItemVo } from '../entities';
import { Portfolio } from '../../shared';
import { RuleService } from '../rule.service';
import { Regulation } from '../../shared';

import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import { MsgService } from 'app/core/msg.service';
import { ModalDirective } from 'app/shared/ngx-bootstrap';
@Component({
  selector: 'app-ruleselect',
  templateUrl: 'rule-select.component.html',
  styleUrls: ['rule-select.component.css']
})
export class RuleSelectComponent implements OnInit {
  @ViewChild('ruleSelectModal') public ruleSelectModal: ModalDirective;
  @Output()
  ruleSelectFinish: EventEmitter<any> = new EventEmitter<any>();
  @Output()
  loadingStart: EventEmitter<any> = new EventEmitter<any>();
  @Output()
  regulation: EventEmitter<any> = new EventEmitter<any>();
  @Output()
  ruleSelectedItem: EventEmitter<any> = new EventEmitter<any>();
  @Output()
  okDisabledFlag: EventEmitter<any> = new EventEmitter<any>();
  @Input()
  set regulationType(regulationType: number) {
    this.regulationId = regulationType;
  }

  ruleList: Regulation[];
  ruleSelected: Regulation;
  portfolioSelected: Portfolio;
  regulationId: number;
  constructor(private ruleService: RuleService, private msg: MsgService, private toastr: ToastsManager) {
  }

  ngOnInit() {
  }

  generateRuleRegulationList(data) {
    this.ruleList = [];
    if (!data) {
      return;
    }
    for (let item of data) {
      if (+item.regulation_id === 31) continue;
      let rule = new Regulation();
      rule.generateByRaw(item);
      this.ruleList.push(rule);
    }
    if (this.ruleList.length > 0) {
      this.ruleSelected = this.ruleList[0];
      this.ruleSelectedItem.emit(this.ruleSelected);
    }
  }

  okClick() {
    this.loadingStart.emit(true);
    this.ruleSelectModal.hide();
    this.ruleService.getCompanyListByRule(this.ruleSelected.id).subscribe(
      data => {
        if (data == null) {
          data = [];
        }
        this.regulation.emit(this.ruleSelected);
        this.ruleSelectFinish.emit(data);
      },
      error => console.log('error:', error)
    );
  }

  selectRule(item: Regulation) {
    this.ruleSelected = item;
    this.okClick();
  }

  selectRuleItem(item: Regulation) {
    this.ruleSelectedItem.emit(item);
    this.okDisabledFlag.emit(false);
  }

  show() {
    this.ruleService.getRegulationList(this.regulationId).subscribe(
      data => this.generateRuleRegulationList(data),
      error => console.log('error:', error)
    );
    this.ruleSelectModal.config.backdrop = false;
    this.ruleSelectModal.show();
  }
  deleteRegulation(id) {
    this.msg.confirm('确定删除吗?').result
      .then(result => {
        this.ruleService.deleteRuleRegulationList(id).subscribe(
          data => {
            this.toastr.success('删除成功');
            this.show();
          },
          error => {
            this.toastr.error('该规则已被组合使用，无法删除');
          }
        );
      });
  }
  hide() {
    this.ruleSelectModal.hide();
  }

}



