import { Injectable } from '@angular/core';
import { BondQueryParams, BondCreditAdjustSaveParam } from './bond';
import { Bond, PagedResult } from '../../shared';
import { BondParty } from '../../shared/entities/bond/bond-party';
import { BondPledge } from '../../shared/entities/bond/bond-pledge';
import { BondWarrantor } from '../../shared/entities/bond/bond-warrantor';
import { HttpClient } from '@angular/common/http';
@Injectable()
export class BondService {
  public companyName: string;
  public companyId: number;
  public companyIsAdd: boolean;
  public bondAddFinishStatus: boolean;
  // 债券
  private getBondDetailApi = '/bonds/bond';
  public notAllowRatingBondTypeList = [
    '住房抵押贷款证券化', '政策性银行债', '信贷资产证券化', '地方政府债', '国债',
    '同业存单', '资产支持票据', '国际机构债券', '央行票据', '券商专项资产管理',
    '未知', '政府支持机构债', '政策性银行次级债', '不动产投资信托(REITs)', '汽车抵押贷款证券化',
  ];

  constructor(private http: HttpClient) { }

  search(params: BondQueryParams) {
    window.localStorage.setItem('bondSearchParams', JSON.stringify(params));
    return this.http.post<PagedResult<Bond>>('/bonds/search', params);
  }

  getFactorOption(factorCode: string) {
    return this.http.get('/bond/model/factor_option/factor_code/' + factorCode);
  }

  getCompanyIssueBondList(companyId: number) {
    return this.http.get(`/bond/${companyId}/basics?size=1000`);
  }

  // 获取债券基本信息
  getBondDetail(companyId: number) {
    return this.http.get<Bond>(this.getBondDetailApi + `/${companyId}`);
  }

  // 新增债券基本信息
  createBond(bond: Bond) {
    return this.http.post<Bond>(this.getBondDetailApi, bond);
  }

  // 编辑债券基本信息
  updateBond(bondId: number, bond: Bond) {
    return this.http.put(this.getBondDetailApi + `/${bondId}`, bond);
  }

  // 删除债券
  delBond(bondId: number) {
    return this.http.delete(this.getBondDetailApi + `/${bondId}`);
  }

  // 获取债券类型
  getSecurityTypes() {
    return this.http.get<any>('/lookup/bond/securityTypes');
  }

  // 获取债券发行发行
  getIssueTypes() {
    return this.http.get<any>(`/lookup/bond/issueTypes`);
  }

  // 获取债券利率类型
  getCouponTypes() {
    return this.http.get<any>(`/lookup/bond/couponTypes`);
  }

  // 判断债券是否存在
  bondExists(name: string, market: string) {
    return this.http.get(`/bonds/bond/exist`, {params: {'bondName': name, 'tradeMarket': market}});
  }

  // 新增债券当事人
  updateBondParty(bondParty: BondParty) {
    return this.http.post('/bonds/party', bondParty);
  }

  // 删除债券当事人
  deleteBondParty(bondPartySid: number) {
    return this.http.delete(`/bonds/party/${bondPartySid}`);
  }

  // 获取抵押品信息
  getBondPledgeDetail(companyId: number) {
    return this.http.get<BondPledge[]>(`/bonds/${companyId}/pledges`);
  }

  // 获取债券抵押品类型
  getPledgeTypes() {
    return this.http.get<any>(`/lookup/bond/pledgeTypes`);
  }

  // 新增债券抵质押品
  updateBondPledge(bondPledge: BondPledge) {
    return this.http.post('/bonds/pledge', bondPledge);
  }

  // 删除债券抵质押品
  deleteBondPledge(id: number) {
    return this.http.delete('/bonds/pledge/' + id);
  }

  // 获取债券第三方抵押类型
  getPledgeDepends() {
    return this.http.get<any>(`/lookup/bond/pledgeDepends`);
  }

  // 获取债券抵债追偿类型
  getPledgeControls() {
    return this.http.get<any>(`/lookup/bond/pledgeControls`);
  }

  // 获得省市
  getProvinces() {
    return this.http.get<any>('/regions/provinces');
  }

  // 获取保证人信息
  getBondWarrantorDetail(secinnerId: number) {
    return this.http.get<BondWarrantor[]>(`/bonds/${secinnerId}/warrantors`);
  }

  // 获取保证人类型
  getWarrantorTypes() {
    return this.http.get<any>(`/lookup/bond/warrantorTypes`);
  }

  // 获取保证人担保方式
  getGuaranteeTypes() {
    return this.http.get<any>(`/lookup/bond/guaranteeTypes`);
  }

  // 新增债券担保人
  updateBondWarrantor(bondWarrantor: BondWarrantor) {
    return this.http.post('/bonds/warrantor', bondWarrantor);
  }

  // 删除债券担保人
  deleteBondWarrantor(id: number) {
    return this.http.delete(`/bonds/warrantor/${id}`);
  }

  // 获取保证人担保强度
  getWarrantyStrengthTypes() {
    return this.http.get<any>(`/lookup/bond/warrantyStrengthTypes`);
  }

  // 获取内外评信息
  getOuterRatingList(secInnerId: number) {
    return this.http.get(`/bonds/outerRating/secinnerId/${secInnerId}`);
  }

  getInnerRatingList(secInnerId: number) {
    return this.http.get(`/bonds/rating/secinnerId/${secInnerId}`);
  }

  // 获取相同发行人信息
  getSameIssuer(secinnerId: number, page: number) {
    return this.http.get(`/bonds/${secinnerId}/sameIssuer?page=${page}`);
  }

  // 获取发行人信息
  getIssuer(secinnerId: number) {
    return this.http.get<BondParty[]>(`/bonds/${secinnerId}/party/6502/parties`);
  }

  // 获取承销商信息
  getUnderwriter(secinnerId: number) {
    return this.http.get<BondParty[]>(`/bonds/${secinnerId}/party/6495/parties`);
  }

  // 根据企业名称精确查询企业信息
  getCompanyByName(name: string) {
    return this.http.get<any>(`/companies/`, {params: {'companyName': name}});
  }

  getCompany(companyId: number) {
    return this.http.get(`/company/${companyId}/basicinfo`);
  }

  // 获取某一个债券的债券评级
  getBondCredit(secInnerId) {
    return this.http.get(`/bonds/rating/calculate/secinnerId/${secInnerId}`);
  }

  // 批量计算债券评级
  calculateBondsCredits(ratingRecord: any) {
    return this.http.post<any>(`/bonds/rating/calculate/ratingRecord`, ratingRecord);
  }

  getBondCreditScale() {
    return this.http.get(`/ratingModels/scales`);
  }

  // 提交调整后的债券评级结果
  adjustBondCredit(param: BondCreditAdjustSaveParam) {
    return this.http.post(`/bonds/rating/record/add`, param);
  }

  /**
   * 导出债券
   */
  exportBond(bondId: number) {
    return this.http.get(`/bonds/${bondId}/excel`, {observe: 'response', responseType: 'blob'});
  }

  /**
   * 批量获取债券评级records
   *
   * @param bondRatingRecordIds
   */
  getBondRatingRecords(bondRatingRecordIds: number[]) {
    return this.http.post(`/bonds/rating/ratingRecords`, bondRatingRecordIds);
  }

  /**
   * 根据债券名称或债券简称或债券代码查询债券信息
   */
  getBondsByTypes(type: number, types: string[]) {
    return this.http.post<any>(`/bonds/${type}/property`, types);
  }

  // 通过债券名称或债券简称或债券代码的部分信息查询债券信息
  getBondShortNameListForTypeaHead(keyword: string) {
    return this.http.get<any>(`/bonds/2/complete?bondProperty=${keyword}`)
      .map(el => {
        return el ? el.map(data => ({ id: data.secinnerId, name: data.securityShortName })) : null;
      });
  }

  getBondNameListForTypeaHead(keyword: string) {
    return this.http.get<any>(`/bonds/1/complete?bondProperty=${keyword}`)
      .map(el => {
        return el ? el.map(data => ({ id: data.secinnerId, name: data.securityName })) : null;
      });
  }

  getBondCodeListForTypeaHead(keyword: string) {
    return this.http.get<any>(`/bonds/3/complete?bondProperty=${keyword}`)
      .map(el => {
        return el ? el.map(data => ({ id: data.secinnerId, name: data.securityCode })) : null;
      });
  }

  saveBondsCalculatedRecords(calculateRecords: any[]) {
    return this.http.post(`/bonds/rating/calculatedRecords/add`, calculateRecords);
  }

  updateBondsRatingRecordStatus(ratingRecordId: number) {
    return this.http.put(`/bonds/rating/records/update/${ratingRecordId}`, ratingRecordId);
  }

  /**
   * 生成债券评级报告
   * @param companyRatingRecordId
   */
  saveBondRatingReport(companyRatingRecordId: number) {
    return this.http.post(`/bonds/companyRating/${companyRatingRecordId}/report`, null);
  }

  downloadBondRatingReport(bondRatingRecordId: number) {
    return this.http.get(`/bonds/rating/{bondRatingRecordId}/report`, {observe: 'response', responseType: 'blob'});
  }

  /**
   * 根据债券id集合获取债券信息集合
   */
  getBondsByIds(bondIds: number[]) {
    return this.http.post('/bonds/bondIds', bondIds);
  }

}



