import { Component, OnInit,  ViewChild } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { Observable } from 'rxjs/Observable';

import { ToastsManager } from 'ng2-toastr/ng2-toastr';

import { CompanyService } from '../../company/company.service';
import { FinanceService } from '../../company/finance.service';
import { ModelsService } from '../models.service';
import { FactorService } from '../factor.service';
import { ElementService } from '../element.service';
import { FormulaEditorComponent } from './formula-editor/formula-editor.component';
import { MsgService } from 'app/core/msg.service';
declare let math: any;

@Component({
  selector: 'app-factor-config',
  templateUrl: './factor-config.component.html',
  styleUrls: ['./factor-config.component.css']
})
export class FactorConfigComponent implements OnInit {

  currentType: any;
  factors;
  factorTypes;
  private factorTypesForSelect;
  factorLib: string;
  public factorLibName = { 'qual': '企业定性指标', 'quan': '企业定量指标', 'region': '区域指标', 'special': '特例调整指标' };
  isCollapsed = true;
  private origFactor;
  private factorCopy;
  vars = new Map<string, string>();
  accountTypes;
  flatData;
  treeData;
  specData;
  category = 'compy_bankaddfin';
  finReport = 'compy_balancesheet';
  rptTemplate = 0;
  elements;
  elementsShown;
  elementsSelected = [];
  elementTypes;

  @ViewChild('formulaEditor')
  formulaEditor: FormulaEditorComponent;

  constructor(private modelsService: ModelsService, private factorService: FactorService,
    private financeService: FinanceService, private elementService: ElementService,
    private companyService: CompanyService, private toastr: ToastsManager,
    private msg: MsgService, private route: ActivatedRoute) {
  }

  set factor(value) {
    this.origFactor = value;
    this.factorCopy = JSON.parse(JSON.stringify(value));
  }
  get factor() {
    return this.factorCopy;
  }


  ngOnInit() {
    this.route.params.forEach(params => {
      this.factorLib = params['type'];
      this.currentType = null;
      this.getFactorTypes();
      if (this.factorLib === 'quan') {
        this.factorService.getAccountSubjectTypes().subscribe(data => {
          this.accountTypes = data;
          this.factorService.getTemplateTypes().subscribe(temps => {
            this.accountTypes.templateTypes = temps;
          });
          this.accountTypes.specTypes = data.specialType.concat(data.elementType.map(it => ({ nameEn: it.name, nameCh: it.name })));
        });

        this.factorService.getAccountSubjects().subscribe(data => {
          this.flatData = data;
          let addToMap = it => {
            if (it.isLabel) { return; }
            it.subjectNameCh = it.subjectNameCh.trim();
            this.vars[it.subjectNameEn] = it;
            if (it.subjectLastYear) { this.vars[it.subjectLastYear] = it; }
            if (it.subjectBlastYear) { this.vars[it.subjectBlastYear] = it; }
          };
          data.specialSubjects.forEach(addToMap);
          data.elements.forEach(addToMap);
          data.financialSubjects.forEach(addToMap);
          data.elements.forEach(it => it.subjectLevel = 1);
          this.generateTreeData();
          this.generateSpecData();
        });
      } else if (this.factorLib === 'qual') {
        Observable.forkJoin(this.elementService.getElement(),
          this.elementService.getElementList(null)).subscribe(data => {
            this.elementTypes = data[0];
            this.elements = data[1];
            // if (this.elementTypes.length > 0) {
            //   this.category = this.elementTypes[0].name;
            // }
            // this.onElementTypeChange();
          });
      }
    });

  }

  private onElementTypeChange() {
    this.elementsShown = this.elements.filter(it => it.type === this.category);
  }

  private generateTreeData() {
    let tree = [];
    let data = this.flatData.financialSubjects.filter(it => it.subjectTypeName === this.finReport
      && it.companyType === +this.rptTemplate);
    let getChildren = subject => {
      return {
        data: subject, children: data.filter(child => subject.subjectCode != null && subject.subjectCode === child.parentSubjectCode)
          .map(it => getChildren(it)), collapsed: true
      };
    };
    data.filter(it => it.subjectLevel === 1).forEach(it => {
      tree.push(getChildren(it));
    });
    this.treeData = tree;
  }

  private generateSpecData() {
    let tree = [];
    let data = this.flatData.elements.concat(this.flatData.specialSubjects).filter(it => it.subjectTypeName === this.category);
    let getChildren = subject => {
      return {
        data: subject, children: data.filter(child => subject.subjectCode != null && subject.subjectCode === child.parentSubjectCode)
          .map(it => getChildren(it)), collapsed: true
      };
    };
    data.filter(it => it.subjectLevel === 1).forEach(it => {
      tree.push(getChildren(it));
    });
    this.specData = tree;
    console.log(this.specData);
  }

  private getFactorTypes() {
    let sub = null;
    switch (this.factorLib) {
      case 'quan':
        sub = this.factorService.getQuanTypes();
        break;
      case 'qual':
        sub = this.factorService.getQualTypes();
        break;
      case 'region':
        sub = this.factorService.getAreaTypes();
        break;
      case 'special':
        sub = this.factorService.getSpecialTypes();
        break;
    }
    if (sub) {
      sub.subscribe(data => {
        if (this.factorLib === 'special') {
          data = data.map(element => {
            return { name: element };
          });
        }
        this.factorTypes = data;
        this.factorTypesForSelect = data.map(it => ({ id: it.name, text: it.name }));
        if (data && data.length > 0) {
          this.getFactors(this.currentType || data[0]);
        } else {
          this.factors = [];
          this.currentType = '';
          this.isCollapsed = true;
        }
      });
    }
  }

  editType(item) {
    item.nameCopy = item.name;
    item.editing = true;
  }
  saveType(item) {
    item.name = item.name.trim();
    if (!item.name) {
      this.toastr.error('该名字不能为空');
      return;
    }
    if (item.nameCopy === item.name) {
      item.editing = false;
      return;
    }
    if (this.factorTypes.find(it => (it.nameCopy || it.name) === item.name) != null) {
      this.toastr.error('该名字的类别已经存在');
      return;
    }
    item.editing = false;
    (this.factorLib === 'quan' ? this.factorService.changeQuanType(item.nameCopy, item.name) :
      this.factorLib === 'qual' ? this.factorService.changeQualType(item.nameCopy, item.name) :
        this.factorLib === 'region' ? this.factorService.changeAreaType(item.nameCopy, item.name) :
          this.factorService.changeSpecialType(item.nameCopy, item.name)).subscribe(null, error => this.toastr.error(error));
    delete item.nameCopy;
  }
  cancelType(item) {
    item.name = item.nameCopy;
    item.editing = false;
    delete item.nameCopy;
  }


  getFactors(type) {
    let sub = null;
    switch (this.factorLib) {
      case 'quan':
        sub = this.factorService.getQuanFactors(type.name);
        break;
      case 'qual':
        sub = this.factorService.getQualFactors(type.name);
        break;
      case 'region':
        sub = this.factorService.getAreaFactors(type.name);
        break;
      case 'special':
        sub = this.factorService.getSpecialFactors(type.name);
        break;
    }
    if (sub) {
      sub.subscribe(data => {
        this.factors = data;
        this.currentType = type;
        this.isCollapsed = true;
      });
    }
  }

  showFactors(lib) {
    this.factorLib = lib;
    this.getFactorTypes();
  }

  addFactor() {
    this.isCollapsed = false;
    this.factor = { isNew: true };
    if (this.factorLib === 'special') {
      this.factor.adjustType = (<any>this.currentType).name;
    } else {
      this.factor.factorType = (<any>this.currentType).name;
    }
  }

  removeFactor(item) {
    this.msg.confirm('确定要删除？').result
      .then(result => {
        (this.factorLib === 'quan' ? this.factorService.deleteQuan(item) :
          this.factorLib === 'qual' ? this.factorService.deleteQual(item) :
            this.factorLib === 'region' ? this.factorService.deleteArea(item) :
              this.factorService.deleteSpecial(item)).subscribe(data => {

              if (this.factors.length === 1) { // delete last factor in category
                this.currentType = null;
              }
              this.getFactorTypes();
              this.toastr.success('成功删除该指标');
            }, error => {
              this.toastr.error('删除该指标失败');
            });
      });

  }

  editFactor(item) {
    this.isCollapsed = false;
    this.factor = item;
    if (this.factorLib === 'qual') {
      this.elementsSelected.forEach(it => it.selected = false);
      this.elementsSelected.length = 0;
      if (item.relatedElements) {
        item.relatedElements.forEach(it => this.elementsSelected.push(this.elements.find(e => e.code === it.code)));
        this.elementsSelected.forEach(it => it.selected = true);
      }
    }
  }

  private valid(f): boolean {
    if (this.factorLib !== 'special' && !f.factorCode) {
      this.toastr.error('指标代码不能为空');
      return false;
    }
    if (!((f.factorName && f.factorName.trim()) || (f.adjustName && f.adjustName.trim()))) {
      this.toastr.error('指标名称不能为空');
      return false;
    }
    if (!((f.factorType && f.factorType.trim()) || (f.adjustType && f.adjustType.trim()))) {
      this.toastr.error('指标分类不能为空');
      return false;
    }
    if (this.factorLib === 'quan') {
      if (!f.format) {
        this.toastr.error('显示格式不能为空');
        return false;
      }
      if (f.propertyCode == null) {
        this.toastr.error('请选择指标性质');
        return false;
      }
      if (f.direction == null) {
        this.toastr.error('请选择风险指向');
        return false;
      }
      if (!f.formulaEn) {
        this.toastr.error('计算表达式不能为空');
        return false;
      }
      try {
        math.parse(f.formulaEn);
      } catch (e) {
        this.toastr.error('计算表达式错误');
        return false;
      }
    } else if (this.factorLib === 'qual') {
      if (!f.elementCodes || f.elementCodes.length === 0) {
        this.toastr.error('定性指标必须包含一个字段');
        return false;
      }
    } else if (this.factorLib === 'special') {
      if (!f.direction) {
        this.toastr.error('调整方向不能为空');
        return false;
      }
      if (!f.importance) {
        this.toastr.error('调整程度不能为空');
        return false;
      }
      if (this.factors.find(it => (it.adjustName === f.adjustName && it.adjustCode !== f.adjustCode))) {
        this.toastr.error('该指标已存在');
        return false;
      }
    }
    return true;
  }

  saveFactor() {
    if (this.factorLib === 'quan') {
      this.formulaEditor.show();
      this.factor.formulaEn = this.formulaEditor.enFormula;
      this.factor.formulaCh = this.formulaEditor.cnFormula;
    } else if (this.factorLib === 'qual') {
      this.factor.elementCodes = this.elementsSelected.map(it => it.code);
    }
    if (!this.valid(this.factor)) {
      return;
    }
    let obs;
    if (this.factor.isNew) {
      // delete this.factor.isNew;
      obs = this.factorLib === 'quan' ? this.factorService.QuanExists(this.factor.factorCode) :
        this.factorLib === 'qual' ? this.factorService.QualExists(this.factor.factorCode) :
          this.factorLib === 'region' ? this.factorService.AreaExists(this.factor.factorCode) :
            this.factorService.checkSpecialExist(this.factor.adjustType, this.factor.adjustName);
      obs = obs.flatMap(exists => exists ? Observable.throw('该指标已存在') :
        (this.factorLib === 'quan' ? this.factorService.createQuan(this.factor) :
          this.factorLib === 'qual' ? this.factorService.createQual(this.factor) :
            this.factorLib === 'region' ? this.factorService.createArea(this.factor) :
              this.factorService.createSpecial(this.factor))
      );
    } else {
      obs = this.factorLib === 'quan' ? this.factorService.updateQuan(this.factor) :
        this.factorLib === 'qual' ? this.factorService.updateQual(this.factor) :
          this.factorLib === 'region' ? this.factorService.updateArea(this.factor) : (
            this.factors.find(it => it.adjustType === this.factor.adjustType) ? obs = this.factorService.updateSpecial(this.factor) :
            this.factorService.checkSpecialExist(this.factor.adjustType, this.factor.adjustName).flatMap(
              exists => exists ? Observable.throw('该指标已存在') : obs = this.factorService.updateSpecial(this.factor)
            )
          );
    }
    obs.subscribe(data => {
      this.toastr.success('成功保存该指标');
      this.getFactorTypes();
      this.category = '';
      this.elementsShown = [];
    }, error => {
      this.toastr.error(error, '保存该指标失败');
      this.category = '';
      this.elementsShown = [];
    });
    this.isCollapsed = true;
  }

  cancel() {
    this.isCollapsed = true;
    this.category = '';
    this.elementsShown = [];
  }

  addElement(item) {
    item.selected = true;
    this.elementsSelected.push(item);
  }

  removeElement(item) {
    item.selected = false;
    this.elementsSelected.splice(this.elementsSelected.indexOf(item), 1);
  }

}



