import { NgModule, ApplicationRef } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';

import { CircleComponent } from './components/spinner/circle/circle';
import { CompanyRatingPipe } from './pipes/company-rating-pipe';
import { CustomSelectComponent } from './components/custom-select/custom-select.component';
import { RatingViewComponent } from './components/rating-view.component';
import { nvD3 } from './components/nvd3.component';
import { PrivilegeDirective } from './directives/privilege.directive';
import { VisiblePrivilegeDirective } from './directives/visible-privilege.directive';
import { AutosizeTextareaDirective } from './directives/autosize-textarea.directive';

import { RatingFluidComponent } from './components/rating-fluid.component';
import { SelectModule } from './components/select/select.module';
import { CompanyElementPipe } from './pipes/company-element-pipe';
import { SearchFilterComponent } from './components/search-filter/search-filter.component';
import { DatexPipe } from './pipes/datex-pipe';
import { NgxDatatableModule } from '@swimlane/ngx-datatable/release';
import { CollapseModule } from './ngx-bootstrap/collapse';
import { BsDropdownModule } from './ngx-bootstrap/dropdown';
import { TabsModule } from './ngx-bootstrap/tabs';
import { PaginationModule } from './ngx-bootstrap/pagination';
import { TypeaheadModule } from './ngx-bootstrap/typeahead';
import { DatepickerModule } from './ngx-bootstrap/datepicker';
import { PopoverModule } from './ngx-bootstrap/popover';
import { ModalModule } from './ngx-bootstrap/modal';
import { WarningPopupComponent } from 'app/shared/components/warning-popup/warning-popup.component';

@NgModule({
  declarations: [
    CircleComponent, CustomSelectComponent, nvD3, CompanyRatingPipe,
    RatingViewComponent, PrivilegeDirective, VisiblePrivilegeDirective, AutosizeTextareaDirective,
    RatingFluidComponent, CompanyElementPipe, SearchFilterComponent, DatexPipe, WarningPopupComponent
  ],
  imports: [
    CommonModule,
    SelectModule,
    FormsModule,
    NgxDatatableModule,
    TypeaheadModule.forRoot(),
    PaginationModule.forRoot(),
    BsDropdownModule.forRoot(),
    TabsModule.forRoot(),
    CollapseModule.forRoot(),
    DatepickerModule.forRoot(),
    PopoverModule.forRoot(),
    ModalModule.forRoot()
  ],
  exports: [
    CircleComponent,
    CustomSelectComponent,
    nvD3,
    CompanyRatingPipe,
    RatingViewComponent,
    PrivilegeDirective,
    VisiblePrivilegeDirective,
    CommonModule,
    AutosizeTextareaDirective,
    RatingFluidComponent,
    CompanyElementPipe,
    SelectModule,
    FormsModule,
    TypeaheadModule,
    PaginationModule,
    BsDropdownModule,
    TabsModule,
    CollapseModule,
    DatepickerModule,
    PopoverModule,
    ModalModule,
    NgxDatatableModule,
    SearchFilterComponent,
    DatexPipe,
    WarningPopupComponent
  ]
})
export class SharedModule {

}



