import { BaseClass } from '../base-class';

// 用户对应实体对象
export class User extends BaseClass {
  userName: string; // 用户名
  displayName: string; // 全名
  email: string; // 邮箱
  phone: string; // 电话
  clientId: number; // 用户对应的唯一id
  userId: number; // 用户记录id
  role: string; // 用户角色
  createByDisplayName: string;
}

export class UserPrivileges extends BaseClass {
  type: string;
  items: UserPrivilegesItem[];
  step: number;
}
export class UserPrivilegesItem extends BaseClass {
  name: string;
  value: boolean;
  assignable?: boolean;
}
export class SaveUserPrivilegesItem extends BaseClass {
  type: string;
  name: string;
}
export class UserItem extends BaseClass {
  item: string;
}
export class UserPassword extends BaseClass {
  oldPassword: string;
  newPassword: string;
  confirmNewPasswordT: string;
}
export class SystemConfig extends BaseClass {
  name: string;
  value: string;
  valueSupplement: string;
  month: string;
  day: string;
  valueMode1: string;
  valueMode2: string;
  name1: string;
  name2: string;
}



