import { Task } from './task';
import { BaseClass } from '../base-class';
export class Workflow extends BaseClass {
  workflowSid: number;
  workflowName: string;
  targetName: string;
  targetId: number;
  context: number;
  workflowType: string;
  workflowStatus: string;
  comment: string;
  clientId: number;
  createUser: string;
  createUserName: string;
  createTime: string;
  updateUser: string;
  updateTime: string;
  tasks: Task[];
  ishover: boolean;

  generateByRaw(raw: any) {
    super.generateByRaw(raw);
    this.tasks = [];
    if (raw.tasks && Array.isArray(raw.tasks) && raw.tasks.length > 0) {
      for (let item of raw.tasks) {
        let task = new Task();
        task.generateByRaw(item);
        this.tasks.push(task);
      }
    }
  }
}



