import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import 'rxjs/add/operator/catch';

import { CompanySearchParams } from '../shared/entities/company/company-search-params';
import { Company } from '../shared/entities/company/company';
import { Portfolio, PagedResult, RatingBrief} from '../shared';
import { PortfolioItemVo } from './../portfolio/entities/portfolio-item';
import { CompanyVo } from './entities/company-vo';
import { QualFactor } from './entities/company-qualfactor';

@Injectable()
export class CompanyService {
  public companyEditFinanceStepPass: boolean;
  public companyEditEntity: Company;
  public followCompanys: number[];
  public portfolio: Portfolio;
  public isFromList: boolean;

  private companyApiPrefix = '/companies';
  private industyApiPrefix = '/exposures';
  private regionApiPrefix = '/regions';

  public specialCompanyElementList = [
    '公司人数', '公司的航线数量', '利用小时数（小时/年）', '经营年数', '公司人数', '公司拥有的飞机数量', '航线数量', '会计师事务所排名', '公司承销保荐及财务顾问业务净收入排名', '房地产企业排名',
    '公司收入占行业收入比排名', '建筑业企业排名', '经济业务净收入排名', '融资融券业务利息收入排名', '审计机构排名', '受托客户资产管理业务净收入排名', '自营投资业务净收入排名', '成立时间', '是否特大型企业'
  ];

  constructor(private http: HttpClient) { }

  /**
   * 删除企业
   */
  delCompany(companyId: number) {
    return this.http.delete(this.companyApiPrefix + `/${companyId}`);
  }

  /**
   * 企业新增和编辑
   */
  editCompany(companyEditEntity: Company) {
    return this.http.post<any>(this.companyApiPrefix + '/', companyEditEntity);
  }

  /**
   * 根据公司企业id集合获取公司信息集合
   */
  getCompaniesByIds(companyIds: number[]) {
    return this.http.post<PortfolioItemVo[]>(this.companyApiPrefix + '/companyIds', companyIds);
  }

  /**
   * 根据公司企业工商登记号集合获取公司信息集合
   */
  getCompaniesByBlnumbCodes(blnumbCodes: string[]) {
    return this.http.post<any>(this.companyApiPrefix + '/blnumbCodes', blnumbCodes);
  }

  /**
   * 根据公司名称集合获取公司信息集合
   */
  getCompaniesByCompanyNames(companyNames: string[]) {
    return this.http.post<any>(this.companyApiPrefix + '/companyNames', companyNames);
  }


  /**
   * 根据公司id获取公司基本信息
   */
  getCompanyDetail(companyId: number) {
    return this.http.get<Company>(this.companyApiPrefix + `/${companyId}`);
  }

  /**
   * 根据公司id获取公司三大财务报表
   */
  getCompanyFinancial(companyId: number) {
    return this.http.get(`/finance/${companyId}`);
  }

  /**
   * 获取公司固定财务指标
   */
  getCompanyFixQuanfactor(companyId: number) {
    return this.http.get(this.companyApiPrefix + `/${companyId}/quanfactors?type=fix`);
  }

  /**
   * 获取公司非固定财务指标
   */
  getCompanyUnfixQuanfactor(companyId: number) {
    return this.http.get(this.companyApiPrefix + `/${companyId}/quanfactors?type=notfix`);
  }

  /**
   * 获取行业均值、中值、最大值、最小值
   */
  getIndustryQuanStatistics(industryId: number, quanfactorCode: string) {
    return this.http.post(this.industyApiPrefix + `/${industryId}/factorSummary`, [quanfactorCode]);
  }

  /**
   * 查询公司列表
   */
  searchCompanies(searchParams: CompanySearchParams) {
    window.localStorage.setItem('companySearchParams', JSON.stringify(searchParams));
    return this.http.post<PagedResult<CompanyVo>>(this.companyApiPrefix + '/search', searchParams);
  }

  getCompanyListForTypeaHead(keyword: string) {
    const url = encodeURI(this.companyApiPrefix + `/complete?companyName=${keyword}`);
    return this.http.get<any>(url)
    .map(el => {
      return el ? el.map(data => ({ id: data.id, name: data.name })) : [];
    });
  }

  /**
   * 获取行业信息
   */
  getExposures() {
    return this.http.get<any>(this.industyApiPrefix + '/');
  }

  /**
   * 获取省份
   */
  getProvinces() {
    return this.http.get<any>(this.regionApiPrefix + '/provinces');
  }

  /**
   * 获取省份下的城市
   */
  getCities(province: string) {
    return this.http.get<any>(this.regionApiPrefix + `/provinces/${province}/cities`);
  }

  /**
   *获取城市下的县城
   */
  getCountries(province: string, city: string) {
    return this.http.get<any>(this.regionApiPrefix + `/provinces/${province}/cities/${city}/counties`);
  }
  /**
   * 获取公司性质集合
   */
  getCompanyOrgForms() {
    return this.http.get<any>('/lookup/company/orgForms');
  }

  /**
   * 获取公司发行的债券
   */
  getCompanyIssueBonds(companyId: number) {
    return this.http.get<any>(this.companyApiPrefix + `/${companyId}/bonds`);
  }
  /**
  * 获取公司股东列表
  */
  getShareHolderList(companyId: number) {
    return this.http.get<any>(this.companyApiPrefix + `/${companyId}/shareholderHistory`);
  }
  /**
  * 查询公司的地区
  */
  getCompanyRegion(companyId: number) {
    return this.http.get<any>(this.companyApiPrefix + `/${companyId}/regions`);
  }

  /**
 * 查询区域经济实力与财政实力
 */
  getRegionPower(code: number) {
    return this.http.get(this.regionApiPrefix + `/${code}/quanfactors`);
  }

  getCompanyQualfactors(companyId: number) {
    return this.http.get<QualFactor[]>(this.companyApiPrefix + `/${companyId}/qualfactors`);
  }

  /**
   * 企业展台导出
   */
  exportCompany(companyId: number) {
    return this.http.get( this.companyApiPrefix + `/${companyId}/excel`, {observe: 'response', responseType: 'blob'});
  }

  getRatingBrief(companyId: number) {
    return this.http.get<RatingBrief>(`/ratingBrief/company/${companyId}`);
  }
}



