import {
  Component, enableProdMode, OnInit, OnDestroy, ViewChild,
  TemplateRef, trigger, state, style, transition, animate
} from '@angular/core';
import { NgClass } from '@angular/common';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { Observer } from 'rxjs/Observer';
import { Subscription } from 'rxjs/Subscription';
import { Subject } from 'rxjs/Subject';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import { BaseClass } from '../../shared/base-class';
import { AdminService } from '../admin.service';
import { UserPrivileges, UserPrivilegesItem, SaveUserPrivilegesItem } from '../../shared/index';
import { UserService } from '../../user/user.service';
import { MsgService } from 'app/core/msg.service';

@Component({
  selector: 'app-admin-user',
  templateUrl: 'admin-user.component.html',
  styleUrls: ['admin-user.component.css']
})
export class AdminUserComponent implements OnInit {
  currentRole: string; // 当前角色
  userPrivilegeList: UserPrivileges[];
  addStatus: boolean;
  editStatus: boolean;
  rolesList;
  isSub = false;
  private oldRole: string;
  private step: number;
  private previousModel;
  private userPrivilegesItems: UserPrivilegesItem[];
  isShow: boolean;
  private updateSub: Subscription;
  showAllowAuthorize = false;

  constructor(private router: Router, private activeRoute: ActivatedRoute,
    private adminService: AdminService, private msg: MsgService, public userService: UserService, private toastr: ToastsManager) {
  }

  ngOnInit() {
    this.rolesList = [];
    this.currentRole = '';
    this.getUserRoles();
    this.getUserPrivilege();
    this.addStatus = false;
    this.editStatus = false;
    this.isShow = true;
  }
  // 获取用户角色
  getUserRoles() {
    this.rolesList = [];
    this.adminService.getUserRoles().subscribe(data => {
      this.rolesList = [];
      if (this.userService.CurrentUser.role === 'admin') {
        data.filter(it => it.createByRoleId === this.userService.CurrentUser.roleId).forEach(element => {
          let children = data.filter(it => it.createByRoleId === element.roleId);
          if (children) {
            element.children = children;
          }
          this.rolesList.push(element);
        });
      } else {
        this.rolesList = data;
      }
      if (this.rolesList.length) { this.highlight(this.rolesList[0].roleName); }
    }, error => console.log('error', error)
    );
  }
  // 导航栏高亮显示
  highlight(role: string) {
    this.currentRole = role;
    this.oldRole = role;
    this.addStatus = false;
    this.editStatus = false;
    this.isShow = true;
    this.adminService.getUserPrivileges(this.currentRole).subscribe(
      data => {
        this.resetUserPrivilegesItem(); // 重置权限
        this.showAllowAuthorize = false;
        if (data && data.length > 0) {
          for (let item of data) {
            this.setUserPrivilegesItem(item.type, item.name);
            if (item.code === 'user_add' || item.code === 'user_edit') {
              this.showAllowAuthorize = true;
            }
          }
          if (this.showAllowAuthorize) {
            this.adminService.getRolePrivilegesAssign(role).subscribe(privileges => {
              this.resetAssignPrivilegesItem();
              for (let item of privileges) {
                this.setAssignPrivilegesItem(item.type, item.name);
              }
            });
          }
        }
      },
      error => console.log('error', error)
    );
  }

  // 新增用户角色
  addUserRoles() {
    this.addStatus = true;
    this.currentRole = '';
    this.isShow = false;
  }
  // 编辑用户角色
  editRoles() {
    this.editStatus = true;
  }
  // 保存新增用户角色
  saveNewRole() {
    if (this.editStatus === true) {
      this.editStatus = false;
      if (this.oldRole === this.currentRole) {
        return;
      }
      this.adminService.editUserRoles(this.oldRole, this.currentRole).subscribe(
        data => {
          this.oldRole = this.currentRole;
          this.getUserRoles(); // 刷新列表
          this.highlight(this.currentRole);
        },
        error =>  { if (this.currentRole === error) { this.toastr.error('该角色已经存在，请更换角色名'); } }
      );
    } else {
      this.addStatus = false;
      this.adminService.addUserRoles(this.currentRole).subscribe(
        data => {
          this.getUserRoles(); // 刷新列表
          this.highlight(this.currentRole);
        },
        error => { if (this.currentRole === error) { this.toastr.error('该角色已经存在，请更换角色名'); } }
      );
    }

  }

  getUserPrivilege() {
    this.adminService.getPrivilegesAssign().subscribe(
      data => {
        this.userPrivilegeList = [];
        if (data) {
          for (const item of data) {
            if (!this.isExistInArr(item.type, this.userPrivilegeList)) {
              const userPrivilegesTemp = new UserPrivileges();
              userPrivilegesTemp.type = item.type;
              userPrivilegesTemp.items = this.getUserPrivilegesItem(item.type, data);
              this.userPrivilegeList.push(userPrivilegesTemp);
            }
          }
        }
      },
      error => console.log('error', error)
    );
  }

  // 设置角色的权限
  togglePrivilege(type: string, name: string, add: boolean) {
    this.adminService.toggleRolePrivilege(this.currentRole, {type: type, name: name}, add).subscribe(data => {
      this.highlight(this.currentRole);
    }, error => {
      this.highlight(this.currentRole);
    });
  }

  // 设置角色能授予的权限
  togglePrivilegeAssign(type: string, name: string, add: boolean) {
    this.adminService.toggleRolePrivilegeAssign(this.currentRole, {type: type, name: name}, add).subscribe(data => {
      this.highlight(this.currentRole);
    }, error => {
      this.highlight(this.currentRole);
    });
  }


  // 设置角色
  setUserPrivilegesItem(type: string, name: string) {
    for (let item of this.userPrivilegeList) {
      if (item.type === type) {
        for (let userPrivilegesItem of item.items) {
          if (userPrivilegesItem.name === name) {
            userPrivilegesItem.value = true;
            if (userPrivilegesItem.value === true) {
              item.step++;
            }
          }
        }
      }
    }
  }

  setAssignPrivilegesItem(type, name) {
    for (let item of this.userPrivilegeList) {
      if (item.type === type) {
        for (let userPrivilegesItem of item.items) {
          if (userPrivilegesItem.name === name) {
            userPrivilegesItem.assignable = true;
          }
        }
      }
    }
  }


  // 重置所有权限
  resetUserPrivilegesItem() {
    for (let item of this.userPrivilegeList) {
      item.step = 0;
      for (let userPrivilegesItem of item.items) {
        userPrivilegesItem.value = false;
      }
    }
  }
  resetAssignPrivilegesItem() {
    for (let item of this.userPrivilegeList) {
      for (let userPrivilegesItem of item.items) {
        userPrivilegesItem.assignable = false;
      }
    }
  }

  // 判断是否存在
  isExistInArr(type: string, data: UserPrivileges[]) {
    let returnValue = false;
    for (let item of data) {
      if (item.type === type) {
        returnValue = true;
      }
    }
    return returnValue;
  }

  // 获取相同类型的权限
  getUserPrivilegesItem(type: string, data: any[]) {
    let userPrivilegesItems = [];
    for (let item of data) {
      if (item.type === type) {
        let userPrivilegesItemTemp = new UserPrivilegesItem();
        userPrivilegesItemTemp.name = item.name;
        userPrivilegesItemTemp.value = false;
        userPrivilegesItems.push(userPrivilegesItemTemp);
      }
    }

    return userPrivilegesItems;
  }

  // 删除角色
  delUserRoles() {
    this.msg.confirm('确定删除吗?').result
      .then(result => {
        this.adminService.delUserRoles(this.currentRole).subscribe(
          data => {
            this.msg.alert('删除成功');
            this.currentRole = 'admin';
            this.highlight(this.currentRole);
            this.getUserRoles();
          },
          error => {
            this.msg.alert('删除失败，请重试！');
          }
        );
      });

  }


  // 切换状态
  toggleCollapsed(item) {
    item.isCollapsed = !item.isCollapsed;
    if (this.previousModel && this.previousModel !== item) { this.previousModel.isCollapsed = false; }
    if (item.isCollapsed === true) {
      this.previousModel = item;
    }
  }

}



