import { RatingSubmodel } from './rating-submodel';
import { RatingCalcRefPd } from './rating-calc-ref-pd';

export class RatingModel {
  id: number;
  code: string;
  name: string;
  version: number;
  type: string;
  validFromDate: Date;
  validToDate: Date;
  clientId: number;
  msType: string;
  isActive: number;

  exposures: Array<any> = [];

  ratingSubModels: Array<RatingSubmodel> = [];
  ratingCalcRefPd: Array<RatingCalcRefPd> = [];
}



