export class CompanyRatingRecord {
  id: number;
  createDate: string;
  exposure: string;
  modelName: string;
  totalScore: number;
  adjustComment: string;
  scaledRating: string;
  ratingResult: string;
  initiator: string;
  scaledPd: number;
  factorDate: string;
  companyId: number;
  companyName: string;
  finalRting: string;
  exposureId: number;
  modelId: number;
  rawPd: number;
  rptTimetypeCd: number;
  scaledRawRating: string;
  scalingPoint: number;
  presentExposureName: string;
  ratingRecordDetail: RatingRecordDetailInfo[];
  warnings: any[];
}

export class RatingRecordDetailInfo {
  id: number;
  ratingRecordId: number;
  subModelId: number;
  score: number;
  subModelName: string;
  type: string;
  factorScore: FactorScore[];
}

export class FactorScore {
  companyFactorId: number;
  ratingModelFactorId: number;
  factorCode: string;
  factorName: string;
  factorRanking: number;
  factorValue: number;
  sum: number;
  initialValue: number;
  factorScore: number;
  comment: string;
}



