import { Injectable } from '@angular/core';
import { RatingModel } from './entities';
import { HttpClient } from '@angular/common/http';
@Injectable()
export class ModelsService {
  scaleTypes = [
    { name: '禁投级', level: 3, type: 'R' },
    { name: '投资级', level: 1, type: 'G' },
    { name: '审慎级', level: 2, type: 'Y' }
  ];

  allScales;


  constructor(private http: HttpClient) {
  }

  getLevel(rating: string): number {
    const s = this.allScales.find(it => it.rating === rating);
    if (s) {
      const t = this.scaleTypes.find(it => it.type === s.type);
      return t ? t.level : 0;
    }
    return 0;
  }

  getRatingModels() {
    return this.http.get('/ratingModels/client');
  }

  getSubModel(modelId: number) {
    return this.http.get(`/ratingModels/${modelId}/submodelFactor`);
  }

  getModel(modelId) {
    return this.http.get<RatingModel>(`/ratingModels/full/${modelId}`);
  }

  getHasUsed(modelId) {
    return this.http.get('/ratingModels/hasUsed', {params: {'modelId': modelId}});
  }

  saveModel(model, isNew = false) {
    return this.http.post('/ratingModels/saveAll', model, {params: {'isNew': isNew.toString()}});
  }

  deleteModel(model) {
    return this.http.delete('/ratingModels/delete/' + model.id);
  }

  private getFactors(exposureIds, urlSegment) {
    return this.http.get(`/ratingModels/${urlSegment}/`, {params: {'exposureId': exposureIds}});
  }

  getFactorsFin(exposureIds) {
    return this.getFactors(exposureIds, 'financeFactors');
  }

  getFactorsDiscrete(exposureIds) {
    return this.getFactors(exposureIds, 'discreteFactors');
  }

  getFactorsQuan(exposureIds) {
    return this.getFactors(exposureIds, 'quanFactors');
  }

  getFactorsDiscreteQuan(exposureIds) {
    return this.getFactors(exposureIds, 'discreteQuanFactors');
  }

  publish(model: RatingModel) {
    return this.http.put('/ratingModels/publish/', null,
    {params: {'code': model.code, 'version': model.version.toString()}});
  }

  revoke(model: RatingModel) {
    return this.http.put('/ratingModels/revoke/' + model.id, null);
  }

  getScale() {
    return this.http.get<any>('/ratingModels/scales').map(res => {
      this.allScales = res;
      return this.allScales;
    });
  }

  saveScales(scales) {
    return this.http.post('/ratingModels/saveScales', scales);
  }

  // ** warning model */
  getWarningsFinance() {
    return this.http.get('/warningModels/factorFinances/');
  }

  getWarningsExtra() {
    return this.http.get('/warningModels/others/');
  }

  updateWarningModelExtra(model) {
    return this.http.put('/warningModels/others/', model);
  }

  warningModelExists(name) {
    return this.http.get('/warningModels/factorFinances/name/exist', {params: {'name': name}});
  }

  updateWarningModel(model) {
    return this.http.post('/warningModels/factorFinances/', model);
  }

  deleteWarningModel(id) {
    return this.http.delete('/warningModels/factorFinances/' + id);
  }

}



