import { BaseClass } from '../base-class';
export class BondPledge extends BaseClass {
  bondPledgeSid: number;
  bondPledgeId: number;
  secinnerId: number; // 必填：证券内码标识符
  noticeDate: Date;	 // 选填：公告日期
  pledgeName: string;	// 必填：抵质押品名称
  pledgeType: string; // 必填：抵质押品类型
  pledgeDescription: string; // 选填：
  pledgeOwnerId: number; // 	选填：
  pledgeOwner: string;	// 选填：
  pledgeValue: number; // 选填：抵质押品价值
  priorityValue: number;	// 选填：优先求偿价值
  pledgeDepend: string; // 选填：抵质押品独立性
  pledgeControl: string; // 	选填：抵质押品控制力
  region: string; // 选填执法环境
  mitigationValue: string; // 	选填：执法环境
}



