import { Injectable } from '@angular/core';
import { FeedService } from '../core/feedAPI/feedAPI.service';
import { TaskSearchParams, Workflow, PagedResult } from '../shared/';
import { TaskStatusEnum } from './entities';
import { HttpClient } from '@angular/common/http';
@Injectable()
export class TaskService {

  private taskApiPrefix = '/workflow';
  private uploadApi = this.taskApiPrefix + '/upload';
  public status = TaskStatusEnum.WAIT_FOR_PRORESS;
  constructor(public feedService: FeedService, private http: HttpClient) { }

  /**
   * 获取待处理任务列表
   */
  getWaitForProcessTaskList(taskSearchParams: TaskSearchParams) {
    return this.http.post<any>(this.taskApiPrefix +
      `/inprogress/tasks?size=${taskSearchParams.size}&page=${taskSearchParams.page}`, taskSearchParams);
  }

  // 获取历史工作流列表
  getHistoryWorkflowList(taskSearchParams: TaskSearchParams) {
    return this.http.post<PagedResult<Workflow>>(this.taskApiPrefix +
      `/history/workflows?size=${taskSearchParams.size}&page=${taskSearchParams.page}`, taskSearchParams);
  }

  // 获取工作流详情
  getWorkflowDetail(workflowId: number) {
    return this.http.get<any>(this.taskApiPrefix + `/${workflowId}`);
  }

  // 评级工作流发起
  startCompanyRatingWorkflow(workflow: Workflow) {
    return this.http.post<any>(this.taskApiPrefix + '/', workflow);
  }

  // 评级认定
  affirmCompanyRating(taskId: number, params: any) {
    return this.http.post<any>(this.taskApiPrefix + `/task/${taskId}/directlyIdentified`, params);
  }

  // 评级审核
  approveCompanyRating(taskId: number, params: any) {
    return this.http.put(this.taskApiPrefix + `/task/${taskId}`, params);
  }

  // 提交复核
  submitForCompanyRatingApprove(taskId: number, params: any) {
    return this.http.post<any>(this.taskApiPrefix + `/task/${taskId}`, params);
  }

  // 复核通过
  passReviewCompanyRating(taskId: number, params: any, effectStartDate: string) {
    return this.http.post(this.taskApiPrefix + `/task/${taskId}/reviewApprove/${effectStartDate}`, params);
  }

  // 复核拒绝
  rejectReviewCompanyRating(taskId: number, params: any) {
    return this.http.post(this.taskApiPrefix + `/task/${taskId}/reviewReject`, params);
  }

  // 提交审核
  submitForExamineCompanyRating(taskId: number, params: any, examineUserName: string) {
    return this.http.post(this.taskApiPrefix + `/task/${taskId}/reviewToExamine`, params, {params: {'examineUserName': examineUserName}});
  }

  // 审核通过
  passExamineCompanyRating(taskId: number, params: any, effectStartDate: string) {
    return this.http.post(this.taskApiPrefix + `/task/${taskId}/examineApprove/${effectStartDate}`, params);
  }

  // 审核拒绝
  rejectExamineCompanyRating(taskId: number, params: any, affirmUserName: string) {
    return this.http.post(this.taskApiPrefix + `/task/${taskId}/examineReject`, params, {params: {'affirmUserName': affirmUserName}});
  }

  // 获取评级上传附件参数
  getUploaderOption(workflowId: number) {
    let token = this.feedService.getToken();
    return {
      url: this.feedService.getEndpoint() + this.uploadApi,
      authToken: token ? token.access_token : null,
      authTokenPrefix: token ? token.token_type : null,
      headers: {Authorization: `${token.token_type} ${token.access_token}`},
      data: {
        foreignkeySid: workflowId,
        attachmentDescription: ''
      }
    };
  }

  // 获取评级工作流所有的附件
  getWorkflowAttachments(workflowId: number) {
    return this.http.get<any>(this.taskApiPrefix + `/${workflowId}/attachments`);
  }

  // 下载评级附件
  downloadAttachment(attachmentId: number) {
    return this.http.get(this.taskApiPrefix + `/attachment/${attachmentId}`, {observe: 'response', responseType: 'blob'});
  }

  // 删除评级附件
  deleteAttachment(attachmentId: number) {
    return this.http.delete(this.taskApiPrefix + `/attachment/${attachmentId}`);
  }

  completeWarningTask(taskId, warning) {
    return this.http.put(`/workflow/updateWarningTask/taskId/${taskId}`, warning);
  }

  // 根据债券评级记录id获取工作流
  getWorkflowByBondRatingRecordId(bondRatingRecordId: number) {
    return this.http.get(this.taskApiPrefix + `/bondRatingRecord/${bondRatingRecordId}/tasks`);
  }

  // 评级有效期为系统生效时，获取生效时间（与财报日期有关）
  getEffectStartTime(taskId: number, params: any, effectStartDate: string) {
    return this.http.post<any>(this.taskApiPrefix + `/task/${taskId}/getEffectStartTime/${effectStartDate}`, params);
  }

  // 取消任务
  cancelTask(taskId: number, remark: string) {
    return this.http.get(this.taskApiPrefix + `/cancel/task/${taskId}/`, {params: {'remark': remark}});
  }
}



