import { Injectable } from '@angular/core';
import {
  HttpEvent,
  HttpInterceptor,
  HttpHandler,
  HttpRequest
} from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { AuthService } from 'app/core/auth/auth.service';
import { FeedService } from 'app/core/feedAPI/feedAPI.service';

@Injectable()
export class AuthInterceptor implements HttpInterceptor {
  constructor(private auth: AuthService, private feed: FeedService) {}

  intercept(
    req: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    let authHeader: string;
    let authReq: HttpRequest<any>;
    if (!req.headers.has('Authorization')) {
      // Get the auth header from the service.
      authHeader = this.auth.getAuthorizationHeader();
      // Clone the request to add the new header.
      authReq = req.clone({ setHeaders: { Authorization: authHeader }, url: this.feed.getFullUrl(req.url) });
    } else {
      authReq = req.clone({ url: this.feed.getFullUrl(req.url) });
    }
    // Pass on the cloned request instead of the original request.
    return next.handle(authReq).catch((error) => this.feed.handleError(error));
  }
}



