import {
  Component, OnInit, OnDestroy, ViewChild, TemplateRef,
  trigger, state, style, transition, animate, HostListener
} from '@angular/core';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { WarningService } from '../warning.service';
import { Warning, WarningQueryParam, WarningType } from '../../shared';
import { Observable } from 'rxjs/Observable';

@Component({
  selector: 'app-warning-list',
  templateUrl: 'warning.component.html',
  styleUrls: ['warning.component.css']
})
export class WarningComponent implements OnInit, OnDestroy {
  warningDetailState: string;
  warningTypeList: WarningType[];
  filterWarningTypeList: WarningType[];
  warningTypeSelected: WarningType;
  warningTypeAll: WarningType;
  warningImportanceList: any[] = [
    { importance: 3, value: '高', status: true },
    { importance: 2, value: '中', status: true },
    { importance: 1, value: '低', status: true },
  ];
  importanceSelected: any[];
  warningList: Warning[];
  warningQueryParam: WarningQueryParam;
  activeWarning: Warning;
  total: number;
  itemsPerPage: number;
  currentPage: number;
  loading: boolean;
  inputSearchOrRemove: boolean; // 点击搜索后显示叉，点击叉后显示搜索。
  typeaheadSource: Observable<any>; // 联想词数据源
  isInputFocus: boolean;
  sub: Subscription;

  constructor(private router: Router, private warningService: WarningService) {
  }

  ngOnInit() {
    window.scrollTo(0, 0);
    this.isInputFocus = false;
    this.warningDetailState = 'out';
    this.warningTypeList = [];
    this.warningTypeAll = new WarningType();
    this.warningTypeAll.id = 0;
    this.warningTypeAll.typeName = '全部类型';
    this.warningTypeSelected = this.warningTypeAll;
    this.importanceSelected = this.warningImportanceList;
    this.warningList = [];
    this.total = 0;
    this.itemsPerPage = 10;
    this.warningQueryParam = new WarningQueryParam();
    this.warningQueryParam.isDefault = 1;
    this.warningQueryParam.page = 0;
    this.warningQueryParam.size = this.itemsPerPage;
    this.activeWarning = new Warning();
    this.loading = true;

    // 获取本地缓存的查询条件
    const localData = window.localStorage.getItem('warningQueryParam');
    if (localData) {
      this.warningQueryParam = JSON.parse(localData);
      if (this.warningQueryParam.importance) {
        this.warningImportanceList.forEach(importance => {
          let isContain = false;
          this.warningQueryParam.importance.forEach(item => {
            if (importance.importance === item) {
              isContain = true;
            }
          });
          importance.status = isContain;
        });
      }
    }
    this.inputSearchOrRemove = this.warningQueryParam.keyword ? false : true;

    this.getWarningTypeList(0);
    this.getWarningList();

    this.typeaheadSource = Observable.create((observer: any) => {
      // Runs on every search
      observer.next(this.warningQueryParam.keyword);
    }).mergeMap((keyword: string) => this.companyTypeaheadSearch(keyword));
  }

  ngOnDestroy() {
    if (this.sub) {
      this.sub.unsubscribe();
    }
  }

  changeWarningType(warningType: WarningType) {
    this.warningTypeSelected = warningType;
    this.warningQueryParam.typeId = [];
    if (warningType.id > 0) {
      this.warningQueryParam.typeId.push(warningType.id);
    }
    this.getWarningList();
  }

  generateWarningList(data) {
    this.warningList = [];
    this.total = data && data.total ? data.total : 0;
    if (data && data.items && Array.isArray(data.items) && data.items.length > 0) {
      for (let item of data.items) {
        let warning = new Warning();
        warning.generateByRaw(item);
        this.warningList.push(warning);
      }
    }
    window.scrollTo(0, 0);
    this.loading = false;
  }

  generateWarningTypeList(data) {
    this.warningTypeList = [];
    if (data && Array.isArray(data) && data.length > 0) {
      data.forEach(item => {
        let warningType = new WarningType();
        warningType.generateByRaw(item);
        this.warningTypeList.push(warningType);
      });
    }
    this.showWarningTypeList();
  }

  getWarningList(page: number = 1) {
    window.localStorage.setItem('warningQueryParam', JSON.stringify(this.warningQueryParam));
    this.loading = true;
    this.currentPage = page;
    if (this.sub) {
      this.sub.unsubscribe();
    }
    this.sub = this.warningService.getWarnings(this.warningQueryParam).subscribe(
      data => {
        this.generateWarningList(data);
      },
      error => console.log('getWarningList error:', error)
    );
  }

  getWarningTypeList(importance: number) {
    this.warningService.getWarningTypes(importance).subscribe(
      data => this.generateWarningTypeList(data),
      error => console.log('getWarningTypeList error:', error)
    );
  }

  goToCompany(companyId) {
    this.router.navigate(['company/detail', companyId]);
  }

  goToBond(bondId) {
    this.router.navigate(['product/bond/detail/', bondId]);
  }

  selectImportance(item) {
    this.importanceSelected = item;
    this.warningQueryParam.importance = [];
    this.warningImportanceList.map(importanceItem => {
      if (importanceItem.importance === item.importance) {
        importanceItem.status = !item.status;
      }
      if (importanceItem.status) {
        this.warningQueryParam.importance.push(importanceItem.importance);
      }
    });
    this.getWarningList();
  }

  showWarningDetail(warning: Warning) {
    if (this.warningDetailState === 'in' && warning.id === this.activeWarning.id) {
      this.warningDetailState = 'out';
      return;
    }
    this.warningDetailState = 'out';
    setTimeout(() => {
      this.warningDetailState = 'in';
      this.activeWarning = warning;
    }, 300);
    if (warning.warningContent) {
      return;
    }
    this.warningService.getWarningDetail(warning.id, warning.typeId).subscribe(
      data => {
        warning.warningContent = data.content;
      },
      error => console.log('error:', error)
    );
  }

  showWarningTypeList() {
    if (this.warningImportanceList.every(item => !item.status) || this.warningImportanceList.every(item => item.status)) {
      this.filterWarningTypeList = this.warningTypeList.filter(item => item.id > 0);
    } else {
      this.filterWarningTypeList = this.warningTypeList.filter(item =>
        this.warningImportanceList.some(
          warningImportance => warningImportance.status && item.importance && warningImportance.importance === item.importance
        )
      );
    }
    this.filterWarningTypeList.unshift(this.warningTypeAll);
    this.filterWarningTypeList.forEach(item => {
      if (this.warningQueryParam.typeId && this.warningQueryParam.typeId.length > 0) {
        if (this.warningQueryParam.typeId[0] === item.id) {
          this.warningTypeSelected = item;
        }
      }
    });
  }

  pageChanged($event) {
    this.warningQueryParam.page = $event.page - 1;
    this.warningQueryParam.size = this.itemsPerPage;
    this.getWarningList($event.page);
    console.log('pageChanged:', $event);
  }

  /**
   * 获取搜索联想词
   */
  companyTypeaheadSearch(keyword: string): Observable<any> {
    if (keyword.trim().length < 2) {
      return Observable.create((observer: any) => {
        observer.next([{}]);
      });
    }
    return this.warningService.getSimilarWords(keyword.trim());
  }

  /**
   * 搜索/清空预警信息
   */
  searchWarningInfo() {
    window.scrollTo(0, 0);
    if (this.inputSearchOrRemove && this.warningQueryParam.keyword) {
      this.inputSearchOrRemove = false;
    } else if (!this.inputSearchOrRemove) {
      this.inputSearchOrRemove = true;
      this.warningQueryParam.keyword = '';
    }
    this.getWarningList();
  }

  /**
   * 回车搜索预警信息
   */
  @HostListener('document:keypress', ['$event'])
  searchKeyPress($event) {
    if (parseInt($event.keyCode, 10) === 13 && this.isInputFocus) {
      this.searchWarningInfo();
    }
  }

  highLight(item: string) {
    if (this.warningQueryParam.keyword && this.warningQueryParam.keyword.trim().length > 0) {
      const startIdx = item.indexOf(this.warningQueryParam.keyword);
      const len = this.warningQueryParam.keyword.trim().length;
      if (startIdx >= 0) {
        const normalStartWord = item.substring(0, startIdx);
        const normalEndWord = item.substring(startIdx + len);
        const highWord = item.substring(startIdx, startIdx + len);
        item = normalStartWord + '<strong class="match">' + highWord + '</strong>' + normalEndWord;
      }
    }
    return item;
  }
}



