import { Component, OnInit, ViewChild, ElementRef, TemplateRef, trigger, state, style, transition, animate } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';

import { BondRatingRecord, Ultility, Bond, BondPledge, BondWarrantor, BondParty, Task, Rating, InnerRating } from '../../shared/';
import { BondService } from '../../product/bond/bond.service';
import { HomeService } from '../../root/home/home.service';
import { TaskService } from '../task.service';

@Component({
  selector: 'app-bond-rating-task',
  templateUrl: './bond-rating-task.component.html',
  styleUrls: ['./bond-rating-task.component.css'],
  animations: [
    trigger('ratingHistoryState', [
      state('out', style({ opacity: 0, height: 0 })),
      state('in', style({ opacity: 1, height: '*' })),
      transition('out => in', [
        animate('100ms ease-in')
      ]),
      transition('in => out', [
        animate('100ms ease-out')
      ])
    ]),
    trigger('taskCommentState', [
      state('out', style({ height: 0 })),
      state('in', style({ height: '*' })),
      transition('out => in', [
        animate('200ms ease-in')
      ]),
      transition('in => out', [
        animate('200ms ease-out')
      ])
    ])
  ]
})
export class BondRatingTaskComponent implements OnInit {
  loading: boolean;
  bondRatingRecordSid: number;
  scrollUrl: string;
  bondRatingRecord: BondRatingRecord;
  bondDetail: Bond;
  pledgeList: BondPledge[];
  warrantorList: BondWarrantor[];
  issuerList: BondParty[];
  issuerTableColumns = [];
  ratingProcedureTableColumns = [];
  ratingProcedureTasks: Task[];
  innerRatingList;
  outerRatingList;
  taskCommentState: string;
  activeTaskComment: string;
  initStatus = [
    { 'type': 'ratingResult', 'status': false },
    { 'type': 'bondDetail', 'status': false },
    { 'type': 'issuer', 'status': false },
    { 'type': 'pledge', 'status': false },
    { 'type': 'warrantor', 'status': false },
    { 'type': 'workflow', 'status': false }
  ];
  ratingHistoryState = 'out';
  step;

  @ViewChild('scrollWrapper') scrollWrapper: ElementRef;
  @ViewChild('ratingTemplate') ratingTemplate: TemplateRef<any>;
  @ViewChild('ratingDateTemplate') ratingDateTemplate: TemplateRef<any>;
  @ViewChild('exposureTemplate') exposureTemplate: TemplateRef<any>;
  @ViewChild('timeFmt') timeFmt: TemplateRef<any>;
  @ViewChild('taskCommentTemplate') taskCommentTemplate: TemplateRef<any>;

  constructor(private router: Router, private activeRoute: ActivatedRoute, private bondService: BondService,
    private homeService: HomeService, private taskService: TaskService
    , private toastr: ToastsManager) {
  }

  ngOnInit() {
    this.loading = true;
    this.scrollUrl = '#card_result';
    this.pledgeList = [];
    this.warrantorList = [];
    this.ratingProcedureTasks = [];
    this.innerRatingList = [];
    this.outerRatingList = [];
    this.taskCommentState = 'out';

    this.activeRoute.params.forEach(
      (params: Params) => {
        console.log('params:', params);
        this.bondRatingRecordSid = params['bondRatingRecordSid'];
        if (this.bondRatingRecordSid) {
          this.getBondRatingRecord();
          this.getWorkflow();
        }
        console.log('this.bondRatingRecordSid:', this.bondRatingRecordSid);
      }
    );
    this.initTableColumns();
  }

  scrollTo(elementRef: string) {
    this.scrollUrl = elementRef;
    this.homeService.scrollInside(elementRef, this.scrollWrapper);
  }

  private initTableColumns() {
    this.issuerTableColumns = [
      { name: '名称', prop: 'partyName', resizeable: false, sortable: false, draggable: false },
      { name: '内评', cellTemplate: this.ratingTemplate, resizeable: false, sortable: false, draggable: false },
      { name: '评级日期', cellTemplate: this.ratingDateTemplate, resizeable: false, sortable: false, draggable: false },
      { name: '企业性质', prop: 'company.orgFormName', resizeable: false, sortable: false, draggable: false },
      { name: '敞口', cellTemplate: this.exposureTemplate, resizeable: false, sortable: false, draggable: false }
    ];
    this.ratingProcedureTableColumns = [
      { name: '日期', prop: 'updateTime', cellTemplate: this.timeFmt, resizeable: false, sortable: false, draggable: false },
      { name: '任务内容', prop: 'taskType', resizeable: false, sortable: false, draggable: false },
      { name: '处理人', prop: 'assignee', resizeable: false, sortable: false, draggable: false },
      { name: '处理结果', prop: 'taskResult', resizeable: false, sortable: false, draggable: false },
      { name: '处理意见', prop: 'comment', cellTemplate: this.taskCommentTemplate, resizeable: false, sortable: false, draggable: false },
    ];
  }

  getInnerRatingList() {
    this.bondService.getInnerRatingList(this.bondRatingRecord.secinnerId).subscribe(
      data => this.generateInnerRatingList(data),
      error => console.log('getInnerRatingList error:', error)
    );
  }

  generateInnerRatingList(data) {
    this.innerRatingList = [];
    if (!data || data.length < 1) {
      return;
    }
    for (let item of data) {
      let innerRating = new Rating();
      innerRating.isCollapse = true;
      innerRating.generateByRaw(item);
      this.innerRatingList.push(innerRating);
    }
  }

  getOuterRatingList() {
    this.bondService.getOuterRatingList(this.bondRatingRecord.secinnerId).subscribe(
      data => this.generateOuterRatingList(data),
      error => console.log('getOuterRatingList error:', error)
    );
  }

  generateOuterRatingList(data) {
    this.outerRatingList = [];
    if (!data || data.length < 1) {
      return;
    }
    for (let item of data) {
      let outerRating = new Rating();
      outerRating.generateByRaw(item);
      this.outerRatingList.push(outerRating);
    }
  }

  private getBondRatingRecord() {
    let ids = [this.bondRatingRecordSid];
    this.bondService.getBondRatingRecords(ids).subscribe(
      res => {
        if (Ultility.arrayIsNotEmpty(res)) {
          this.bondRatingRecord = res[0];
          this.bondRatingRecord.adjustRating = this.bondRatingRecord.adjustRating ?
            this.bondRatingRecord.adjustRating : this.bondRatingRecord.rawRating;
          this.bondRatingRecord.adjustLGDGrade = this.bondRatingRecord.adjustLGDGrade ?
            this.bondRatingRecord.adjustLGDGrade : this.bondRatingRecord.rawLGDGrade;
          this.bondRatingRecord.adjustLGDScore = this.bondRatingRecord.adjustLGDScore ?
            this.bondRatingRecord.adjustLGDScore : this.bondRatingRecord.rawLGDScore;
          this.getBondDetail();
          this.getPledgeList();
          this.getWarrantorList();
          this.getIssuerList();
          this.getInnerRatingList();
          this.getOuterRatingList();
        }
        this.initFinish('ratingResult');
      },
      error => {
        console.log('getBondRatingRecord error:', error);
      }
    );
  }

  get adjustRatingLevel() {
    if (this.bondRatingRecord && this.bondRatingRecord.adjustRating) {
      if (this.bondRatingRecord.adjustRating.substring(0, 1) === 'A') {
        return 1;
      } else if (this.bondRatingRecord.adjustRating.substring(0, 2) === 'BB') {
        return 2;
      }
    }
    return 3;
  }

  private getBondDetail() {
    this.bondService.getBondDetail(this.bondRatingRecord.secinnerId).subscribe(
      res => {
        this.bondDetail = res;
        this.initFinish('bondDetail');
      },
      error => {
        console.log('getBondDetail error:', error);
      }
    );
  }

  private getPledgeList() {
    this.bondService.getBondPledgeDetail(this.bondRatingRecord.secinnerId).subscribe(
      res => {
        console.log('getPledgeList res：', res);
        if (Ultility.arrayIsNotEmpty(res)) {
          this.pledgeList = res;
        }
        this.initFinish('pledge');
      },
      error => {
        console.log('getPledgeList error:', error);
      }
    );
  }

  private getWarrantorList() {
    this.bondService.getBondWarrantorDetail(this.bondRatingRecord.secinnerId).subscribe(
      res => {
        console.log('getWarrantorList res:', res);
        if (Ultility.arrayIsNotEmpty(res)) {
          this.warrantorList = res;
        }
        this.initFinish('warrantor');
      },
      error => {
        console.log('getWarrantorList error:', error);
      }
    );
  }

  private getIssuerList() {
    this.bondService.getIssuer(this.bondRatingRecord.secinnerId).subscribe(
      res => {
        if (Ultility.arrayIsNotEmpty(res)) {
          this.issuerList = res;
        }
        this.initFinish('issuer');
      },
      error => {
        console.log('getIssuerList error:', error);
      }
    );
  }

  private getWorkflow() {
    this.taskService.getWorkflowByBondRatingRecordId(this.bondRatingRecordSid).subscribe(
      res => {
        if (Ultility.arrayIsNotEmpty(res) && Ultility.arrayIsNotEmpty(res[0].tasks)) {
          this.ratingProcedureTasks = res[0].tasks;
          for (let i = 0; i < this.ratingProcedureTasks.length; i++) {
            if (this.ratingProcedureTasks[i].taskType === '认定') { // 将“认定”改为“分析”
              this.ratingProcedureTasks[i].taskType = '分析';
            }
          }
        }
        this.initFinish('workflow');
      },
      error => {
        console.log('getWorkflow error:', error);
      }
    );
  }

  private initFinish(type: string) {
    this.initStatus.map(item => item.type === type && (item.status = true));
    if (this.initStatus.every(item => item.status === true)) {
      this.loading = false;
    }
  }

  showTaskComment(comment: string) {
    this.taskCommentState = 'in';
    this.activeTaskComment = comment;
  }

  downloadBondRatingReport(rating: any) {
    let ratingRecordId;
    if (!rating) {
      ratingRecordId = this.bondRatingRecordSid;
    } else {
      ratingRecordId = rating.bondRatingRecordSid;
      if (!rating.ratingType.includes('认定')) {
        return;
      }
    }
    this.loading = true;
    this.bondService.downloadBondRatingReport(ratingRecordId).subscribe(
      res => {
        this.loading = false;
        const fileName = this.bondDetail.securityShortName + '债券评级报告.xls';
        if (window.navigator.msSaveBlob) {
          window.navigator.msSaveBlob(res.body, fileName);
        } else {
          let downloadUrl = window.URL.createObjectURL(res.body);
          let downloadLink = document.createElement('a');
          downloadLink.href = downloadUrl;
          downloadLink.download = fileName;
          document.body.appendChild(downloadLink);
          downloadLink.click();
          document.body.removeChild(downloadLink);
        }
      },
      error => {
        this.loading = false;
        this.toastr.error('债券评级报告下载失败，请重试！');
        console.log('downloadBondRatingReport error:', error);
      }
    );
  }
}



