import { Component, ViewChild, EventEmitter, Input, Output, OnInit, TemplateRef, ElementRef } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { Observable } from 'rxjs/Observable';
import { ValidStatus } from './valid-status';

import { CompanyService } from '../../company/company.service';
import { BondService } from '../../product/bond/bond.service';
@Component({
  selector: 'app-tag-input',
  templateUrl: 'tag-input.component.html',
  styleUrls: ['tag-input.component.css']
})
export class TagInputComponent implements OnInit {
  @Input()
  items: any[];
  @Input()
  placeholder: string;
  @Input()
  max: number;
  @Input()
  separatorKeys: number[];
  @Input()
  removeDisable: boolean;
  @Output()
  errorEvent: EventEmitter<any> = new EventEmitter();
  @Output()
  itemsChange: EventEmitter<any> = new EventEmitter();
  @Output()
  removeItem: EventEmitter<any> = new EventEmitter();

  @Input()
  set chosenType(chosenType: string) {
    this.currentType = chosenType;
  }
  @Output()
  reValid: EventEmitter<any> = new EventEmitter();

  height = 50;
  params: string;
  textareaHeight: string;

  isInput: boolean;
  companySearchSource: Observable<any>;
  bondShortNameSearchSource: Observable<any>;
  bondCodeSearchSource: Observable<any>;
  bondSearchSource: Observable<any>;
  isSearchCompany: boolean;
  itemString: string;
  isSwitchTag: boolean;
  currentType: string;
  constructor(private elementRef: ElementRef, private companyService: CompanyService, private bondService: BondService) {
  }


  ngOnInit() {
    this.textareaHeight = this.height + 'px';
    this.removeDisable = false;
    this.isInput = false;
    this.isSwitchTag = false;

    this.bondShortNameSearchSource = Observable.create((observer: any) => {
      observer.next(observer.destination.outerValue);
    }).mergeMap((keyword: string) => this.bondShortNamesTypeaheadSearch(keyword));

    this.bondSearchSource = Observable.create((observer: any) => {
      observer.next(observer.destination.outerValue);
    }).mergeMap((keyword: string) => this.bondTypeaheadSearch(keyword));

    this.bondCodeSearchSource = Observable.create((observer: any) => {
      observer.next(observer.destination.outerValue);
    }).mergeMap((keyword: string) => this.bondCodeTypeaheadSearch(keyword));

    this.companySearchSource = Observable.create((observer: any) => {
      observer.next(observer.destination.outerValue);
    }).mergeMap((keyword: string) => this.companyTypeaheadSearch(keyword));

    this.isSearchCompany = false;

  }
  blur($event) {
    if (this.params) {
      this.change(this.params, true);
    }
  }

  change($event: string, addSingleItem = false) {
    if ( $event === '' || $event === '\n') {
      return;
    }
    let params = $event.replace(/[\n]+/g, '￥@!');
    params = params.trim();
    let paramList = params.split('￥@!');
    if (paramList.length === 1 && !addSingleItem) {
      return;
    }
    paramList = paramList.filter(item => item != null && item !== '');
    let items = [];
    for (let param of paramList) {
      if (this.items.some(item => item.text === param)) {
        continue;
      }
      let newItem = {
        text: param,
        status: ValidStatus.UNCHECK,
        isSwitchTag: false
      };
      items.push(newItem);
    }

    if (items.length > 0) {
      this.items.push(...items);
      this.itemsChange.emit(true);
    }
    this.params = null;
    this.elementRef.nativeElement.querySelector('#item-textarea').value = null;
  }

  remove(item) {
    this.removeItem.emit(item);
  }
  private companyTypeaheadSearch(keyword: string): Observable<any> {
    if (this.isSearchCompany) {
      this.isSearchCompany = false;
      return Observable.create((observer: any) => {
        observer.next([]);
      });
    }
    return this.companyService.getCompanyListForTypeaHead(keyword.trim());
  }

  private bondShortNamesTypeaheadSearch(keyword: string): Observable<any> {
    if (this.isSearchCompany) {
      this.isSearchCompany = false;
      return Observable.create((observer: any) => {
        observer.next([]);
      });
    }
    return this.bondService.getBondShortNameListForTypeaHead(keyword.trim());
  }

  private bondTypeaheadSearch(keyword: string): Observable<any> {
    if (this.isSearchCompany) {
      this.isSearchCompany = false;
      return Observable.create((observer: any) => {
        observer.next([]);
      });
    }
    return this.bondService.getBondNameListForTypeaHead(keyword.trim());
  }

  private bondCodeTypeaheadSearch(keyword: string): Observable<any> {
    if (this.isSearchCompany) {
      this.isSearchCompany = false;
      return Observable.create((observer: any) => {
        observer.next([]);
      });
    }
    return this.bondService.getBondCodeListForTypeaHead(keyword.trim());
  }


  changeValue(item) {
    for (let i of this.items) {
      i.status = 0;
    }
    this.reValid.emit();
  }

  isChangeTag(item) {
    item.isSwitchTag = true;
  }
  choseCompanies(item) {
    item.isSwitchTag = false;
  }

}




