import { Component, OnInit, Input, Output, EventEmitter, NgZone } from '@angular/core';
import { Subject } from 'rxjs/Subject';
import { Observable } from 'rxjs/Observable';
import { Observer } from 'rxjs/Observer';
import { Router, ActivatedRoute } from '@angular/router';

import { FinanceService } from '../finance.service';
import { FinanceSubjctItem, FinanceSet } from '../entities/finance';
import { BondService } from '../../product/bond/bond.service';
import { Bond } from '../../shared/index';
import { CompanyService } from '../company.service';
import { MsgService } from 'app/core/msg.service';

@Component({
  selector: 'app-bond-edit',
  styleUrls: ['bond-edit.component.css'],
  templateUrl: 'bond-edit.component.html'
})

export class BondEditComponent implements OnInit {
  @Input()
  companyId: number;
  @Input()
  companyName: string;
  @Input()
  addStatus: boolean; // true为录入，false为编辑
  @Output()
  loadingEvent: EventEmitter<any> = new EventEmitter();
  @Output()
  finish: EventEmitter<any> = new EventEmitter();

  bondList: Bond[];

  constructor(private router: Router, private bondService: BondService, private companyService: CompanyService, private msg: MsgService) {
  }

  ngOnInit() {
    this.bondList = [];
    this.bondService.bondAddFinishStatus = null;
    this.getCompanyIssueBondList();
  }

  addBond() {
    this.bondService.companyName = this.companyName;
    this.bondService.companyId = this.companyId;
    this.bondService.companyIsAdd = this.addStatus;
    this.router.navigate(['product/bond/new']);
  }

  deleteBond(bond: Bond) {
    this.msg.confirm('确定删除吗?').result
      .then(result => {
        this.loadingEvent.next(true);
        this.bondService.delBond(bond.secinnerId).subscribe(
          data => {
            this.loadingEvent.next(false);
            this.bondList = this.bondList.filter(item => item.secinnerId !== bond.secinnerId);
          },
          error => {
            this.loadingEvent.next(false);
            this.msg.alert('企业基本信息删除失败，请重试！');
          }
        );
      });
  }

  editBond(bond: Bond) {
    this.bondService.companyId = this.companyId;
    this.bondService.companyName = this.companyName;
    this.bondService.companyIsAdd = this.addStatus;
    this.router.navigate(['product/bond', bond.secinnerId]);
  }

  getCompanyIssueBondList() {
    this.companyService.getCompanyIssueBonds(this.companyId).subscribe(
      data => this.generateBondList(data),
      error => console.log('getCompanyIssueBondList error:', error)
    );
  }

  generateBondList(data) {
    if (!data || !data.items || (Array.isArray(data.items) && data.items.length < 1)) return;
    for (let item of data.items) {
      let bond = new Bond();
      bond.generateByRaw(item);
      this.bondList.push(bond);
    }
  }
}



