import { Injectable } from '@angular/core';
import { PageScrollService, PageScrollInstance, PageScrollConfig } from 'ng2-page-scroll';
import { HttpClient } from '@angular/common/http';
@Injectable()
export class HomeService {
  public document: any;
  public mainwrapper: any;
  constructor(private pageScrollService: PageScrollService, private http: HttpClient) {
    PageScrollConfig.defaultScrollOffset = 180;
    PageScrollConfig.defaultDuration = 300;
    PageScrollConfig.defaultEasingLogic = {
      ease: (t: number, b: number, c: number, d: number): number => {
        // easeInOutExpo easing
        if (t === 0) return b;
        if (t === d) return b + c;
        if ((t /= d / 2) < 1) return c / 2 * Math.pow(2, 10 * (t - 1)) + b;
        return c / 2 * (-Math.pow(2, -10 * --t) + 2) + b;
      }
    };
  }

  public scrollInside(element: any, wrapper: any) {
    let pageScrollInstance: PageScrollInstance = PageScrollInstance.simpleInstance(this.document, element);
    this.pageScrollService.start(pageScrollInstance);
  }

  downloadHelpDocument() {
    return this.http.get(`/attachments/downLoad/helpDocument`, {observe: 'response', responseType: 'blob'});
  }
}



