import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ExposureFactorsItems } from 'app/models/entities';
@Injectable()
export class FactorService {
  private quanPrefix = '/financeFactors/';
  private qualPrefix = '/operationFactors/';
  private areaPrefix = '/regionFactors/';
  private specialPrefix = '/factorsAdjustment/';
  constructor(private http: HttpClient) { }

  createQuan(factor) {
    return this.http.post(`${this.quanPrefix}factor`, factor);
  }

  updateQuan(factor) {
    return this.http.put(`${this.quanPrefix}factor/${factor.factorCode}`, factor);
  }

  deleteQuan(factor) {
    return this.http.delete(`${this.quanPrefix}factor/${factor.factorCode}`);
  }

  getQuanFactors(type: string = null) {
    return this.http.get<ExposureFactorsItems[]>(`${this.quanPrefix}factors`, {params: {'factorType': type}});
  }

  QuanExists(code) {
    return this.http.get(`${this.quanPrefix}factor/existence/${code}`);
  }


  getQuanTypes() {
    return this.http.get(`${this.quanPrefix}factorTypes`);
  }

  changeQuanType(oldType, newType) {
    return this.http.put(`${this.quanPrefix}factorType/${oldType}/to/${newType}`, null);
  }


  createQual(factor) {
    return this.http.post(`${this.qualPrefix}factor`, factor);
  }

  updateQual(factor) {
    return this.http.put(`${this.qualPrefix}factor/${factor.factorCode}`, factor);
  }

  deleteQual(factor) {
    return this.http.delete(`${this.qualPrefix}factor/${factor.factorCode}`);
  }

  getQualFactors(type: string = null) {
    return this.http.get<ExposureFactorsItems[]>(`${this.qualPrefix}factors`, {params: {'factorType': type}});
  }

  QualExists(code) {
    return this.http.get(`${this.qualPrefix}factor/existence/${code}`);
  }

  getQualTypes() {
    return this.http.get(`${this.qualPrefix}factorTypes`);
  }

  changeQualType(oldType, newType) {
    return this.http.put(`${this.qualPrefix}factorType/${oldType}/to/${newType}`, null);
  }

  createArea(factor) {
    return this.http.post(`${this.areaPrefix}factor`, factor);
  }

  updateArea(factor) {
    return this.http.put(`${this.areaPrefix}factor/${factor.factorCode}`, factor);
  }

  deleteArea(factor) {
    return this.http.delete(`${this.areaPrefix}factor/${factor.factorCode}`);
  }

  getAreaFactors(type: string = null) {
    return this.http.get<ExposureFactorsItems[]>(`${this.areaPrefix}factors`, {params: {'factorType': type}});
  }

  AreaExists(code) {
    return this.http.get(`${this.areaPrefix}factor/existence/${code}`);
  }

  getAreaTypes() {
    return this.http.get(`${this.areaPrefix}factorTypes`);
  }

  getSpecialTypes() {
    return this.http.get(`${this.specialPrefix}factorTypes`);
  }

  getSpecialFactors(type: string = null) {
    return this.http.get(`${this.specialPrefix}factors`, {params: {'factorType': type}});
  }

  checkSpecialExist(type: string, name: string) {
    return this.http.get(encodeURI(`${this.specialPrefix}${type}/${name}`));
  }

  createSpecial(factor) {
    return this.http.post(`${this.specialPrefix}`, factor);
  }

  updateSpecial(factor) {
    return this.http.put(`${this.specialPrefix}`, factor);
  }

  deleteSpecial (factor) {
    return this.http.delete(`${this.specialPrefix}${factor.adjustCode}/`);
  }

  changeSpecialType (oldType, newType) {
    return this.http.put(`${this.specialPrefix}factorType/${oldType}/to/${newType}`, null);
  }

  // 根据评级记录ID查询特例调整指标信息
  getFactorAdjustmentByRating(ratingID) {
    return this.http.get<any>(`${this.specialPrefix}rating/${ratingID}/factorAdjustment`);
  }

  // 根据敞口查询特例调整指标信息
  getFactorAdjustmentByExposure(exposureSid) {
    return this.http.get<any>(`${this.specialPrefix}exposure/${exposureSid}/factorAdjustment`);
  }

  // 新增指标与敞口映射关联关系
  createRelationShipFactorAndExposure(relationShip) {
    return this.http.post(`${this.specialPrefix}exposure/factorAdjustment/`, relationShip);
  }

  // 修改指标与敞口映射关联关系
  updateRelationShipFactorAndExposure(relationShip) {
    return this.http.put(`${this.specialPrefix}exposure/factorAdjustment/`, relationShip);
  }

  // 删除指标与敞口映射关联关系
  deleteRelationShipFactorAndExposure(id) {
    return this.http.delete(`${this.specialPrefix}exposure/factorAdjustment/${id}/`);
  }

  // 新增评级与特例调整指标映射关系
  createRelationShipFactorAndRating(relationShipList) {
    return this.http.post(`${this.specialPrefix}rating/factorAdjustment/`, relationShipList);
  }

  // 删除评级与特例调整指标映射关系
  deleteRelationShipFactorAndRating(deletedList) {
    return this.http.put(`${this.specialPrefix}rating/factorAdjustment/`, deletedList);
  }

  changeAreaType(oldType, newType) {
    return this.http.put(`${this.areaPrefix}factorType/${oldType}/to/${newType}`, null);
  }

  getAccountSubjectTypes() {
    return this.http.get<any>(`/accountingSubject/subjectTypes`);
  }

  getAccountSubjects() {
    return this.http.get<any>(`/accountingSubject/subjects`);
  }

  getTemplateTypes() {
    return this.http.get(`/lookup/companyTypePairs`);
  }

  getSubjects(reportDat: string, companyId, factorCode) {
    return this.http.get<string[]>(`/financeFactors/subjects`, {params:
      {'factorCode': factorCode, 'companyId': companyId, 'reportDate': reportDat}});
  }

}



