import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from '../core/auth/auth-guard.service';
import { TaskListComponent } from './task-list/task-list.component';
import { CompanyRatingTaskComponent } from './company-rating-task/company-rating-task.component';
import { WarningTaskComponent } from './warning-task/warning-task.component';
import { BondRatingTaskComponent } from './bond-rating-task/bond-rating-task.component';


export const taskRoutes: Routes = [
  { path: 'task', component: TaskListComponent, canActivateChild: [AuthGuard] },
  { path: 'task/company/rating/:step/:workflowSid', component: CompanyRatingTaskComponent, canActivateChild: [AuthGuard] },
  { path: 'task/warning/:workflowSid/:taskStatus', component: WarningTaskComponent, canActivateChild: [AuthGuard] },
  { path: 'task/bond/rating/:bondRatingRecordSid', component: BondRatingTaskComponent, canActivateChild: [AuthGuard] }
];



