import { Component, OnInit, ViewContainerRef } from '@angular/core';

import { ToastsManager } from 'ng2-toastr/ng2-toastr';

import { CompanyService } from '../../company/company.service';
import { ModelsService } from '../models.service';
import { MsgService } from 'app/core/msg.service';

@Component({
  selector: 'app-warning-finance',
  templateUrl: './warning-finance.component.html',
  styleUrls: ['./warning-finance.component.css']
})
export class WarningFinanceComponent implements OnInit {

  currentModel;
  warningModels;
  exposures;
  calTypes = ['绝对值', '同比', '环比'];
  calSigns = ['>', '<', '>=', '<='];
  severities = ['高', '中', '低'];
  factors;
  selectedFactors;

  constructor(private companyService: CompanyService, private modelsService: ModelsService, private toastr: ToastsManager,
    private msg: MsgService) {
  }

  ngOnInit() {
    this.getExposures();
    this.getModels();
  }

  getExposures() {
    this.exposures = this.companyService.getExposures().subscribe(data => {
      this.exposures = data;
    });
  }
  onExposureChange() {
    this.getFactors(this.currentModel.exposureSid);
  }
  addModel() {
    this.currentModel = {
      isActive: false,
      exposureSid: this.exposures ? this.exposures[0].id : undefined,
      calType: this.calTypes[0],
      calSign: this.calSigns[0],
      severity: this.severities[0]
    };
    this.selectedFactors = null;
    this.getFactors(this.currentModel.exposureSid);
  }

  editModel(model) {
    this.currentModel = JSON.parse(JSON.stringify(model));
    this.getFactors(model.exposureSid);
  }

  getModels() {
    this.modelsService.getWarningsFinance().subscribe(data => {
      this.warningModels = data;
      this.warningModels.forEach(element => {
        if (element.warningRegulationDetailVOList && element.warningRegulationDetailVOList.length > 0) {
          element.factorCode = element.warningRegulationDetailVOList[0].factorCode;
        }
      });
      if (this.currentModel) {
        this.editModel(this.warningModels.find(it => it.name === this.currentModel.name));
      }
    });
  }

  getFactors(exposureId) {
    this.modelsService.getFactorsFin([exposureId]).subscribe(data => {
      this.factors = data;
      this.factors.forEach(this.transferItem);
      if (this.currentModel.warningRegulationDetailVOList && this.currentModel.warningRegulationDetailVOList.length > 0) {
        this.selectedFactors = this.factors.filter(it => it.id === this.currentModel.warningRegulationDetailVOList[0].factorCode);
        this.currentModel.calType = this.currentModel.warningRegulationDetailVOList[0].calType;
        this.currentModel.calSign = this.currentModel.warningRegulationDetailVOList[0].symbol;
        this.currentModel.threshold = this.currentModel.warningRegulationDetailVOList[0].value;
        this.currentModel.isRate = this.currentModel.warningActiongVO && this.currentModel.warningActiongVO.actionCode === 'TRIGGER_RATING';
      }
    });
  }

  private transferItem(it) {
    if (it == null) { return { id: '', text: '' }; }
    it.id = it.factorCode;
    it.text = `<span style="color:#49b6d6">类别：</span><span style="display:inline-block; width:8rem">${it.factorType}</span>
          <span style="color:#49b6d6">名称：</span><span style="display:inline-block; width:16rem">${it.factorName}</span>
          <span style="color:#49b6d6">代码：</span>${it.factorCode}`;
    if (it.formulaCh) { it.text += `<div style="white-space: normal;"><span style="color:#49b6d6">描述公式：</span>${it.formulaCh}</div><hr>`; }
    if (it.description) {
      it.text += `<div style="white-space: normal;"><span style="color:#49b6d6">描述公式：</span>${it.description}</div><hr>`;
    }
    return it;
  }

  factorSelected(item) {
    this.currentModel.factorCode = item.id;
  }

  private valid(m): boolean {
    if (!m.name) {
      this.toastr.error('名称不能为空');
      return false;
    }
    if (!m.factorCode) {
      this.toastr.error('请选择财务指标');
      return false;
    }
    if (m.threshold == null || m.threshold.length === 0) {
      this.toastr.error('阈值不能为空');
      return false;
    }
    return true;
  }

  save() {
    if (!this.valid(this.currentModel)) {
      return;
    }
    this.currentModel.isActive = this.currentModel.isActive ? 1 : 0;
    this.currentModel.warningRegulationDetailVOList = [];
    const warningRegulationDetailVO = {
      'factorCode': this.currentModel.factorCode,
      'calType': this.currentModel.calType,
      'symbol': this.currentModel.calSign,
      'value': this.currentModel.threshold
    };
    this.currentModel.warningRegulationDetailVOList.push(warningRegulationDetailVO);
    this.currentModel.warningActiongVO = {};
    this.currentModel.warningActiongVO.actionCode = this.currentModel.isRate ? 'TRIGGER_RATING' : '';

    this.modelsService.updateWarningModel(this.currentModel).subscribe(data => {
      if (!this.currentModel.id) {
        this.currentModel = data;
      }
      this.getModels();
      this.toastr.success('保存成功');
    }, error => {
      this.toastr.error('保存失败');
    });
  }

  delete(model) {
    this.msg.confirm('请确认是否删除该预警规则？').result
      .then(result => {
        this.modelsService.deleteWarningModel(this.currentModel.id).subscribe(data => {
          this.getModels();
          this.currentModel = undefined;
          this.toastr.success('删除成功');
        });
      });
  }
}



