import { Routes } from '@angular/router';

import { ProductComponent } from './product.component';
import { BondComponent } from './bond/bond.component';
import { BondNewComponent } from './bond/bond-new.component';
import { AuthGuard } from '../core/auth/auth-guard.service';
import {BondDetailComponent } from './bond/bond-detail/bond-detail.component';
import {BondRatingComponent } from './bond/bond-rating/bond-rating.component';

export const productRoutes: Routes = [
  {
    path: 'product',
    // canActivate: [AuthGuard],
    component: ProductComponent,
    children: [
      { path: 'bond', component: BondComponent },
      { path: 'bond/new', component: BondNewComponent },

      // { path: 'archive', component: RatingArchiveComponent }
      { path: 'bond/detail/:bondId', component: BondDetailComponent },
      { path: 'bond/rating/:bondId', component: BondRatingComponent },

      { path: 'bond/:id', component: BondNewComponent }

    ]
  },
];



