import { ModuleWithProviders } from '@angular/core';
import { Routes } from '@angular/router';

import { AdminComponent } from './admin.component';
import { AdminUserComponent } from './user/admin-user.component';
import { AdminInfoComponent } from './user-info/user-info.component';
import { UserSystemComponent } from './user-system/user-system.component';
import { ExposureOwnerComponent } from './exposure-owner/exposure-owner.component';
import { AuthGuard } from '../core/auth/auth-guard.service';
export const adminRoutes: Routes = [
  {
    path: 'admin',
    // canActivate: [AuthGuard],
    component: AdminComponent
  },
  {
    path: 'admin/user',
    component: AdminUserComponent
  },
  {
    path: 'admin/user/info',
    component: AdminInfoComponent
  },
  {
    path: 'admin/system',
    component: UserSystemComponent
  },
  {
    path: 'admin/system/exposure',
    component: ExposureOwnerComponent
  }
];



