import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Subject } from 'rxjs/Subject';
import 'rxjs/add/operator/catch';

@Injectable()
export class AnalysisService {
  private GET_ANALYSIS_FACTORS_COMPANY = '/analysis/factor/companies';
  private GET_ANALYSIS_FACTORS_EXPOSURE = '/analysis/factor/exposures';
  private GET_ANALYSIS_FACTORSDATA = '/analysis/factor/companies/data';
  public AnalysisResultAnnouncement = new Subject();

  constructor(private http: HttpClient) {
  }

  getAnalysisFactors(idList: any[]) {
    return this.http.post<any>(this.GET_ANALYSIS_FACTORS_COMPANY, idList);
  }
  getAnalysisFactorsData(company_ids: any[], factors: any[], portfolio_id?: number) {
    let data = { 'companyIds': company_ids, 'factors': factors, 'portfolioId': portfolio_id };
    return this.http.post(this.GET_ANALYSIS_FACTORSDATA, data);
  }
}



