import { Directive, Input } from '@angular/core';
import {
  AbstractControl,
  ValidatorFn,
  NG_ASYNC_VALIDATORS,
  AsyncValidator
} from '@angular/forms';
import { CheckUserField, AdminService } from 'app/admin/admin.service';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';

@Directive({
  selector: '[appCheckUser]',
  providers: [
    {
      provide: NG_ASYNC_VALIDATORS,
      useExisting: CheckUserDirective,
      multi: true
    }
  ]
})
export class CheckUserDirective implements AsyncValidator {
  @Input() appCheckUser: string;
  @Input() field: CheckUserField;

  constructor(private admin: AdminService) {}

  validate(
    c: AbstractControl
  ): Promise<{ [key: string]: any }> | Observable<{ [key: string]: any }> {
    return Observable.timer(500).switchMap(() =>
      this.admin
        .checkUser(this.appCheckUser, this.field, c.value)
        .map(it => (!!it ? { checkuser: it } : null))
    );
  }
}



