import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../core/auth/auth-guard.service';

import { analysisRoutes } from './company-analysis/company-analysis.routing';

import { CompanySearchComponent } from './company-search/company-search.component';
import { CompanyDetailComponent } from './company-detail/company-detail.component';
import { CompanyEditComponent } from './company-edit/company-edit.component';
import { CompanyAnalysisComponent } from './company-analysis/company-analysis.component';

export const companyRoutes: Routes = [
  { path: 'company', component: CompanySearchComponent, canActivateChild: [AuthGuard] },
  { path: 'company/detail/:companyId', component: CompanyDetailComponent, canActivateChild: [AuthGuard] },
  { path: 'company/edit/:companyId', component: CompanyEditComponent, canActivateChild: [AuthGuard] },
  { path: 'company/add', component: CompanyEditComponent, canActivateChild: [AuthGuard] },
  { path: 'company/analysis', component: CompanyAnalysisComponent, children: [...analysisRoutes] }
];



