import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../core/auth/auth-guard.service';
import { AuthService } from '../core/auth/auth.service';

import { HomeComponent } from './home/home.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { NotFoundComponent } from './notfound/notfound.component';

import { OverviewComponent } from '../overview/overview.component';
import { overviewRoutes } from '../overview/overview.routing';
import { analysisRoutes } from '../analysis/analysis.routing';
import { companyRoutes } from '../company/company.routing';
import { productRoutes } from '../product/product.routing';
import { portfolioRoutes } from '../portfolio/portfolio.routing';
import { modelsRoutes } from '../models/models.routing';
import { adminRoutes } from '../admin/admin.routing';
import { taskRoutes } from '../task/task.routing';
import { MasterScaleResolver } from '../root/master-scale-resolver';
import { logRoutes } from '../log/log.routing';


const rootRoutes: Routes = [
  {
    path: '',
    component: HomeComponent,
    canActivate: [AuthGuard],
    children: [
      // {
      //      path: '',
      //      canActivateChild: [AuthGuard],
      //      component: OverviewComponent
      // },
      ...overviewRoutes,
      ...analysisRoutes,
      ...portfolioRoutes,
      ...modelsRoutes,
      ...companyRoutes,
      ...productRoutes,
      ...adminRoutes,
      ...taskRoutes,
      ...logRoutes
    ],
    resolve: { scale: MasterScaleResolver }
  }

];
export const appRoutingProviders: any[] = [
  AuthGuard,
  AuthService
  //   CanDeactivateGuard
];
export const rootRouting: ModuleWithProviders = RouterModule.forChild(rootRoutes);



