  ngOnDestroy() {
    if (this.searchSub) this.searchSub.unsubscribe();
  }

  private highlight(item: string): string {
    let keyword = this.searchParams.keyword;
    if (!keyword || !item) return item;
    keyword = keyword.trim();
    if (keyword.length > 0) {
      let startIdx = item.indexOf(keyword);
      let len = keyword.length;
      if (startIdx >= 0) {
        item =
          item.substring(0, startIdx) +
          '<mark>' +
          item.substring(startIdx, startIdx + len) +
          '</mark>' +
          item.substring(startIdx + len);
      }
    }
    return item;
  }

  private clearFollow() {
    this.bondFollowList.map(data => (data.followStatus = false));
    this.bondFollowList = [];
    this.pagedBond.items.map(data => (data.followStatus = false));
    window.localStorage.removeItem('bondFollowList');
  }

  private disfollow(item: Bond) {
    item.followStatus = false;
    this.bondFollowList = this.bondFollowList.filter(
      data => data.secinnerId !== item.secinnerId
    );
    this.pagedBond.items.map(data => {
      if (data.secinnerId === item.secinnerId) {
        data.followStatus = false;
      }
    });
    window.localStorage.setItem(
      'bondFollowList',
      JSON.stringify(this.bondFollowList)
    );
  }

  private follow(item: Bond) {
    if (item.followStatus || this.bondFollowList.length >= 20) {
      return;
    }
    item.followStatus = true;
    this.bondFollowList.push(item);
    window.localStorage.setItem(
      'bondFollowList',
      JSON.stringify(this.bondFollowList)
    );
  }

  pageChanged(event) {
    this.searchParams.page = event.page - 1;
    this.searchParams.size = event.itemsPerPage;
    this.search(true);
  }

  searchAndSetStatus() {
    if (this.inputSearchOrRemove && this.searchParams.keyword) {
      this.inputSearchOrRemove = false;
    } else if (!this.inputSearchOrRemove) {
      this.inputSearchOrRemove = true;
      this.searchParams.keyword = '';
    }
    this.search();
  }

  search(isPageChange = false) {
    if (!isPageChange) {
      this.currentPage = 1;
    }
    this.loadingStatus = true;
    this.bondSearchParams.page = this.searchParams.page;
    this.bondSearchParams.size = this.itemsPerPage;
    this.bondSearchParams.keyword = this.searchParams.keyword
      ? this.searchParams.keyword.trim()
      : null;
    if (this.searchSub) {
      this.searchSub.unsubscribe();
    }

    window.localStorage.setItem(
      'bondSearchConditionShowList',
      JSON.stringify(this.bondSearchConditionShowList)
    );
    this.searchSub = this.bondService.search(this.bondSearchParams).subscribe(
      data => {
        this.pagedBond = data;
        let bondinfos: Bond[] = [];
        for (let item of this.pagedBond.items) {
          item.followStatus = this.bondFollowList.some(
            it => it.secinnerId === item.secinnerId
          );
        }
      },
      error => {
        console.log('Error: ' + error);
      },
      () => (this.loadingStatus = false)
    );
  }

  isNumeric(n): boolean {
    return !isNaN(parseFloat(n)) && isFinite(n);
  }
  selectCouponRate() {
    this.errorMsg = null;
    if (this.rateA) {
      if (Number(this.rateA) < 0) this.errorMsg = '请不要输入负数';
      if (!this.isNumeric(this.rateA)) this.errorMsg = '请输入数字';
      if (this.rateB && this.isNumeric(this.rateB)) {
        if (+this.rateA > +this.rateB) this.errorMsg = '请合理输入';
      }
    } else {
      this.rateA = '';
    }
    if (this.rateB) {
      if (Number(this.rateB) < 0) this.errorMsg = '请不要输入负数';
      if (!this.isNumeric(this.rateB)) this.errorMsg = '请输入数字';
    } else {
      this.rateB = '';
    }
    if (this.errorMsg) return;
    this.bondSearchParams.couponRate = [this.rateA + ',' + this.rateB];
    let bondSearchConditionShow = new CompanySearchConditionShow();
    bondSearchConditionShow.label = '票面利率';
    bondSearchConditionShow.items = this.bondSearchParams.couponRate;
    this.bondSearchConditionShowList.push(bondSearchConditionShow);
    this.couponRateMultSel = false;
    this.couponRateIsSelect = true;
    this.search();
  }
  selectPeriod(id) {
    this.searchParams.period = id;
    this.search();
  }
  goToDetail(secinner_id: number) {
    this.router.navigate(['product/bond/detail', secinner_id]);
  }

  removeSearchConditon(label: string) {
    this.bondSearchConditionShowList = this.bondSearchConditionShowList.filter(
      item => item.label !== label
    );
    switch (label) {
      case '内部评级':
        this.bondSearchParams.rating = [];
        this.searchParams.rating = null;
        break;
      case '评级类型':
        this.bondSearchParams.ratingType = [];
        this.ratingTypeIsSelect = false;
        this.searchParams.ratingType = null;
        break;
      case '外部评级':
        this.bondSearchParams.creditRating = [];
        this.searchParams.creditRating = null;
        break;
      case '所属敞口':
        this.bondSearchParams.bussion = [];
        this.searchParams.bussion = null;
        break;
      case '债券类型':
        this.bondSearchParams.bondType = [];
        this.searchParams.bondType = null;
        break;
      case '发行方式':
        this.bondSearchParams.issueType = null;
        this.issueTypeIsSelect = false;
        this.issueTypeList.map(item => (item.status = false));
        this.searchParams.issueType = null;
        break;
      case '发行规模':
        this.bondSearchParams.issueVolum = [];
        this.issueVolumIsSelect = false;
        this.issueVolumList.map(item => (item.status = false));
        this.searchParams.issueVolum = null;
        break;
      case '交易市场':
        this.bondSearchParams.tradeMarket = null;
        this.marketNamesIsSelect = false;
        this.marketNamesList.map(item => (item.status = false));
        this.searchParams.tradeMarket = null;
        break;
      case '票面利率':
        this.bondSearchParams.couponRate = null;
        this.rateA = '';
        this.rateB = '';
        this.couponRateIsSelect = false;
        this.searchParams.couponRate = null;
        break;
      case '剩余期限':
        this.bondSearchParams.period = null;
        this.periodIsSelect = false;
        this.periodList.map(item => (item.status = false));
        this.searchParams.period = null;
        break;
    }
    this.addDesc();
    if (this.bondSearchConditionShowList.length < 1) {
      this.regulationCollapseStatus = true;
    }
    this.search();
  }

  selRating(items) {
    this.bondSearchParams.rating = items.map(it => it.text);
    if (items.length > 0) {
      let bondSearchConditionShow = new CompanySearchConditionShow();
      bondSearchConditionShow.label = '内部评级';
      bondSearchConditionShow.items = items.map(it => it.text);
      this.bondSearchConditionShowList.push(bondSearchConditionShow);
    }
    this.search();
  }

