import { Component, OnInit, ViewContainerRef } from '@angular/core';

import { ToastsManager } from 'ng2-toastr/ng2-toastr';

import { CompanyService } from '../../company/company.service';
import { ModelsService } from '../models.service';
import { ExposureService } from '../exposure.service';
import { RatingModel, Exposures, ExposureFactorsItems, OptionVosItems } from '../entities';
import { FactorService } from '../factor.service';
import { UserService } from '../../user/user.service';
import { MsgService } from 'app/core/msg.service';

@Component({
  selector: 'app-exposure-config',
  templateUrl: './exposure-config.component.html',
  styleUrls: ['./exposure-config.component.css']
})
export class ExposureConfigComponent implements OnInit {

  exposures;
  currentExposure: string;
  currentExposureId: number;
  isCollapsed = true;
  allFactors: ExposureFactorsItems[];
  factorMeta;
  exposureDetail: Exposures;
  editInfoList: ExposureFactorsItems;
  edit: boolean;
  searchParmas: Exposures;
  innerObj: ExposureFactorsItems;
  newExposure = false;
  exposureName = '';
  private code: number;
  hasEditPrivilege = false;
  listOrSpecial = true;
  factor: any;
  factors: any;
  factorTypes: any;
  allSpecialFactors;
  selectedSpecialFactor: string;
  specialFactorMeta;
  factorAdjustmentExposureId = -1;

  constructor(private modelsService: ModelsService, private exposureService: ExposureService,
    private companyService: CompanyService, private toastr: ToastsManager, private userService: UserService,
    private msg: MsgService, private factorService: FactorService) {
    this.hasEditPrivilege = userService.hasPrivilege(userService.modelEdit);
  }

  ngOnInit() {
    this.getExposures();
    this.editInfoList = new ExposureFactorsItems();
    this.edit = false;
    this.editInfoList.options = [];
    this.searchParmas = new Exposures;
    this.innerObj = new ExposureFactorsItems();
    this.getSpecialFactorTypes();
    this.code = 1;
  }

  private getExposures() {
    this.companyService.getExposures().subscribe(
      data => {
        this.exposures = data;
      },
      error => console.log('getExposures error:', error)
    );
  }

  getDetail(item) {
    this.isCollapsed = true;
    this.currentExposure = item.exposure;
    this.currentExposureId = item.id;
    this.listOrSpecial = true;
    this.exposureService.getExposureDetail(item.id).subscribe(
      data => this.generateExposureDetail(data),
      error => console.log('error', error)
    );
  }
  generateExposureDetail(data) {
    this.exposureDetail = new Exposures();
    if (data) {
      let i = 0, len = 0, j = 0, sublen = 0;
      if (data.defaultFactorDisps && data.financeFactors) {
        for (i = 0, len = data.defaultFactorDisps.length; i < len; i++) {
          for (j = 0, sublen = data.financeFactors.length; j < sublen; j++) {
            if (data.defaultFactorDisps[i].factorCode === data.financeFactors[j].factorCode) {
              data.financeFactors[j].isDefault = true;
              break;
            }
          }
        }
      }
      if (!data.defaultFactorDisps || data.defaultFactorDisps.length === 0) {
        this.toastr.info('请选择至少一个默认指标');
      }

      this.exposureDetail.generateByRaw(data);
    }
  }
  // TODO:check & refactor
  selectFactor(type1, type2) {
    if (+type1 === 0) {
      this.factorService.getQuanFactors().subscribe(
        data => {
          this.allFactors = data;
          this.allFactors.forEach(this.transferItem);
          this.factorMeta = this.allFactors.find(it => it.factorCode === this.editInfoList.factorCode);
          return this.allFactors;
        },
        error => console.log('error', error)
      );
    } else if (+type1 === 1) {
      this.factorService.getQualFactors().subscribe(
        data => {

          this.allFactors = data;
          this.allFactors.forEach(this.transferItem);
          this.factorMeta = this.allFactors.find(it => it.factorCode === this.editInfoList.factorCode);
          this.editInfoList.options = [];
          return this.allFactors;
        },
        error => console.log('error', error)
      );
    } else if (+type1 === 2) {

      this.factorService.getAreaFactors().subscribe(
        data => {

          this.allFactors = data;
          this.allFactors.forEach(this.transferItem);
          this.factorMeta = this.allFactors.find(it => it.factorCode === this.editInfoList.factorCode);
          this.editInfoList.options = [];
          return this.allFactors;
        },
        error => console.log('error', error)
      );
    }

  }
  editing(item) {
    item.exposureCopy = item.exposure;
    item.editing = true;
    this.factorMeta = null;
    this.allFactors = null;
  }
  save(item) {
    if (item.exposure === item.exposureCopy) {
      item.editing = false;
      return;
    }
    if (!item.exposure) {
      this.toastr.error('请输入敞口名');
      return;
    }
    if (this.exposures.find(it => (it.exposureCopy || it.exposure) === item.exposure) != null) {
      this.toastr.error('该名字的敞口已经存在');
      return;
    }
    item.editing = false;
    this.getDetail(item);
    delete item.exposureCopy;
    this.exposureService.saveExposure(item).subscribe(null, error => this.toastr.error(error));
  }
  saveNew(name) {
    if (!name) {
      this.toastr.error('请输入敞口名');
      return;
    }
    if (this.exposures.find(it => it.exposure === name) != null) {
      this.toastr.error('该名字的敞口已经存在');
      return;
    }
    this.newExposure = false;
    let item = { exposure: name, code: name };
    this.exposureService.createExposure(item).subscribe(it => {
      this.exposures.push({ id: it, exposure: name, code: name });
      this.newExposure = false;
      this.exposureName = '';
    }, error => {
      this.toastr.error(error);
      this.newExposure = true;
    });
  }
  delete(item) {
    this.msg.confirm('确定要删除？').result
      .then(result => {
        this.exposureService.deleteExposure(item).subscribe(it => {
          this.exposures.splice(this.exposures.indexOf(item), 1);
          this.exposureDetail = null;
        }, error => this.toastr.error(error));
      });
  }
  cancel(item) {
    item.exposure = item.exposureCopy;
    item.editing = false;
    delete item.exposureCopy;
  }

  addFactor(id) {
    this.isCollapsed = false;
    this.edit = false;
    this.editInfoList = new ExposureFactorsItems();
    this.factorMeta = null;
    this.allFactors = null;
    this.allSpecialFactors = null;
    this.specialFactorMeta = null;
    this.selectedSpecialFactor = null;
    this.factorAdjustmentExposureId = -1;
}

  editFactor(id, item) {
    this.factorMeta = null;
    this.allFactors = null;
    this.edit = true;
    if (item) {
      this.isCollapsed = false;
      this.editInfoList = new ExposureFactorsItems();
      this.editInfoList = item;
      if (+item.categoryCode === 0 && +item.applicationCode === 0) {
        this.editInfoList.categoryCode = 0;
        this.factorService.getQuanFactors().subscribe(
          data => {
            this.allFactors = data;
            this.allFactors.forEach(this.transferItem);
            this.factorMeta = this.allFactors.find(it => it.factorCode === this.editInfoList.factorCode);
          },
          error => console.log('error', error)
        );
      } else if (+item.categoryCode === 1 && +item.applicationCode === 0) {
        this.editInfoList.categoryCode = 1;
        this.factorService.getQualFactors().subscribe(
          data => {
            this.allFactors = data;
            this.allFactors.forEach(this.transferItem);
            this.factorMeta = this.allFactors.find(it => it.factorCode === this.editInfoList.factorCode);
          },
          error => console.log('error', error)
        );
        this.editInfoList.elementVos = item.elementVos;
      } else if (+item.categoryCode === 1 && +item.applicationCode === 1) {
        this.editInfoList.categoryCode = 2;
        this.factorService.getAreaFactors().subscribe(
          data => {
            this.allFactors = data;
            this.allFactors.forEach(this.transferItem);
            this.factorMeta = this.allFactors.find(it => it.factorCode === this.editInfoList.factorCode);
          },
          error => console.log('error', error)
        );
      }

      if (this.editInfoList.options != null) {
        this.exposureDetail.format = 'discrete';
      } else {
        this.exposureDetail.format = 'continuous';
      }
    }
  }
  // 删除敞口下指标列表某一指标
  removeFactor(id, item) {
    this.msg.confirm('确定删除吗?').result
      .then(result => {
        this.exposureService.delExposureDetail(id, item.factorCode).subscribe(
          _ => {
            this.toastr.success('删除成功');
            this.exposureService.getExposureDetail(id).subscribe(
              data => this.generateExposureDetail(data),
              error => console.log('error', error)
            );
          },
          error => console.log('error', error)
        );
      });
  }
  private transferItem(it) {
    if (it == null) { return { id: '', text: '' }; }
    it.id = it.factorCode;
    it.text = `<div><div style="color:#49b6d6;float:left">类别：</div>
          <div style="display:inline-block; width:8rem;float:left">${it.factorType}</div>
          <div style="color:#49b6d6;float:left">名称：</div>
          <div style="display:inline-block; width:16rem;text-overflow: ellipsis;white-space: nowrap;overflow: hidden;float:left"
          title="${it.factorName}">${it.factorName}</div>
          <div style="color:#49b6d6;float:left">代码：</div>
          <div style="float:left">${it.factorCode}</div><div style="clear:both;"></div></div>`;
    if (it.formulaCh) { it.text += `<div style="white-space: normal;"><span style="color:#49b6d6">描述公式：</span>${it.formulaCh}</div><hr>`; }
    if (it.description) {
      it.text += `<div style="white-space: normal;"><span style="color:#49b6d6">描述公式：</span>${it.description}</div><hr>`;
    }
    return it;
  }
  factorSelected(item) {
    this.factorMeta = this.allFactors.find(it => it.factorCode === item.id);
    this.editInfoList.elementVos = this.factorMeta.relatedElements;
    return this.editInfoList.elementVos;
  }

  // 增加档位
  addOption(id) {
    let optionTempt = new OptionVosItems();
    optionTempt.exposureSid = id;
    optionTempt.formulaEn = null;
    if (this.editInfoList.options == null) {
      this.editInfoList.options = [];
    }
    this.editInfoList.options.push(optionTempt);
    for (let i = 0; i < this.editInfoList.options.length; i++) {
      this.editInfoList.options[i].optionNumber = i + 1;
    }
  }
  // 删除档位
  removeOption(item) {
    this.editInfoList.options.splice(this.editInfoList.options.indexOf(item), 1);
    for (let i = 0; i < this.editInfoList.options.length; i++) {
      this.editInfoList.options[i].optionNumber = i + 1;
    }
  }
  valid(i) {
    if (i.categoryCode == null || !this.factorMeta) {
      this.toastr.error('*为必填项');
      return false;
    }
    if (this.exposureDetail.format === 'discrete' || +i.categoryCode === 1 || +i.categoryCode === 2) {
      if (!i.options || i.options.length < 2) {
        this.toastr.error('至少需要2个档位');
        return false;
      } else if (i.options.length >= 2) {
        for (let j = 0; j < i.options.length; j++) {
          if (i.options[j].formulaCh == null || i.options[j].formulaCh.length === 0) {
            this.toastr.error('档位内选项为必填项');
            return false;
          }
        }
      }
    }
    return true;
  }
  // 保存配置
  saveConfig(innerItem, item) {
    if (!this.valid(innerItem)) { return; }
    this.searchParmas = new Exposures();
    this.searchParmas.operationFactors = [];
    this.searchParmas.financeFactors = [];
    this.searchParmas.regionFactors = [];
    this.innerObj.factorCode = this.factorMeta.factorCode;
    this.innerObj.options = innerItem.options;
    this.searchParmas.exposureId = item.exposureId;
    if (+innerItem.categoryCode === 0) {
      this.searchParmas.financeFactors.push(this.innerObj);
    } else if (+innerItem.categoryCode === 1) {
      this.searchParmas.operationFactors.push(this.innerObj);
    } else {
      this.searchParmas.regionFactors.push(this.innerObj);
    }
    if (this.edit === true) {
      this.exposureService.editExposureDetail(item.exposureId, this.searchParmas).subscribe(
        res => {
          this.toastr.success('编辑成功');
          this.isCollapsed = true;
          this.edit = false;
          this.exposureService.getExposureDetail(item.exposureId).subscribe(
            data => this.generateExposureDetail(data),
            error => console.log('error', error)
          );
        },
        error => this.toastr.error(error, '编辑失败')
      );

    } else {
      this.exposureService.addExposureDetail(item.exposureId, this.searchParmas).subscribe(
        _ => {
          this.toastr.success('保存成功');
          this.isCollapsed = true;
          this.exposureService.getExposureDetail(item.exposureId).subscribe(
            data => this.generateExposureDetail(data),
            error => console.log('error', error)
          );
        },
        error => this.toastr.error(error, '保存失败')
      );
    }
  }
  cancelAdd() { this.isCollapsed = true; this.editInfoList = new ExposureFactorsItems(); }
  chageFormat(item) {
    if (item === 'continuous') {
      this.editInfoList.options = null;
    } else {
      this.editInfoList.options = [];
    }
  }

  /**
   * 设置/取消默认
   * @param exposure 敞口长清单指标列表
   * @param item 选中的指标
   */
  defaultSettings(exposure, item) {
    // 默认设置不能大于6，大于6返回
    if ((!item.isDefault && exposure.defaultFactorDisps.length >= 6)) {
      this.toastr.info('默认指标不多于6个，不少于1个。');
      return;
    }

    // 默认设置不能小于1，小于1只给出提示
    if (item.isDefault && exposure.defaultFactorDisps.length <= 1) {
      this.toastr.info('默认指标不多于6个，不少于1个。');
    }

    // 是否默认
    if (item.isDefault) {
      this.exposureService.cancelDefaultFactor(exposure.exposureId, item.factorCode).subscribe(
        data => console.log('cancalDefaultData', data),
        error => console.log('cancelDefaultData', error)
      );
    } else {
      this.exposureService.setDefaultFactor(exposure.exposureId, item.factorCode).subscribe(
        data => console.log('setDefaultData', data),
        error => console.log('setDefaultError', error)
      );
    }
    this.exposureService.getExposureDetail(exposure.exposureId).subscribe(
      subdata => this.generateExposureDetail(subdata),
      error => console.log('error', error)
    );
  }

  // 获取当前敞口的长清单指标
  getListFactors() {
    this.listOrSpecial = true;
    this.isCollapsed = true;
    this.exposureService.getExposureDetail(this.currentExposureId).subscribe(
      data => this.generateExposureDetail(data),
      error => console.log('error', error)
    );
  }

  // 获取当前敞口的特例调整指标
  getSpecialFactors() {
    this.listOrSpecial = false;
    this.isCollapsed = true;
    this.factorService.getFactorAdjustmentByExposure(this.currentExposureId).subscribe(
      data => {
        this.factors = data;
      },
      error => console.log('error', error)
    );
  }

  getSpecialFactorTypes() {
    this.factorService.getSpecialTypes().subscribe(
      data => {
        this.factorTypes = data;
      },
      error => console.log('error', error)
    );
  }

  selectSpecialFactor(item) {
    this.factorService.getSpecialFactors(item).subscribe(
      data => {
        this.allSpecialFactors = data;
        this.allSpecialFactors.forEach(this.transferSpecialItem);
      },
      error => console.log('error', error)
    );
  }

  private transferSpecialItem(it) {
    if (it == null) { return { id: '', text: '' }; }
    it.id = it.adjustCode;
    it.text = `<div><div style="color:#49b6d6;float:left">指标名称：</div>
          <div style="display:inline-block;width:10rem;text-overflow:ellipsis;white-space:nowrap;overflow:hidden;float:left">
          ${it.adjustName}</div>
          <div style="color:#49b6d6;float:left">指标分类：</div>
          <div style="display:inline-block;width:8rem;text-overflow:ellipsis;white-space:nowrap;overflow:hidden;float:left">
            ${it.adjustType}</div>
          <div style="color:#49b6d6;float:left">调整方向：</div>
          <div style="display:inline-block; width:6rem;float:left">${it.direction}</div>
          <div style="color:#49b6d6;float:left">调整强度：</div>
          <div style="float:left">${it.importance}</div>
          <div style="clear:both;"></div></div>`;
    if (it.description) {
      it.text += `<div style="white-space: normal;"><span style="color:#49b6d6">描述公式：</span>${it.description}</div><hr>`;
    }
    return it;
  }

  specialFactorSelected(item) {
    this.specialFactorMeta = this.allSpecialFactors.find(it => it.adjustCode === item.id);
  }

  saveSpecialFactor() {
    if (this.specialFactorMeta) {
      let relationShip = {
        id: -1,
        exposureSid: this.currentExposureId,
        adjustCode: this.specialFactorMeta.adjustCode
      };
      if (this.factorAdjustmentExposureId === -1) {
        this.factorService.createRelationShipFactorAndExposure(relationShip).subscribe(
          data => {
            this.toastr.success('添加成功');
            this.getSpecialFactors();
          },
          error => this.toastr.error('添加失败')
        );
      } else {
        relationShip.id = this.factorAdjustmentExposureId;
        this.factorService.updateRelationShipFactorAndExposure(relationShip).subscribe(
          data => {
            this.toastr.success('修改成功');
            this.getSpecialFactors();
          },
          error => this.toastr.error('修改失败')
        );
      }
    }
  }

  removeSpecialFactor(item) {
    this.msg.confirm('确定要删除？').result
    .then(result => {
      this.factorService.deleteRelationShipFactorAndExposure(item.factorAdjustmentExposureId).subscribe(
        data => {
          this.toastr.success('删除成功');
          this.getSpecialFactors();
        },
        error => this.toastr.error('删除失败')
      );
    });
  }

  editSpecialFactor(item) {
    this.selectedSpecialFactor = item.adjustType;
    this.specialFactorMeta = item;
    this.factorAdjustmentExposureId = item.factorAdjustmentExposureId;
    this.isCollapsed = false;
    this.edit = true;
    this.selectSpecialFactor(item.adjustType);
  }
}



