import { PagedResult } from '../paged-result';
import { Warning } from './warning';
export class PagedWarning extends PagedResult<Warning> {
  generateByRaw(raw: any) {
    Object.assign(this, raw);
    this.items = [];
    for (let item of raw.items) {
      let warning = new Warning();
      warning.generateByRaw(item);
      this.items.push(warning);
    }
  }
}



