import { Directive, Input, ElementRef, OnInit } from '@angular/core';

@Directive({
  selector: '[appAutosizeTextarea]'
})
export class AutosizeTextareaDirective implements OnInit {
  @Input() minHeight: number;
  @Input() maxHeight: number;
  @Input()
  private set appAutosizeTextarea(value: string) {
    this.textareaChange(this.ele.nativeElement);
  }

  constructor(private ele: ElementRef) {}

  ngOnInit() {}

  private textareaChange(el) {
    el.style.height = this.minHeight + 'px';
    let scrollHeight = el.scrollHeight;
    if (scrollHeight < this.minHeight) {
      el.style.height = this.minHeight + 'px';
    } else {
      if (this.maxHeight && scrollHeight > this.maxHeight) {
        el.style.height = this.maxHeight + 'px';
      } else {
        el.style.height = scrollHeight + 2 + 'px';
      }
    }
  }
}



