import { Component, OnInit } from '@angular/core';
import { AdminService } from '../../admin/admin.service';
import { CompanyService } from '../../company/company.service';
import { Observable } from 'rxjs/Observable';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';

@Component({
  selector: 'app-exposure-owner',
  templateUrl: './exposure-owner.component.html',
  styleUrls: ['./exposure-owner.component.css']
})
export class ExposureOwnerComponent implements OnInit {

  ratingUsers;
  warningUsers;
  exposureOwners;
  constructor(private adminService: AdminService, private companyService: CompanyService,
    private toastr: ToastsManager
  ) { }

  ngOnInit() {
    this.adminService.getHasPrivilegeUsers('企业', '评级分析').subscribe(data => this.ratingUsers = data);
    this.adminService.getHasPrivilegeUsers('预警', '预警管理').subscribe(data => this.warningUsers = data);

    this.getOwners();
  }

  getOwners() {
    Observable.forkJoin(
      this.adminService.getExposureRatingOwners(),
      this.adminService.getExposureWarningOwners(),
      this.companyService.getExposures()
    ).subscribe(data => {
      const r = data[0];
      const w = data[1];
      this.exposureOwners = data[2].map(e => {
        const re = r && r.find(it => it.exposureSid === e.id);
        const we = w && w.find(it => it.exposureSid === e.id);
        return {
          id: e.id,
          name: e.exposure,
          rating: re && re.userName,
          warning: we && we.userName
        };
      });
    });
  }

  save() {
    const ro = this.exposureOwners.map(it => ({ exposureSid: it.id, userName: it.rating }));
    const wo = this.exposureOwners.map(it => ({ exposureSid: it.id, userName: it.warning }));
    Observable.forkJoin(
      this.adminService.updateExposureRatingOwners(ro),
      this.adminService.updateExposureWarningOwners(wo)
    ).subscribe(data => {
      this.toastr.success('保存成功');
    }, error => {
      this.toastr.error(error, '保存失败');
    });
  }

  cancel() {
    this.getOwners();
  }

}



