import { Injectable, Optional } from '@angular/core';
import {
  Http, RequestMethod, Response,
  RequestOptionsArgs, Headers,
  RequestOptions, Request
} from '@angular/http';
import { Router } from '@angular/router';

import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';

import { AccessToken, API_V1 } from './feedAPI';
import { UserService } from '../../user/user.service';


export class FeedServiceConfig {
  endpoint = 'http://';
}
/* deprecated */
@Injectable()
export class FeedService {
  public loading = new Subject();

  private endpoint = '';

  private headers: Headers;

  private getCurrentUserApi = '/user/currentUser';

  constructor(private http: Http, private router: Router, private toastr: ToastsManager,
    private userService: UserService, @Optional() config: FeedServiceConfig) {

    if (config) { this.endpoint = config.endpoint; }
    userService.CurrentUser = this.getCurrentUser();
  }

  private getUser() {
    const options = new RequestOptions({ headers: this.getHeader(), method: RequestMethod.Get });
    return this.http.get(this.getEndpoint() + this.getCurrentUserApi, options)
      .map(res => res.text() ? res.json() : null)
      .catch(error => this.handleError(error));
  }

  public getAccess(username: string, password: string, kaptcha: string): Observable<AccessToken> {
    this.headers = null;
    const headers = new Headers();
    headers.append('Authorization', API_V1._AUTHORIZATION_HEAD);
    headers.append('Content-Type', 'application/x-www-form-urlencoded');
    let options = new RequestOptions({ headers: headers, method: RequestMethod.Post, withCredentials: true});
    window.localStorage.setItem('username', username);
    const body = 'kaptcha=' + kaptcha + '&client_id=' + API_V1._CLIENT_ID + '&grant_type=' +
      API_V1._GRANT_TYPE + '&username=' + username + '&password=' + password;
    return this.http.post(this.endpoint + API_V1.ACCESS_TOKEN_POST, body, options)
      .map(res => this.extractData(res)).switchMap(at => {
        return this.getUser().map(data => {
          window.localStorage.setItem('userInfo', JSON.stringify(data));
          this.userService.CurrentUser = data;
          return at;
        });
      })
      .catch(error => this.handleError(error));
  }

  public getEndpoint() {
    return this.endpoint;
  }

  public getHeader() {
    if (!this.headers) {
      let token = window.localStorage.getItem('login_cm_token');
      if (token) {
        token = JSON.parse(token);
        this.generateHeader(token);
      }
    }
    return this.headers;
  }

  // 目前cookie中拿不到username
  // public getUsername() {
  //   return Cookie.get('username');
  // }

  public getUsername() {
    let userInfo = window.localStorage.getItem('userInfo');
    if (userInfo) {
      let user = JSON.parse(userInfo);
      return user ? user.userName : null;
    }
    return null;
  }

  public getUserClientId() {
    let userInfo = window.localStorage.getItem('userInfo');
    if (userInfo) {
      let user = JSON.parse(userInfo);
      return user ? user.clientId : null;
    }
    return null;
  }

  public getCurrentUser() {
    const userInfo = window.localStorage.getItem('userInfo');
    if (userInfo) {
      const user = JSON.parse(userInfo);
      return user;
    }
    return null;
  }

  public getToken() {
    const token = window.localStorage.getItem('login_cm_token');
    let object: any;
    if (token) {
      object = JSON.parse(token);
    }
    return token ? object : null;
  }

  private getErrorMsg(error) {
    if (error._body) {
      const o = JSON.parse(error._body);
      return o.errors && o.errors.field || o.message || o;
    }
    return error.status ? `${error.status} - ${error.statusText}` : 'Server error';
  }

  public handleError(error: any) {
    // In a real world app, we might use a remote logging infrastructure
    // We'd also dig deeper into the error to get a better message
    console.log('error:', error);
    if (error.status === 403) {
      this.logout();
    } else if (error.status === 406) {
      // this.toastr.error('用户权限不足，请联系管理员', '权限不足');
    }
    let errMsg: string;
    if (error.status !== 0) {
      console.log('status:', error.status);
      errMsg = this.getErrorMsg(error);
    } else {
      errMsg = '访问异常,请重试';
    }
    console.log('errMsg:', errMsg); // log to console instead
    return Observable.throw(errMsg);
  }

  public logout() {
    this.logoutService().subscribe(
      data => null,
      error => console.log('Logout', error)
    );

    window.localStorage.removeItem('login_cm_token');
    window.localStorage.removeItem('username');
    window.localStorage.removeItem('userInfo');
    window.localStorage.clear();

    this.headers = null;
    this.router.navigate(['login']);
  }

  private logoutService(): Observable<any> {
    const options = new RequestOptions({ headers: this.getHeader(), method: RequestMethod.Get });
    return this.http.get(this.getEndpoint() + API_V1.OAUTH_LOGOUT, options)
      .map(res => res.text() ? res.json() : null);
    // comment out line below. it causes a infinit loop.
    // .catch(error => { return this.handleError(error); });
  }

  private extractData(res: Response) {
    console.log('res:', res);
    const body = res.json();
    if (body.access_token) {
      window.localStorage.setItem('login_cm_token', JSON.stringify(body));
    }
    return body || {};
  }

  private generateHeader(token: any) {
    this.headers = new Headers();
    this.headers.append('Authorization', `${token.token_type} ${token.access_token}`);
    this.headers.append('Content-Type', 'application/json');
  }


  /**
   * 方便HTTP Methods的调用
   */

  /**
   * Performs any type of http request.
   * @param url
   * @param options
   * @returns {Observable<Response>}
   */
  request(url: string | Request, options?: RequestOptionsArgs): Observable<Response> {
    return this.http.request(url, options);
  }

  /**
   * Performs a request with `get` http method.
   * @param url
   * @param options
   * @returns {Observable<>}
   */
  get(url: string, options?: RequestOptionsArgs): Observable<any> {
    this.requestInterceptor();
    return this.http.get(this.getFullUrl(url), this.requestOptions(options))
      .catch(this.onCatch.bind(this))
      .do((res: Response) => {
        this.onSubscribeSuccess(res);
      }, (error: any) => {
        this.onSubscribeError(error);
      })
      .finally(() => {
        this.onFinally();
      });
  }

  getLocal(url: string, options?: RequestOptionsArgs): Observable<any> {
    return this.http.get(url, options);
  }

  /**
   * Performs a request with `post` http method.
   * @param url
   * @param body
   * @param options
   * @returns {Observable<>}
   */
  post(url: string, body: any, options?: RequestOptionsArgs): Observable<any> {
    this.requestInterceptor();
    return this.http.post(this.getFullUrl(url), body, this.requestOptions(options))
      .catch(this.onCatch.bind(this))
      .do((res: Response) => {
        this.onSubscribeSuccess(res);
      }, (error: any) => {
        this.onSubscribeError(error);
      })
      .finally(() => {
        this.onFinally();
      });
  }

  /**
   * Performs a request with `put` http method.
   * @param url
   * @param body
   * @param options
   * @returns {Observable<>}
   */
  put(url: string, body: any, options?: RequestOptionsArgs): Observable<any> {
    this.requestInterceptor();
    return this.http.put(this.getFullUrl(url), body, this.requestOptions(options))
      .catch(this.onCatch.bind(this))
      .do((res: Response) => {
        this.onSubscribeSuccess(res);
      }, (error: any) => {
        this.onSubscribeError(error);
      })
      .finally(() => {
        this.onFinally();
      });
  }

  /**
   * Performs a request with `delete` http method.
   * @param url
   * @param options
   * @returns {Observable<>}
   */
  delete(url: string, options?: RequestOptionsArgs): Observable<any> {
    this.requestInterceptor();
    return this.http.delete(this.getFullUrl(url), this.requestOptions(options))
      .catch(this.onCatch.bind(this))
      .do((res: Response) => {
        this.onSubscribeSuccess(res);
      }, (error: any) => {
        this.onSubscribeError(error);
      })
      .finally(() => {
        this.onFinally();
      });
  }

  private requestOptions(options?: RequestOptionsArgs): RequestOptionsArgs {
    if (options == null) {
      options = new RequestOptions();
    }

    if (options.headers == null) {
      options.headers = this.getHeader();
    }
    return options;
  }

  getFullUrl(url: string): string {
    return this.endpoint + url;
  }

  private requestInterceptor(): void {
    // console.log('Sending Request');
    // this.loaderService.showPreloader();
    this.loading.next({
      loading: true, hasError: false, hasMsg: ''
    });
  }

  private responseInterceptor(): void {
    // console.log('Request Complete');
    // this.loaderService.hidePreloader();
  }

  private onCatch(error: any, caught: Observable<any>): Observable<any> {
    // console.log('Something went terrible wrong and error is', error);
    // this.loaderService.popError();
    return this.handleError(error);
  }

  private onSubscribeSuccess(res: Response): void {
    this.loading.next({
      loading: false, hasError: false, hasMsg: ''
    });
  }

  private onSubscribeError(error: any): void {
    // this.loaderService.popError();
    this.loading.next({
      loading: false, hasError: true, hasMsg: 'Something went wrong'
    });
  }

  private onFinally(): void {
    this.responseInterceptor();
  }

}



