export class Ultility {
  static copyProperties(object: any, extension: any) {
    for (let key in object) {
      if (object.hasOwnProperty(key)) {
        extension[key] = object[key];
      }
    }
  }

  static copyPropertiesFromUnderlineToCamel(object: any, extension: any) {
    for (let key in object) {
      if (object.hasOwnProperty(key)) {
        let camelCaseKey = Ultility.transformUnderlineToCamel(key);
        extension[camelCaseKey] = object[key];
      }
    }
  }

  static transformUnderlineToCamel(underlineKey: string): string {
    return underlineKey
      .toLowerCase()
      .replace(/_[a-z]/g, r => r[1].toUpperCase());
  }

  static applyMixins(derivedCtor: any, baseCtors: any[]) {
    baseCtors.forEach(baseCtor => {
      Object.getOwnPropertyNames(baseCtor.prototype).forEach(name => {
        if (name !== 'constructor') {
          derivedCtor.prototype[name] = baseCtor.prototype[name];
        }
      });
    });
  }

  static arrayIsNotEmpty(array: any): boolean {
    if (array && Array.isArray(array) && array.length > 0) {
      return true;
    } else {
      return false;
    }
  }
}



