import { Pipe, PipeTransform } from '@angular/core';
import { CompanyFactorElement } from '../index';
declare let d3: any;

@Pipe({
  name: 'companyElementPipe'
})
export class CompanyElementPipe implements PipeTransform {
  transform(element: CompanyFactorElement): string {
    if (!element || !element.elementValue) {
      return '';
    }
    let value;
    switch (element.elementUnit) {
      case '亿元':
        value = element.elementValue / 100000000;
        break;
      case '百万元':
        value = element.elementValue / 1000000;
        break;
      case '万元':
        value = element.elementValue / 10000;
        break;
      default:
        value = element.elementValue;
        break;
    }
    if (element.elementFormat === '%') {
      value = d3.format('.2%')(value);
    } else if (element.elementFormat && element.elementFormat.includes('#')) {
      let matchs = element.elementFormat.match(/#+/);
      let len = matchs ? matchs[0].length : 5;
      value = d3.format(',.' + len + 'f')(value);
    }
    if (!element.elementFormat && value && !isNaN(+value) && !Number.isInteger(value)) {
      value = d3.format(',.5f')(value);
    }
    /*switch (element.elementFormat) {
        case '%':
            value = d3.format('.2%')(value);
            break;
        case '.#####':
            value = d3.format(',.5f')(value);
            break;
        case '.##':
            value = d3.format(',.5f')(value);
            break;
        default:
            break;
    }*/
    value = element.elementUnit && element.elementUnit !== '%' ? value + element.elementUnit : value;
    return value;
  }
}



