import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { FeedService } from '../../core/feedAPI/feedAPI.service';
import { UserPassword } from '../../shared';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-admin-info',
  templateUrl: 'user-info.component.html',
  styleUrls: ['user-info.component.css']
})
export class AdminInfoComponent implements OnInit {
  isHidden: boolean;
  userPassword: UserPassword;
  error1: string;
  error2: string;

  constructor(private router: Router, private activeRoute: ActivatedRoute, private feedService: FeedService,
    private adminService: AdminService) {
  }

  ngOnInit() {
    this.isHidden = true;
    this.userPassword = new UserPassword();
    this.error1 = '';
    this.error2 = '';
  }

  editUserPassword() {
    this.error1 = null;
    this.error2 = null;
    if ((!this.userPassword.newPassword || this.userPassword.newPassword.trim() === '')
    && (!this.userPassword.confirmNewPasswordT || this.userPassword.confirmNewPasswordT.trim() === '')) {
      this.error1 = '新密码或者确认密码不能为空';
      return;
    }
    if (/^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?([^\w\s]|_)).{8,}$/.test(this.userPassword.newPassword) === false) {
      this.error1 = '新密码最少8个字符，且必须包含大小写、数字、特殊字符';
      return;
    }
    if (this.userPassword.newPassword !== this.userPassword.confirmNewPasswordT) {
      this.error1 = '两次密码不一致';
      return;
    } else {
      this.adminService.editUserPassword(this.userPassword).subscribe(
        data => {
          if (!data) {
            this.isHidden = false;
          } else {
            this.error2 = '原始密码错误';
          }
        },
        error => { this.error2 = error; }
      );

    }

  }

  logout() {
    this.feedService.logout();
    this.router.navigate(['login']);
  }


}



