import { PortfolioItem } from '../../shared';
export class PortfolioItemVo extends PortfolioItem {
    checkstatus: boolean;
    isNew: boolean;
    get facotrDateAt(){
        if (this.factorDate != null) {
          return this.factorDate + '年' + '12月31日';
        }
        return '';
    }
}



