import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { AccessToken, API_V1 } from '../core/feedAPI/feedAPI';


@Injectable()
export class LoginService {

  constructor(private http: HttpClient) {}

  public getAccess(username: string, password: string, kaptcha: string): Observable<AccessToken> {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'application/x-www-form-urlencoded',
        'Authorization': API_V1._AUTHORIZATION_HEAD
      }),
      withCredentials: true
    };

    window.localStorage.setItem('username', username);
    const body = 'kaptcha=' + kaptcha + '&client_id=' + API_V1._CLIENT_ID + '&grant_type=' +
      API_V1._GRANT_TYPE + '&username=' + username + '&password=' + password;
      console.log(body);

    return this.http.post<AccessToken>(API_V1.ACCESS_TOKEN_POST, body, httpOptions);
  }


  getUser() {
    return this.http.get<any>('/user/currentUser');
  }
}



