import { Injectable, Optional } from '@angular/core';
import { RegulationCondition } from '../shared/entities/portfolio/regulation';
import { HttpClient } from '@angular/common/http';
@Injectable()
export class RuleService {

  private ruleApiPrefix = '/regulations';
  private getRuleListApi = this.ruleApiPrefix + '/';
  constructor(private http: HttpClient) { }

  getRuleList() {
    return this.http.get(this.getRuleListApi);
  }

  getCompanyListByRule(regulationId: number) {
    return this.http.get(this.ruleApiPrefix + `/${regulationId}/items`);
  }
  // 根据筛选条件组合成规则
  addRegulationByCondition(regulationCondition: RegulationCondition) {
    return this.http.post(this.getRuleListApi, regulationCondition);
  }
  // 获取当前用户 规则列表
  getRegulationList(regulationType: number) {
    return this.http.get(this.ruleApiPrefix + `/regulationType/${regulationType}`);
  }
  // 删除规则信息
  deleteRuleRegulationList(regulationId: number) {
    return this.http.delete(this.ruleApiPrefix + `/${regulationId}`);
  }
}



