import { Component, OnInit, ViewContainerRef } from '@angular/core';

import { ToastsManager } from 'ng2-toastr/ng2-toastr';

import { CompanyService } from '../../company/company.service';
import { ModelsService } from '../models.service';

@Component({
  selector: 'app-warning-extra',
  templateUrl: './warning-extra.component.html',
  styleUrls: ['./warning-extra.component.css']
})
export class WarningExtraComponent implements OnInit {
  currentModel;
  warningModels;
  severities = ['高', '中', '低'];

  constructor(
    private companyService: CompanyService,
    private modelsService: ModelsService,
    private toastr: ToastsManager
  ) {}

  ngOnInit() {
    this.getModels();
  }

  editModel(model) {
    this.currentModel = JSON.parse(JSON.stringify(model));
  }

  getModels() {
    this.modelsService.getWarningsExtra().subscribe(data => {
      this.warningModels = data;
    });
  }

  save() {
    this.currentModel.isActive = this.currentModel.isActive ? 1 : 0;
    this.modelsService
      .updateWarningModelExtra(this.currentModel)
      .subscribe(data => {
        this.getModels();
        this.toastr.success('保存成功');
      });
  }
}



