  // 查询区域经济实力与财政实力
  getPower(code: number) {
    this.companyService
      .getRegionPower(code)
      .subscribe(
        data => this.generateRegionPower(data),
        error => console.log('getCompanyRegion:', error)
      );
  }

  generateRegionPower(data) {
    this.financialIndexList = [];
    this.economicalIndexList = [];
    if (data) {
      if (data.length > 0) {
        this.regionYears = data[0].factorValues.map(it =>
          new Date(it.reportDate).getFullYear()
        );
      }
      for (let item of data) {
        let unitRatio = 1;
        switch (item.unit) {
          case '千元':
            unitRatio = 1000;
            break;
          case '万美元':
          case '万港元':
          case '万元':
            unitRatio = 10000;
            break;
          case '百万':
            unitRatio = 1000000;
            break;
          case '亿元':
            unitRatio = 100000000;
            break;
        }
        if (item.factorValues) {
          item.factorValues.forEach(element => {
            if (element.value && element.value !== 'null') {
              if (item.format === '%') {
                element.value = element.value * 100;
                element.value = element.value.toFixed(2);
              } else {
                if (!isNaN(element.value)) {
                  element.value = element.value / unitRatio;
                  element.value = element.value.toFixed(
                    item.format ? item.format.split('#').length - 1 : 0
                  );
                }
              }
            }
          });
        }
        if (item.type === '地方政府经济实力') {
          let economiaclIndex = new QuanFactor();
          economiaclIndex.generateByRaw(item);
          this.economicalIndexList.push(economiaclIndex);
        } else {
          let financialIndex = new QuanFactor();
          financialIndex.generateByRaw(item);
          this.financialIndexList.push(financialIndex);
        }
      }
    }
  }

