  // 获取公司股东信息
  private getShareHolderList() {
    this.companyService.getShareHolderList(this.companyId).subscribe(
      data => {
        this.shareHolderList = [];
        if (Ultility.arrayIsNotEmpty(data)) {
          for (let item of data) {
            if (!this.isExistInArr(item.endDate, this.shareHolderList)) {
              let shareHolderTemp = new ShareHolder();
              shareHolderTemp.endDate = item.endDate;
              shareHolderTemp.endYear = item.endDate.toString().substring(0, 4);
              shareHolderTemp.endMonth = item.endDate
                .toString()
                .substring(4, 6);
              shareHolderTemp.endDay = item.endDate.toString().substring(6, 8);
              shareHolderTemp.items = this.getShareHolderItem(
                item.endDate,
                data
              );
              shareHolderTemp.isPlus = true;
              this.shareHolderList.push(shareHolderTemp);
            }
          }
          this.shareHolderList[0].isPlus = false;
        }
      },
      error => console.log('error', error)
    );
  }

  // 判断股东信息日期是否存在
  isExistInArr(endDate: string, data: ShareHolder[]) {
    let returnValue = false;
    for (let item of data) {
      if (item.endDate === endDate) {
        returnValue = true;
      }
    }
    return returnValue;
  }

  // 获取相同日期的股东信息
  getShareHolderItem(endDate: string, data: any[]) {
    let shareHolderItems = [];
    for (let item of data) {
      if (item.endDate === endDate) {
        let shareHolderItemTemp = new ShareHolderItem();
        shareHolderItemTemp.shareholderName = item.shareholderName;
        shareHolderItemTemp.shareholderType = item.shareholderType;
        shareHolderItemTemp.shareholderNum = item.shareholderNum;
        shareHolderItemTemp.shareholderRatio = item.shareholderRatio;
        shareHolderItemTemp.changeAmount = item.changeAmount;
        // shareHolderItemTemp.shareholderRatioChange = item.changeAmount/item.shareholderNum*item.shareholderRatio ;
        shareHolderItemTemp.shareType = item.shareType;
        shareHolderItemTemp.status = item.status;
        shareHolderItemTemp.changeRatio = item.changeRatio;
        shareHolderItems.push(shareHolderItemTemp);
      }
    }
    return shareHolderItems;
  }

