import { CommonModule } from '@angular/common';
import {
  ModuleWithProviders,
  NgModule,
  Optional,
  SkipSelf
} from '@angular/core';
import { HTTP_INTERCEPTORS, HttpClientModule } from '@angular/common/http';
import { MsgService } from 'app/core/msg.service';
import { ToastModule } from 'ng2-toastr/ng2-toastr';
import { ModalModule } from 'ngx-modialog';
import { VexModalModule } from 'ngx-modialog/plugins/vex';
import { FeedService } from './feedAPI/feedAPI.service';
import { FeedServiceConfig } from './feedAPI/feedAPI.service';
import { AuthInterceptor } from 'app/core/AuthInterceptor';

@NgModule({
  imports: [
    CommonModule,
    ToastModule.forRoot(),
    ModalModule.forRoot(),
    VexModalModule,
    HttpClientModule
  ],
  declarations: [],
  exports: [],
  providers: [
    FeedService,
    MsgService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: AuthInterceptor,
      multi: true
    }
  ]
})
export class CoreModule {
  static forRoot(config: FeedServiceConfig): ModuleWithProviders {
    return {
      ngModule: CoreModule,
      providers: [{ provide: FeedServiceConfig, useValue: config }]
    };
  }

  constructor(
    @Optional()
    @SkipSelf()
    parentModule: CoreModule
  ) {
    if (parentModule) {
      throw new Error(
        'CoreModule is already loaded. Import it in the AppModule only'
      );
    }
  }
}



