import { Component, EventEmitter, Input, Output, OnInit, OnDestroy } from '@angular/core';
import { NgClass } from '@angular/common';
import { Router, ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-factor-select',
  templateUrl: 'factor-select.component.html',
  styleUrls: ['factor-select.component.css']
})
export class FactorSelectComponent implements OnInit, OnDestroy {
  @Input() items: any[];
  @Input() placeholder: string;
  @Input() limit: number;
  @Output() selected: EventEmitter<any> = new EventEmitter();
  @Output() removed: EventEmitter<any> = new EventEmitter();

  itemsHidden: boolean;
  itemselected: any[];
  showItems: any[];

  constructor() {
  }

  ngOnInit() {
    this.showItems = [];
    this.itemsHidden = true;
    this.itemselected = [];
  }

  ngOnDestroy() {
  }

  select(item) {
    this.itemselected.push(item);
    this.showItems = this.showItems.filter(data => data.id !== item.id);
    this.itemsHidden = true;
    this.selected.emit(item);
    event.stopPropagation();
  }

  blur() {
    this.itemsHidden = true;
  }

  click() {
    if (this.showItems.length < 1) {
      this.showItems = this.items;
    }
    if (this.itemselected.length >= this.limit) {
      return;
    }
    this.itemsHidden = false;
  }

  removeItem(item) {
    this.itemselected = this.itemselected.filter(data => data.id !== item.id);
    console.log('itemselected:', this.itemselected);
    if (this.itemselected.length > 0) {
      this.showItems = this.items.filter(data => {
        let idx = this.itemselected.findIndex(ele => ele.id === data.id);
        if (idx >= 0) return false;
        else return true;
      });
    } else {
      this.showItems = this.items;
    }
    this.removed.emit(item);
    event.stopPropagation();
  }

  test() {
    console.log('test');
  }
}



