import { BaseClass } from '../base-class';
export class Task extends BaseClass {
  taskSid: number;
  taskName: string;
  workflowSid: number;
  taskType: string;
  taskResult: string;
  comment: string;
  taskStatus: string;
  assignor: string;
  assignorName: string;
  assignee: string;
  assigneeName: string;
  context: number;
  createTime: string;
  updateTime: string;
}



