import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, Params } from '@angular/router';
import { Warning } from '../../shared';
import { CompanyService } from '../../company/company.service';
import { TaskService } from '../../task/task.service';
import { UserService } from '../../user/user.service';
import { WarningService } from '../../overview/warning.service';
import { TaskStatusEnum } from '../entities';

@Component({
  selector: 'app-warning-task',
  templateUrl: './warning-task.component.html',
  styleUrls: ['./warning-task.component.css']
})
export class WarningTaskComponent implements OnInit {
  isView: boolean;
  companyDetail;
  workflowSid;
  warning;
  claimDate: Date;
  task;
  saveDisabled = false;
  taskStatus: TaskStatusEnum;
  warningImportanceList: any[] = [
    { importance: 3, value: '高', status: true },
    { importance: 2, value: '中', status: true },
    { importance: 1, value: '低', status: true },
  ];

  constructor(private router: Router, private activeRoute: ActivatedRoute,
    private companyService: CompanyService,
    private taskService: TaskService,
    public userService: UserService,
    private warningService: WarningService
  ) { }

  ngOnInit() {
    this.workflowSid = this.activeRoute.snapshot.params['workflowSid'];
    this.taskStatus = this.activeRoute.snapshot.params['taskStatus'];
    this.getWarning(this.workflowSid);
  }

  private getWarning(workflowSid) {
    this.taskService.getWorkflowDetail(workflowSid).subscribe(
      data => {
        this.isView = data.workflowStatus === '已完成';
        this.claimDate = data.updateTime || new Date();
        this.task = data.tasks[0];
        this.warningService.getWarning(data.context).subscribe(warning => {
          if (!warning) {
            return;
          }
          this.warning = warning;
          if (!this.isView) {
            this.warning.severityAdjusted = this.warning.severity;
          }
          this.getCompanyDetail(this.warning.subjectId);
        });
      }
    );
  }

  private getCompanyDetail(id) {
    this.companyService.getCompanyDetail(id).subscribe(
      data => this.companyDetail = data
    );
  }

  save() {
    if (this.task) {
      this.saveDisabled = true;
      this.warning.processFlag = 2; // 2-完成认定 1-认定中  0 - default
      this.taskService.completeWarningTask(this.task.taskSid, this.warning).subscribe(data => {
        this.saveDisabled = false;
        this.router.navigate(['/task']);
      }, error => {

      });
    }
  }

  cancel() {
    this.getWarning(this.workflowSid);
  }
}



