import {
  Component,
  OnInit,
  ViewChild,
  ElementRef,
  TemplateRef,
  ViewContainerRef
} from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { HomeService } from '../../../root/home/home.service';
import { BondService } from '../bond.service';
import { BondIssuer } from '../bond';

import { BaseClass } from '../../../shared/base-class';
import {
  Bond,
  BondPledge,
  BondWarrantor,
  BondParty,
  Rating
} from '../../../shared';
import { UserService } from '../../../user/user.service';
import { MsgService } from 'app/core/msg.service';
import { ToastsManager } from 'ng2-toastr';
@Component({
  templateUrl: 'bond-detail.component.html',
  styleUrls: ['bond-detail.component.css']
})
export class BondDetailComponent implements OnInit {
  @ViewChild('scrollWrapper') scrollWrapper: ElementRef;
  bondDetail: Bond | any;
  BondWarrantorDetailList: BondWarrantor[];
  bondPledgeDetailList: BondPledge[];
  SameIssuerList: Bond[];
  issuerList: BondIssuer[];
  underwriterList: BondIssuer[];

  bondParty: BondParty;
  bondId: number;
  outerRatingList;
  innerRatingList;
  maturity: string;
  clientId: number; // 當前用戶標示
  bondRatingHidden: boolean;

  loading: boolean;
  bondDetailLoading: boolean;
  bondIssueLoading: boolean;
  bondUnderwriterLoading: boolean;
  bondSameIssuerLoading: boolean;
  isCollapse: boolean;
  sameIssuerPage: number;
  sameIssuerOver: boolean;
  constructor(
    private router: Router,
    private activeRoute: ActivatedRoute,
    private bondService: BondService,
    private homeService: HomeService,
    private msg: MsgService,
    public userService: UserService,
    private toastr: ToastsManager
  ) {}
  ngOnInit() {
    // 手风琴效果
    this.bondDetailLoading = true;
    this.bondDetail = new Bond();
    this.BondWarrantorDetailList = [];
    this.bondPledgeDetailList = [];
    this.SameIssuerList = [];
    this.issuerList = [];
    this.outerRatingList = [];
    this.innerRatingList = [];
    this.bondParty = new BondParty();
    this.bondRatingHidden = true;
    this.bondIssueLoading = true;
    this.bondUnderwriterLoading = true;
    this.bondSameIssuerLoading = true;
    this.isCollapse = true;
    this.sameIssuerPage = 0;
    this.sameIssuerOver = false;
    this.activeRoute.params.forEach((params: Params) => {
      this.bondId = +params['bondId'];
      this.getBondDetail();
      this.getBondPledgeDetail();
      this.getBondWarrantorDetail();
      this.getSameIssuer(0);
      this.getIssuer();
      this.getOuterRatingList();
      this.getInnerRatingList();
      this.getUnderwriter();
    });

    this.clientId = this.userService.CurrentUser.clientId;
    this.loading = false;
  }

  editBond() {
    this.getIssuer();
    this.router.navigate(['/product/bond', this.bondId]);
  }
  generateBondDetail(data) {
    this.bondDetail = new Bond();
    if (data) {
      this.bondDetail.generateByRaw(data);
    }
    this.checkBondRatingIsHidden();
    this.bondDetailLoading = false;
  }
  generateBondPledgeDetail(data) {
    this.bondPledgeDetailList = [];
    if (data) {
      for (let item of data) {
        let bondPledge = new BondPledge();
        bondPledge.generateByRaw(item);
        this.bondPledgeDetailList.push(bondPledge);
      }
    }
  }
  getBondDetail() {
    this.bondService
      .getBondDetail(this.bondId)
      .subscribe(
        data => this.generateBondDetail(data),
        error => console.log('error', error)
      );
  }
  getBondPledgeDetail() {
    this.bondService
      .getBondPledgeDetail(this.bondId)
      .subscribe(
        data => this.generateBondPledgeDetail(data),
        error => console.log('error', error)
      );
  }

  generateBondWarrantorDetail(data) {
    this.BondWarrantorDetailList = [];
    if (data) {
      for (let item of data) {
        let bondWarrantor = new BondWarrantor();
        bondWarrantor.generateByRaw(item);
        this.BondWarrantorDetailList.push(bondWarrantor);
      }
    }
    console.log('bondWarrantor', this.BondWarrantorDetailList);
  }

  getBondWarrantorDetail() {
    this.bondService
      .getBondWarrantorDetail(this.bondId)
      .subscribe(
        data => this.generateBondWarrantorDetail(data),
        error => console.log('error', error)
      );
  }

  generateSameIssuer(data, page) {
    if (page === 0) this.SameIssuerList = [];
    if (!data || data.length < 1) {
      this.bondSameIssuerLoading = false;
      return;
    }
    if (data) {
      if (data.length < 50) {
        this.sameIssuerOver = true;
      }
      for (let item of data) {
        let sameIssuer = new Bond();
        sameIssuer.generateByRaw(item);
        this.SameIssuerList.push(sameIssuer);
      }
      this.bondSameIssuerLoading = false;
    }
  }
  getSameIssuer(page: number) {
    this.bondService
      .getSameIssuer(this.bondId, this.sameIssuerPage)
      .subscribe(
        data => this.generateSameIssuer(data, page),
        error => console.log('error', error)
      );
  }
  // 加载更多相同发行人信息
  loadMoreSameIssuer() {
    this.sameIssuerPage++;
    this.getSameIssuer(this.sameIssuerPage);
  }
  generateIssuer(data) {
    this.issuerList = [];
    this.bondIssueLoading = false;
    if (!data || data.length < 1) return;
    for (let item of data) {
      let bondIssuer = new BondIssuer();
      bondIssuer.generateByRaw(item);
      this.issuerList.push(bondIssuer);
    }
    this.checkBondRatingIsHidden();
  }

  getIssuer() {
    this.bondService
      .getIssuer(this.bondId)
      .subscribe(
        data => this.generateIssuer(data),
        error => console.log('error', error)
      );
  }

  generateUnderwriter(data) {
    this.underwriterList = [];

    if (!data || data.length < 1) {
      this.bondUnderwriterLoading = false;
      return;
    }
    for (let item of data) {
      if (item.company == null) {
        item.company = [];
      }
      let bondIssuer = new BondIssuer();
      bondIssuer.generateByRaw(item);
      this.underwriterList.push(bondIssuer);
    }

    this.bondUnderwriterLoading = false;
  }

  getUnderwriter() {
    this.bondService
      .getUnderwriter(this.bondId)
      .subscribe(
        data => this.generateUnderwriter(data),
        error => console.log('error', error)
      );
  }

  getOuterRatingList() {
    this.bondService
      .getOuterRatingList(this.bondId)
      .subscribe(
        data => this.generateOuterRatingList(data),
        error => console.log('getOuterRatingList error:', error)
      );
  }

  getInnerRatingList() {
    this.bondService
      .getInnerRatingList(this.bondId)
      .subscribe(
        data => this.generateInnerRatingList(data),
        error => console.log('getInnerRatingList error:', error)
      );
  }

  generateOuterRatingList(data) {
    this.outerRatingList = [];
    if (!data || data.length < 1) {
      return;
    }
    for (let item of data) {
      let outerRating = new Rating();
      outerRating.generateByRaw(item);
      this.outerRatingList.push(outerRating);
    }
  }

  generateInnerRatingList(data) {
    this.innerRatingList = [];
    if (!data || data.length < 1) {
      return;
    }
    for (let item of data) {
      let innerRating = new Rating();
      innerRating.isCollapse = true;
      innerRating.generateByRaw(item);
      this.innerRatingList.push(innerRating);
    }
  }

  // 根据债券标示 删除债券信息
  delBond() {
    this.msg.confirm('确定删除吗?').result.then(result => {
      this.bondService.delBond(this.bondId).subscribe(
        data => {
          this.msg.alert('债券删除成功');
          setTimeout(() => this.router.navigate(['product/bond']), 2000);
        },
        error => {
          console.log('delete bond error:', error);
          this.msg.alert('债券删除失败，请重试！');
        }
      );
    });
  }

  private jumpToBondRating() {
    /*if (this.issuerList.length < 0 || this.issuerList.every(item => !item.company.innerRating)) {
        this.msg.alert('该债券由于所有发行人都未评级，无法进行评级，请先对债券发行人进行评级！', 2000);
        return;
    }
    if (!this.bondDetail.securityType || (this.bondService.notAllowRatingBondTypeList.indexOf(this.bondDetail.securityType) >= 0)) {
        this.msg.alert('该债券不属于可评级的债券类型', 2000);
        return;
    }*/
    this.router.navigate(['product/bond/rating', this.bondId]);
  }

  private checkBondRatingIsHidden() {
    if (
      this.issuerList.length > 0 &&
      this.issuerList.every(item => !!item.company.innerRating) &&
      (this.bondDetail.securityType &&
        this.bondService.notAllowRatingBondTypeList.indexOf(
          this.bondDetail.securityType
        ) < 0)
    ) {
      this.bondRatingHidden = false;
    }
  }

  public scrollTo(elementRef: string) {
    this.homeService.scrollInside(elementRef, this.scrollWrapper);
  }

  // 超链接
  private goToDetail(id: number) {
    this.router.navigate(['company/detail', id]);
  }
  private goToBondDetail(secinnerId: number) {
    this.router.navigate(['product/bond/detail', secinnerId]);
    this.scrollTo('#basicinfowrapper');
  }

  /**
   * 导出
   */
  exportBond() {
    this.loading = true;
    this.bondService.exportBond(this.bondId).subscribe(
      data => {
        this.loading = false;
        let date = new Date();
        const fileName =
          this.bondDetail.securityShortName +
          '_基本信息' +
          date.getFullYear() +
          '-' +
          (date.getMonth() + 1) +
          '-' +
          date.getDate() +
          `.xls`;
        if (window.navigator.msSaveBlob) {
          window.navigator.msSaveBlob(data.body, fileName);
        } else {
          let downloadUrl = window.URL.createObjectURL(data.body);
          let downloadLink = document.createElement('a');
          downloadLink.href = downloadUrl;
          downloadLink.download = fileName;
          document.body.appendChild(downloadLink);
          downloadLink.click();
          document.body.removeChild(downloadLink);
        }
      },
      error => {
        this.loading = false;
        this.toastr.error('债券导出失败，请重试！');
        console.log('exportBond error:', error);
      }
    );
  }
}



