import { Injectable } from '@angular/core';

import { Observable } from 'rxjs/Observable';

import 'rxjs/add/observable/of';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/delay';

@Injectable()
export class AuthService {
  isLoggedIn = false;
  accessToken: string;
  // store the URL so we can redirect after logging in
  redirectUrl: string;
  public AuthUser: string;

  checkLogin(): boolean {
    this.isLoggedIn = !!window.localStorage.getItem('login_cm_token');
    if (this.isLoggedIn) {
      let accessToken = window.localStorage.getItem('login_cm_token');
      this.AuthUser = window.localStorage.getItem('username');
      console.log('from localStorage', this.AuthUser);
    }
    return this.isLoggedIn;
  }

  logout(): void {
    this.isLoggedIn = false;
  }

  getAuthorizationHeader() {
    let token = this.getToken();
    return `${token.token_type} ${token.access_token}`;
  }

  private getToken() {
    let tokenString = window.localStorage.getItem('login_cm_token');
    if (!tokenString) {
      return null;
    }
    return JSON.parse(tokenString);
  }
}



