import { Component, enableProdMode, OnDestroy, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DatatableComponent } from '@swimlane/ngx-datatable/release';
import { MsgService } from 'app/core/msg.service';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';
import { Subscription } from 'rxjs/Subscription';

import { AnalysisItemCompany } from '../../analysis/analysis';
import { AnalysisService } from '../../analysis/analysis.service';
import { CompanyService } from '../../company/company.service';
import { BondService } from '../../product/bond/bond.service';
import { Portfolio, PortfolioItemSingle, Regulation } from '../../shared';
import { PortfolioItemVo } from '../entities';
import { PortfolioService } from '../portfolio.service';
import { RuleSelectComponent } from '../rule-select/rule-select.component';
import { SelfSelectComponent } from '../self-select/self-select.component';
import { ToastsManager } from 'ng2-toastr';
import { UserService } from 'app/user/user.service';

enableProdMode();
@Component({
  selector: 'app-portfolio',
  templateUrl: 'portfolio-admin.component.html',
  styleUrls: ['portfolio-admin.component.css']
})
export class PortfolioAdminComponent implements OnInit, OnDestroy {
  @ViewChild('hdrTpl') hdrTpl: TemplateRef<any>;
  @ViewChild('checkboxTmpl') checkboxTmpl: TemplateRef<any>;
  @ViewChild('delCheckTmpl') delCheckTmpl: TemplateRef<any>;
  @ViewChild('companyTemplate') companyTemplate: TemplateRef<any>;
  @ViewChild('bondTemplate') bondTemplate: TemplateRef<any>;
  @ViewChild('numberTmpl') numberTmpl: TemplateRef<any>;
  @ViewChild('cellRatingTemplate') cellRatingTemplate: TemplateRef<any>;
  @ViewChild('ruleselect') public ruleselect: RuleSelectComponent;
  @ViewChild('selfselect') public selfselect: SelfSelectComponent;
  @ViewChild('issueVolumnTemplate') public issueVolumnTemplate: TemplateRef<any>;
  @ViewChild('portfolioItemsDataTable') public portfolioItemsDataTable: DatatableComponent;
  @ViewChild('portfolioItemsDataTable1') public portfolioItemsDataTable1: DatatableComponent;

  isnamerepeatState: boolean;
  errorMessage: string;
  loading: boolean;
  saveDisabled: boolean;
  setDefaultDisable: boolean;
  portfolioList: Portfolio[];
  portfolioList1: Portfolio[];
  edit: boolean;
  add: boolean;
  addFlag: boolean;
  portfolioItemsList: PortfolioItemVo[];
  portfolioTypes = [{ id: 0, text: '企业' }, { id: 1, text: '债券'}];
  portfolioSelected: Portfolio;
  tableColumns = [];
  tableColumnsBond = [];
  tableMessage: any;
  tableMessage1: any;
  tableLoadingMessage: any;
  portfolioNameSearch = new Subject<string>();
  loadingMessage: string;
  handerFollowSub: Subscription;
  isHanderFollow: boolean;
  addType: string;
  checkAllStatus: boolean;
  handerFollowAnnouncement = new Subject();
  followCompanys: number[];
  followPortfolia: Portfolio;
  isChosen: boolean;
  isAutoUpdate: boolean;
  companyListHidden: boolean;
  bondListHidden: boolean;
  regulationType: number;
  regulationShow: Regulation;
  isRulationShow: boolean;
  newListObj: PortfolioItemSingle;
  currentType: string;
  selfAndRegulation: number;
  newPortfolia: PortfolioItemVo[];
  new: boolean;
  exist: boolean;
  isFromList: boolean;
  tempList: any[];
  portfolioLength: number; // 企业下的组合个数
  addCompOrBondflag = false;
  analyzeFlag = false;
  isSelect = true;
  modifyFlag = true;
  okValue = '确定';
  companyComeFlag = false;
  rule: any;
  okDisabled = false;
  removedItemIds: any[];
  addedItemIds: any[];
  originalPortfolioName: string; // 原始组合名称

  constructor(private portfolioService: PortfolioService, private companyService: CompanyService,
    private bondService: BondService, private analysisService: AnalysisService, private router: Router,
    private msg: MsgService, private toastr: ToastsManager, private userService: UserService) {
  }

  ngOnInit() {
    this.isFromList = false;
    this.tempList = [];
    this.add = false;
    this.addFlag = false;
    this.edit = false;
    this.new = false;
    this.exist = false;
    this.newPortfolia = [];
    this.selfAndRegulation = 0;
    this.companyListHidden = false;
    this.bondListHidden = true;
    this.isChosen = true;
    this.isAutoUpdate = true;
    this.isHanderFollow = false;
    this.addType = '';
    this.loading = false;
    this.isnamerepeatState = false;
    this.saveDisabled = false;
    this.setDefaultDisable = true;
    this.regulationType = 0;
    this.regulationShow = new Regulation();
    this.isRulationShow = false;
    this.currentType = '企业';
    this.removedItemIds = [];
    this.addedItemIds = [];
    this.tableColumns = [
      { prop: 'checkstatus', headerTemplate: this.hdrTpl, cellTemplate: this.checkboxTmpl,
        maxWidth: 35, resizeable: false, sortable: false },
      { name: '企业名称', prop: 'companyName', cellTemplate: this.companyTemplate, minWidth: 300, resizeable: false, sortable: false },
      { name: '所属敞口', prop: 'exposure', resizeable: false, sortable: false },
      { name: '评级结果', prop: 'innerRating.rating', resizeable: false, cellTemplate: this.cellRatingTemplate, sortable: false },

      // { name: '资产总计(万元)', prop: 'sumAsset', resizeable: false, cellTemplate: this.numberTmpl, sortable: false }
      { name: '财报日期', prop: 'innerRating.factorDate', resizeable: false, sortable: false },
      { prop: '', cellTemplate: this.delCheckTmpl, maxWidth: 100, resizeable: false, sortable: false }
    ];
    this.tableColumnsBond = [
      { prop: 'checkstatus', headerTemplate: this.hdrTpl, cellTemplate: this.checkboxTmpl,
        width: 35, maxWidth: 35, resizeable: false, sortable: false },
      { name: '债券代码', prop: 'securityCode', resizeable: false, sortable: false },
      { name: '债券简称', prop: 'securityShortName', cellTemplate: this.bondTemplate, resizeable: false, sortable: false },
      { name: '发行规模', prop: 'issueVolumn', cellTemplate: this.issueVolumnTemplate, resizeable: false, sortable: false },
      { name: '评级结果', prop: 'innerRating.rating', resizeable: false, cellTemplate: this.cellRatingTemplate, sortable: false },
      { name: '财报日期', prop: 'innerRating.factorDate', resizeable: false, sortable: false },
      { prop: '', cellTemplate: this.delCheckTmpl, width: 100, maxWidth: 100, resizeable: false, sortable: false }
    ];
    this.tableMessage = {
      emptyMessage: '该组合无企业',
      totalMessage: '总计'
    };
    this.tableMessage1 = {
      emptyMessage: '该组合无债券',
      totalMessage: '总计'
    };
    this.tableLoadingMessage = {
      emptyMessage: '加载中',
      totalMessage: '总计'
    };
    this.portfolioItemsDataTable.messages = this.tableLoadingMessage;
    this.portfolioSelected = new Portfolio();
    this.portfolioList = [];
    this.portfolioList1 = [];
    this.portfolioItemsList = [];
    this.getCompanyPortfolio();
    this.portfolioNameSearch.debounceTime(800).distinctUntilChanged().switchMap(
      term => term ? this.portfolioService.checkIsNameExist(term, this.portfolioSelected.type) : Observable.of<boolean>(false)
    ).catch(
      error => { console.log('error:', error); return Observable.of<boolean>(false); }
      ).subscribe(
      value => {
        if (!this.portfolioSelected.name || this.portfolioSelected.name.length === 0) return;
        this.isnamerepeatState = value as boolean;
        this.errorMessage = '组合名称已存在，请修改';
        this.saveDisabled = value as boolean;
        if (this.originalPortfolioName !== '' && this.originalPortfolioName === this.portfolioSelected.name) {
          this.errorMessage = '';
          this.saveDisabled = false;
        }
        if (this.portfolioSelected.name.length > 20) {
          this.isnamerepeatState = true;
          this.errorMessage = '组合名称最多允许20个字符';
          this.saveDisabled = true;
        }
        if (this.addType === 'static' && this.portfolioSelected.name && this.portfolioSelected.name.length <= 20) { // 静态组合为空是可以保存
          this.saveDisabled = false;
          return;
        }
        // 动态组合必需要有对应规则
        if (this.addType === 'auto' && this.portfolioSelected.name
        && this.portfolioSelected.name.length <= 20 && this.regulationShow.name) {
          this.saveDisabled = false;
        } else if (this.addType === 'auto' && !this.regulationShow.name) {
          this.saveDisabled = true;
        }
      }
      );
    //  企业搜索关注的企业创建投资组合
    this.acceptFollowCompany();
    this.handerFollowSub = this.handerFollowAnnouncement.subscribe(
      () => this.handerFollowCompany()
    );
  }

  ngOnDestroy() {
    this.handerFollowSub.unsubscribe();
  }
  getBondPortfolio() {
    this.analyzeFlag = false;
    this.add = false;
    this.addFlag = false;
    this.addType = null;
    this.addCompOrBondflag = false;
    this.modifyFlag = true;

    this.regulationType = 1;
    this.portfolioSelected.type = '债券';
    this.removedItemIds = [];
    this.addedItemIds = [];
    this.portfolioService.getPortfolios(1).subscribe(
      data => {
        this.currentType = '债券';
        this.generatePortfolios1(data);
      },
      error => console.log('getPortfolioList error:', error)
    );
  }
  getCompanyPortfolio() {
    this.isSelect = true;
    this.analyzeFlag = false;
    this.add = false;
    this.addFlag = false;
    this.addType = null;
    this.addCompOrBondflag = false;
    this.modifyFlag = true;
    this.regulationType = 0;
    this.portfolioSelected.type = '企业';
    this.loading = true;
    this.portfolioService.getPortfolios(0).subscribe(
      data => {
        this.currentType = '企业';
        this.generatePortfolios(data);
      },
      error => console.log('getPortfolioList error:', error)
    );
  }
  addNewPortfolio(addType: string, isHanderFollow: boolean = false) {
    this.originalPortfolioName = '';
    this.analyzeFlag = false;
    this.checkAllStatus = false;
    this.portfolioItemsList.map(
      value => value.checkstatus = false
    );
    this.portfolioSelected.regulationId = null;
    this.addCompOrBondflag = false;
    this.selfselect.hide();
    this.ruleselect.hide();
    this.addType = addType;
    if (this.addType === '' || this.addType === null) { // 无组合的情况下，默认展示添加动态组合
      this.add = true;
      this.addFlag = true;
      this.addType = 'auto';
    }
    this.modifyFlag = false;
    this.loading = false;
    this.isChosen = true;
    this.isRulationShow = false;
    this.isAutoUpdate = false;
    this.regulationShow = new Regulation();
    this.isnamerepeatState = false;
    this.saveDisabled = true;
    if (this.portfolioSelected.type === '企业') {
      this.portfolioSelected = new Portfolio();
      this.portfolioSelected.type = '企业';
      this.selfAndRegulation = 0;
      this.currentType = '企业';
    } else {
      this.portfolioSelected = new Portfolio();
      this.portfolioSelected.type = '债券';
      this.selfAndRegulation = 0;
      this.currentType = '债券';
    }

    this.portfolioItemsDataTable.offset = 0;
    if (this.portfolioList.length < 1) {
      this.portfolioSelected.isDefault = 1;
      this.setDefaultDisable = true;
    } else {
      this.portfolioSelected.isDefault = 0;
      this.setDefaultDisable = false;
    }
    this.portfolioSelected.owner = this.userService.CurrentUser.userName;
    if (!isHanderFollow) {
      this.portfolioItemsList = [];
    }
  }


  checkall($event) {
    this.portfolioItemsList.map(
      value => value.checkstatus = $event
    );
    let list = [];
    for (let item of this.portfolioItemsList) {
      let portfolioItem = new PortfolioItemVo();
      portfolioItem.generateByRaw(item);
      list.push(portfolioItem);
    }
    this.portfolioItemsList = list;
  }

  check(row: any) {
    this.portfolioItemsList.map(
      value => {
        if (this.currentType === '企业') {
          if (value.companyId === row.companyId) value.checkstatus = !value.checkstatus;
        } else {
          if (value.secinnerId === row.secinnerId) value.checkstatus = !value.checkstatus;
        }
      }
    );
    let sel = this.portfolioItemsList.filter(p => p.checkstatus);
    if (sel.length > 20) {
      this.msg.alert('请选择不超过20个企业进行分析。');
    }
    this.refreshCheckboxAll();
  }

  delCheck(row: any) {
    if (this.currentType === '企业') {
      this.portfolioItemsList = this.portfolioItemsList.filter(it => it.companyId !== row.companyId);
      this.removedItemIds.push(row.companyId);
    } else {
      this.portfolioItemsList = this.portfolioItemsList.filter(it => it.secinnerId !== row.secinnerId);
      this.removedItemIds.push(row.secinnerId);
    }
  }

  cleanAll() {
    this.msg.confirm('确定要清空该组合中的内容吗?').result
      .then(result => {
        if (this.currentType === '企业') {
          this.portfolioItemsList.forEach(it => this.removedItemIds.push(it.companyId));
          this.portfolioItemsList = this.portfolioItemsList.filter(it => !it.companyId);
        } else {
          this.portfolioItemsList.forEach(it => this.removedItemIds.push(it.secinnerId));
          this.portfolioItemsList = this.portfolioItemsList.filter(it => !it.secinnerId);
        }
      })
      .catch(err => { console.log('cancel'); this.loading = false; });
  }

  checkPortfolioName() {
    this.errorMessage = '';
    this.isnamerepeatState = false;
    if (!this.portfolioSelected.name || this.portfolioSelected.name.trim() === '') {
      this.isnamerepeatState = true;
      this.saveDisabled = true;
      this.errorMessage = '组合名称不能为空';
      return;
    }
    if (this.portfolioSelected.name.length > 20) {
      this.isnamerepeatState = true;
      this.errorMessage = '组合名称最多允许20个字符';
      this.saveDisabled = true;
      return;
    }
    if (this.addType === 'static') { // 静态组合为空是可以保存
      this.saveDisabled = false;
    }
    if (this.addFlag === false) {
      this.saveDisabled = false;
    }
    this.portfolioNameSearch.next(this.portfolioSelected.name.trim());
  }

  delPortfolio() {
    if (this.currentType === '企业' && this.portfolioSelected.isDefault === 1 && this.portfolioLength > 1) {
      this.msg.alert('删除该组合前请指定新的默认组合。');
      return;
    }
    this.msg.confirm('确定要删除该组合吗?').result
      .then(result => {
        this.loading = true;
        this.loadingMessage = '删除中...';
        this.portfolioService.delPortfolio(this.portfolioSelected.id).subscribe(
          data => {
            this.portfolioList = this.portfolioList.filter(item => item.id !== this.portfolioSelected.id);
            this.loading = false;
            this.addNewPortfolio(this.addType);
            this.msg.alert('删除成功');
            this.add = false;
            this.addFlag = false;
            this.addType = '';
            this.modifyFlag = true;
            if (this.currentType === '企业') {
              this.getCompanyPortfolio();
            } else {
              this.getBondPortfolio();
            }
          },
          error => { console.log('delPortfolio error:', error); this.loading = false; this.msg.alert('删除失败,请重试'); }
        );
      })
      .catch(err => { console.log('cancel'); this.loading = false; });

  }

  generatePortfolios(data: any[]) {
    this.portfolioList = [];
    if (data) {
      this.portfolioLength = data.length;
    } else {
      this.portfolioLength = 0;
    }
    if (!data || data.length < 1) {
      this.addNewPortfolio(this.addType);
    } else {
      for (let item of data) {
        let portfolio = new Portfolio();
        portfolio.generateByRaw(item);
        portfolio.type = '企业';
        if (!this.isHanderFollow && portfolio.isDefault && portfolio.isDefault === 1) {
          if (!this.portfolioSelected.id) {
            this.portfolioSelected = new Portfolio();
            this.portfolioSelected.generateByRaw(portfolio);
          }
        }
        this.portfolioList.push(portfolio);
      }
      // 获取默认组合的企业列表
      this.getPortfolioItems(this.portfolioSelected.id, this.portfolioSelected.type);
    }
    if (this.isHanderFollow) {
      this.handerFollowAnnouncement.next(true);
      this.isHanderFollow = false;
    }
  }
  generatePortfolios1(data: any[]) {
    this.portfolioList1 = [];
    if (!data || data.length < 1) {
      this.addNewPortfolio(this.addType);
    } else {
      for (let item of data) {
        let portfolio = new Portfolio();
        portfolio.generateByRaw(item);
        portfolio.type = '债券';
        if (!this.isHanderFollow && portfolio.isDefault && portfolio.isDefault === 1) {
          if (!this.portfolioSelected.id) {
            this.portfolioSelected = new Portfolio();
            this.portfolioSelected.generateByRaw(portfolio);
          }
        }
        this.portfolioList1.push(portfolio);
      }
      // 获取默认组合的债券列表
      this.getPortfolioItems(this.portfolioSelected.id, this.portfolioSelected.type);
    }
    if (this.isHanderFollow) {
      this.handerFollowAnnouncement.next(true);
      this.isHanderFollow = false;
    }
  }
  generatePortfolioItems(data: any[]) {
    this.portfolioItemsDataTable.offset = 0;
    this.portfolioItemsList = [];
    if (data && Array.isArray(data)) {
      for (let item of data) {
        if (!item) {
          continue;
        }
        let portfolioItem = new PortfolioItemVo();
        portfolioItem.generateByRaw(item);
        portfolioItem.checkstatus = false; // 由原默认都选中改为默认都不选中
        if (this.new === true) {
          portfolioItem.isNew = true;
        } else {
          portfolioItem.isNew = false;
        }
        this.portfolioItemsList.push(portfolioItem);
      }
    }
    if (this.isFromList === true && this.newPortfolia && this.newPortfolia.length > 0) {
      for (let item of this.newPortfolia) {
        item.checkstatus = false; // 由原默认都选中改为默认都不选中
        item.isNew = true;
      }
      if (this.companyComeFlag) {
        this.modifyFlag = false;
        this.saveDisabled = false;
      }
      this.selfSelectFinish(this.newPortfolia);
    }
    this.isFromList = false;
    // this.portfolioSelected.type = '企业';
    this.portfolioItemsDataTable.messages = this.tableMessage;
    this.loading = false;
    this.refreshCheckboxAll();
  }


  getPortfolioItems(portfolioId: number, portfolioType: string) {
    this.portfolioItemsDataTable.messages = this.tableLoadingMessage;
    this.portfolioItemsList = [];
    this.new = false;
    if (!this.loading) {
      this.loading = true;
      this.loadingMessage = '加载中...';
    }
    this.portfolioService.getPortfolioItems(portfolioId).subscribe(
      data => {
        this.originalPortfolioName = data.name;
        if (data.regulationId == null) {
          this.isChosen = true;
          this.isRulationShow = false;
          this.selfAndRegulation = 0;
          this.isAutoUpdate = true;
          this.regulationShow.name = null;
          this.regulationShow.desc = null;
          this.regulationShow.id = null;
        } else {
          this.isChosen = false;
          this.isRulationShow = true;
          this.regulationShow.name = data.regulationName;
          this.regulationShow.desc = data.regulationDesc;
          this.regulationShow.id = data.regulationId;
          this.selfAndRegulation = 1;
          if (data.isDynamic === 0) {
            this.isAutoUpdate = true;
            this.portfolioSelected.isDynamic = 0;
          } else {
            this.isAutoUpdate = false;
            this.portfolioSelected.isDynamic = 1;
          }
        }
        this.portfolioItemsList = [];
        if (portfolioType === '企业') {
          // tslint:disable-next-line:no-unused-expression
          data && data.companies && this.generatePortfolioItems(data.companies);
        } else if (portfolioType === '债券') {
          // tslint:disable-next-line:no-unused-expression
          data && data.bonds && this.generatePortfolioItems(data.bonds);
        }
        this.loading = false;
        this.portfolioItemsDataTable.messages = this.tableMessage;
      },
      error => { console.log('error:', error); this.loading = false; }
    );
  }

  acceptFollowCompany() {
    let followCompanys = this.companyService.followCompanys;
    let followPortfolios = this.companyService.portfolio;
    if (followCompanys && !followPortfolios) {// 添加到新组合
      this.add = true;
      this.addType = 'static';
    }
    if (followCompanys && followPortfolios) {// 添加到某个已有组合
      this.companyComeFlag = true;
      this.addedItemIds = followCompanys;
    }
    this.isFromList = this.companyService.isFromList;
    if (followCompanys && followCompanys.length > 0) {
      this.isHanderFollow = true;
      this.followCompanys = followCompanys;
      this.companyService.followCompanys = null;
      this.loading = true;
    }
    if (followPortfolios != null) {
      this.isHanderFollow = true;
      this.followPortfolia = followPortfolios;
      this.companyService.portfolio = null;
      this.loading = true;
    }
  }

  handerFollowCompany() {
    if (this.followPortfolia) {
      this.exist = true;
      this.followPortfolia.type = '企业';
      this.companyService.getCompaniesByIds(this.followCompanys).subscribe(
        data => {
          this.newPortfolia = data;
          this.add = true;
          this.selPortfolio(this.followPortfolia);
        },
        error => console.log('getPortfolioItemsListByCompanyList')
      );

    } else {
      this.new = true;
      this.portfolioItemsDataTable.messages = this.tableLoadingMessage;
      this.companyService.getCompaniesByIds(this.followCompanys).subscribe(
        data => {
          this.generatePortfolioItems(data);
          this.addNewPortfolio(this.addType, true);
        },
        error => console.log('getPortfolioItemsListByCompanyList')
      );
    }

  }

  checkIsNameExist(name: string): Observable<any> {
    return this.portfolioService.checkIsNameExist(this.portfolioSelected.name, this.portfolioSelected.type);
  }

  ruleSelect() {
    this.ruleselect.show();
  }

  ruleSelectFinish($event: PortfolioItemVo[]) {
    this.loading = false;
    this.addCompOrBondflag = false;
    this.saveDisabled = false;
    let addNumber = 0;
    if ((this.add === false && this.portfolioSelected.regulationId !== null) || (this.add === true && this.addType === 'auto')) {
      this.portfolioItemsList = []; // 原逻辑为全量更新企业，现在改为动态组合仍全量更新，静态组合增量更新
    }
    let items: PortfolioItemVo[] = [];
    for (let item of $event) {
      let index;
      if (this.currentType === '企业') {
        index = this.portfolioItemsList.findIndex(
          value => value.companyId === item.companyId
        );
      } else {
        index = this.portfolioItemsList.findIndex(
          value => value.secinnerId === item.secinnerId
        );
      }
      if (index < 0) {
        addNumber = addNumber + 1;
        let portfolioItem = new PortfolioItemVo();
        portfolioItem.generateByRaw(item);
        portfolioItem.checkstatus = false; // 由原默认都选中改为默认都不选中
        items.push(portfolioItem);
        if (this.currentType === '企业') {
          this.addedItemIds.push(item.companyId);
        }else {
          this.addedItemIds.push(item.secinnerId);
        }
      }
    }
    this.portfolioItemsList.unshift(...items);
    if (this.currentType === '企业') {
      this.portfolioItemsDataTable.rows = this.portfolioItemsList;
    } else {
      this.portfolioItemsDataTable1.rows = this.portfolioItemsList;
    }

    this.refreshCheckboxAll();
    let filterNumber = $event.length - addNumber;
    let message = `成功添加${addNumber}条`;
    this.msg.alert(message);
    if (this.portfolioSelected.regulationId != null) {
      this.isRulationShow = true;
      this.portfolioSelected.isDynamic = 1;
      this.isAutoUpdate = false;
    }
    if (this.add === true && !this.portfolioSelected.name) {
      this.saveDisabled = true;
    }
    if (this.isnamerepeatState === true) {
      this.saveDisabled = true;
    }
  }

  selSelect() {
    this.selfselect.show();
  }

  selPortfolio(portfolio: Portfolio) {
    this.isSelect = true;
    this.analyzeFlag = false;
    this.add = false;
    this.addFlag = false;
    this.addType = null;
    this.addCompOrBondflag = false;
    this.modifyFlag = true;
    this.selfselect.hide();
    this.ruleselect.hide();
    this.edit = true;
    this.isnamerepeatState = false;
    this.saveDisabled = false;
    this.portfolioSelected = new Portfolio();
    this.currentType = portfolio.type;
    this.new = false;
    this.removedItemIds = [];
    this.addedItemIds = [];
    this.portfolioSelected.generateByRaw(portfolio);
    if (portfolio.isDefault) {
      this.setDefaultDisable = true;
    } else { this.setDefaultDisable = false; }
    this.getPortfolioItems(portfolio.id, portfolio.type);
    if (portfolio.type === '企业') {
      this.regulationType = 0;
    } else {
      this.regulationType = 1;
    }
    this.isRulationShow = false;
  }

  selectePortfolioType(item: any) {
    this.portfolioSelected.type = item.text;
    this.regulationType = item.id;
    if (this.portfolioSelected.type === '企业') {
      this.currentType = '企业';
      this.portfolioItemsList = [];
    } else {
      this.currentType = '债券';
      this.portfolioItemsList = [];
    }
    this.regulationShow = new Regulation(); // 类型改变，清空规则
    this.checkPortfolioName();
    if (!this.portfolioSelected.name) {
      return;
    }
    this.portfolioService.checkIsNameExist(this.portfolioSelected.name, this.portfolioSelected.type).subscribe(
      nameExistFlag => {
        if (nameExistFlag) {
          this.isnamerepeatState = true;
          this.errorMessage = '组合名称已存在，请修改';
          this.saveDisabled = true;
          if (this.originalPortfolioName !== '' && this.originalPortfolioName === this.portfolioSelected.name) {
            this.errorMessage = '';
            this.saveDisabled = false;
          }
        }else {
          if (this.addType === 'static' && this.portfolioSelected.name && this.portfolioSelected.name.length <= 20) { // 静态组合为空是可以保存
            this.saveDisabled = false;
            return;
          }
          // 动态组合必需要有对应规则
          if (this.addType === 'auto' && this.portfolioSelected.name
          && this.portfolioSelected.name.length <= 20 && this.regulationShow.name) {
            this.saveDisabled = false;
          } else if (this.addType === 'auto' && !this.regulationShow.name) {
            this.saveDisabled = true;
          }
        }
      },
      error => console.log('getPortfolioItemsListByCompanyList')
    );
  }

  selfSelectFinish($event: PortfolioItemVo[]) {
    this.loading = false;
    let addNumber = 0;
    if (this.currentType === '企业') {
      for (let item of $event) {
        let index = this.portfolioItemsList.findIndex(
          value => value.companyId === item.companyId
        );
        if (index < 0) {
          addNumber = addNumber + 1;
          let portfolioItem = new PortfolioItemVo();
          portfolioItem.generateByRaw(item);
          portfolioItem.checkstatus = false; // 默认都不选中
          this.portfolioItemsList.unshift(portfolioItem);
          this.addedItemIds.push(item.companyId);
        }
      }
      this.portfolioItemsDataTable.rows = this.portfolioItemsList;
    } else {
      for (let item of $event) {
        let index = this.portfolioItemsList.findIndex(
          value => value.secinnerId === item.secinnerId
        );
        if (index < 0) {
          addNumber = addNumber + 1;
          let portfolioItem = new PortfolioItemVo();
          portfolioItem.generateByRaw(item);
          portfolioItem.checkstatus = false; // 默认都不选中
          this.portfolioItemsList.push(portfolioItem);
          this.addedItemIds.push(item.secinnerId);
        }
      }
      this.portfolioItemsDataTable1.rows = this.portfolioItemsList;
    }

    this.refreshCheckboxAll();
    let filterNumber = $event.length - addNumber;
    let message = `成功添加${addNumber}条`;
    if (filterNumber > 0) {
      message = message + `,有${filterNumber}条重复被自动过滤`;
    }
    this.msg.alert(message);
  }
  okBtnValue($event) {
    this.okValue = $event;
  }
  okDisabledFlag($event) {
    this.okDisabled = $event;
  }
  ruleSelectedItem($event: any) {
    this.rule = $event;
  }
  okClick() {
    if (this.portfolioSelected.regulationId != null || this.addType === 'auto') {
      this.isSelect = false;
    }
    if (this.isSelect === true) {
      this.selfselect.okClick();
    } else if (this.isSelect === false || this.addType === 'auto') {
      if (this.rule) {
        this.ruleselect.selectRule(this.rule);
      } else {
        this.ruleselect.hide();
        this.addCompOrBondflag = false;
      }
    }
    this.addCompOrBondflag = false;
    if (this.add === true && !this.portfolioSelected.name) {
      this.saveDisabled = true;
    }
  }
  RegulationDesc($event: Regulation) {
    this.regulationShow = $event;
    this.isRulationShow = true;
  }
  savePortfolioItems() {
    this.msg.confirm('确定要保存该组合吗?').result
      .then(result => {
        if (this.addType === 'auto' && JSON.stringify(this.regulationShow) === '{}') {
          this.msg.alert('保存失败，请添加筛选规则');
          return;
        }
        this.loading = true;
        this.add = false;
        this.addFlag = false;
        this.modifyFlag = true;
        if (this.portfolioSelected.isDefault) {
          this.portfolioSelected.isDefault = 1;
        } else {
          this.portfolioSelected.isDefault = 0;
        }
        if (this.portfolioSelected.id) {
          this.updatePortfolioItems();
        } else {
          this.addPortfolio();
        }
      })
      .catch(err => { console.log('cancel'); this.loading = false; });
  }

  addPortfolio() {
    let newList = [];
    for (let item of this.portfolioItemsList) {
      this.newListObj = new PortfolioItemSingle();
      if (item.companyId) {
        this.newListObj.itemId = item.companyId;
      } else if (item.secinnerId) {
        this.newListObj.itemId = item.secinnerId;
      }
      newList.push(this.newListObj);
    }
    this.portfolioSelected.regulationId = this.regulationShow.id;
    this.portfolioSelected.portfolioItems = newList;
    if (this.addType === 'auto') {
      this.portfolioSelected.isDynamic = 1;
    } else {
      this.portfolioSelected.isDynamic = 0;
    }
    this.portfolioService.addPortfolio(this.portfolioSelected).subscribe(
      data => {
        if (data) {
          this.portfolioSelected.generateByRaw(data);
          if (this.portfolioSelected.type === '企业') {
            if (this.portfolioSelected.isDefault || (this.portfolioList.length < 1 || !this.portfolioList[0].isDefault)) {
              this.setDefaultDisable = true;
              for (let i = 0; i < this.portfolioList.length; i++) { // 本次新增组合若被设置为默认组合，则将其他组合的isDefault标志置0
                this.portfolioList[i].isDefault = 0;
              }
              this.portfolioList.unshift(this.portfolioSelected);
            } else {
              let defaultPortfolio = this.portfolioList.shift();
              this.portfolioList.unshift(this.portfolioSelected);
              this.portfolioList.unshift(defaultPortfolio);
            }

            this.portfolioLength = this.portfolioList.length;
            this.newPortfolia = [];
            this.companyListHidden = false; this.bondListHidden = true;
            this.getPortfolioItems(this.portfolioSelected.id, this.portfolioSelected.type);
          } else {
            if (this.portfolioSelected.isDefault || (this.portfolioList1.length < 1 || !this.portfolioList1[0].isDefault)) {
              this.portfolioList1.unshift(this.portfolioSelected);
            } else {
              let defaultPortfolio1 = this.portfolioList1.shift();
              this.portfolioList1.unshift(this.portfolioSelected);
              this.portfolioList1.unshift(defaultPortfolio1);
            }

            this.bondListHidden = false; this.companyListHidden = true;
            this.new = false;
            this.exist = false;
            this.getPortfolioItems(this.portfolioSelected.id, this.portfolioSelected.type);
          }
          this.isRulationShow = false;
          this.regulationShow = new Regulation();
          this.addType = '';
        }
      },
      error => {
        console.log('addPortfolio error:', error);
        if (error.indexOf('无效的规则ID') >= 0) {
          this.msg.alert('该规则不存在');
        } else {
          this.msg.alert('保存失败');
        }
        this.loading = false;
      }
    );
  }

  updatePortfolioItems() {
    let updatedPortfolio = new Portfolio();
    Object.assign(updatedPortfolio, this.portfolioSelected);
    updatedPortfolio.portfolioItems = [];
    updatedPortfolio.regulationId = this.regulationShow.id;
    if (this.currentType === '企业') {
      this.portfolioItemsList.forEach(item => {
        updatedPortfolio.portfolioItems.push({ itemId: item.companyId });
      });
    } else {
      this.portfolioItemsList.forEach(item => {
        updatedPortfolio.portfolioItems.push({ itemId: item.secinnerId });
      });
    }
    // 增加组合条目与删除组合条目去重
    if (this.removedItemIds && this.removedItemIds.length > 0 && this.addedItemIds && this.addedItemIds.length > 0) {
      for (let item of this.removedItemIds) {
        let index = this.addedItemIds.findIndex(value => value === item);
        if (index >= 0) {
          this.addedItemIds = this.addedItemIds.filter(value => value !== item);
          this.removedItemIds = this.removedItemIds.filter(value => value !== item);
        }
      }
    }
    updatedPortfolio.removedItemIds = this.removedItemIds;
    updatedPortfolio.addedItemIds = this.addedItemIds;
    this.portfolioService.updatePortfolioItems(this.portfolioSelected.id, updatedPortfolio).subscribe(
      res => {
        this.refreshPortfolioItemsList(res);
        this.removedItemIds = [];
        this.addedItemIds = [];
      },
      error => {
        console.log('error:', error);
        this.loading = false;
        if (error.indexOf('启用规则时组合｛regulationId｝ 不能为空') > 0) {
          this.msg.alert('该组合中不包括规则添加进来的企业，不能启用“自动更新”参数');
        }else if (error.indexOf('无效的规则ID') >= 0) {
          this.msg.alert('该规则不存在');
        } else {
          this.msg.alert('保存失败');
        }
      }
    );
  }

  refreshPortfolioItemsList(res: any) {
    if (!this.portfolioSelected.id) {
      this.portfolioSelected.id = res.id;
      this.portfolioSelected.isDefault = res.isDefault;
      this.portfolioSelected.createDate = res.createDate;
      this.portfolioSelected.updateDate = res.updateDate;
      if (this.portfolioSelected.isDefault) {
        this.portfolioList.map(portfolio => portfolio.isDefault = 0);
        this.portfolioList.unshift(this.portfolioSelected);
      } else {
        this.portfolioList.push(this.portfolioSelected);
      }
      this.newPortfolia = [];
    } else {
      if (this.portfolioSelected.isDefault) {
        if (this.portfolioSelected.type === '企业') {
          this.portfolioList = this.portfolioList.filter(portfolio => portfolio.id !== this.portfolioSelected.id);
          this.portfolioList.map(portfolio => portfolio.isDefault = 0);
          this.portfolioList.unshift(this.portfolioSelected);
        } else {
          this.portfolioList1 = this.portfolioList1.filter(portfolio => portfolio.id !== this.portfolioSelected.id);
          this.portfolioList1.map(portfolio => portfolio.isDefault = 0);
          this.portfolioList1.unshift(this.portfolioSelected);
        }
      } else {
        if (this.portfolioSelected.type === '企业') {
          this.portfolioList.map(portfolio => {
            if (portfolio.id === this.portfolioSelected.id) {
              portfolio.isDefault = this.portfolioSelected.isDefault;
              portfolio.name = this.portfolioSelected.name;
            }
          });
        } else {
          this.portfolioList1.map(portfolio => {
            if (portfolio.id === this.portfolioSelected.id) {
              portfolio.isDefault = this.portfolioSelected.isDefault;
              portfolio.name = this.portfolioSelected.name;
            }
          });
        }
      }
      this.newPortfolia = [];
    }
    this.checkAllStatus = false;
    this.loading = false;
    this.new = false;
    this.exist = false;
    this.msg.alert('保存成功');
    this.getPortfolioItems(this.portfolioSelected.id, this.portfolioSelected.type);
    this.regulationShow = new Regulation();
  }

  refreshCheckboxAll() {
    if (this.portfolioItemsList.length < 1) {
      this.checkAllStatus = false;
      return;
    }
    if (this.portfolioItemsList.every(item => item.checkstatus)) {
      this.checkAllStatus = true;
      return;
    }
    if (this.portfolioItemsList.some(item => !item.checkstatus)) {
      this.checkAllStatus = false;
      return;
    }
  }

  cancelAnalysis() {
    this.analyzeFlag = false;
    this.checkAllStatus = false;
    this.portfolioItemsList.map(
      value => value.checkstatus = false
    );
  }

  analyzePortfolio() {
    this.analyzeFlag = true;
    this.modifyFlag = true;
    let sel = this.portfolioItemsList.filter(p => p.checkstatus);
    if (sel.length < 1) {
      return;
    }

    if (sel.length > 20) {
      this.msg.alert('请选择不超过20个企业进行分析。');
    } else {
      let companyIdList: AnalysisItemCompany[] = [];
      for (let company of this.portfolioItemsList) {
        if (company.checkstatus) {
          let item = new AnalysisItemCompany();
          item.companyId = company.companyId;
          item.companyName = company.companyName;
          item.innerRating = company.innerRating;
          companyIdList.push(item);
        }
      }

      this.analysisService.AnalysisResultAnnouncement = new BehaviorSubject<any>(companyIdList);
      this.router.navigate(['company/analysis']);
    }
  }

  // 超链接
  goToDetail(companyId: number) {
    this.router.navigate(['company/detail', companyId]);
  }
  goToBondDetail(secinnerId: number) {
    this.router.navigate(['product/bond/detail', secinnerId]);
  }

  /**
   * 组合导出
   */
  exportPortfolio() {
    this.loading = true;
    this.portfolioService.exportPortfolio(this.portfolioSelected.id).subscribe(
      data => {
        this.loading = false;
        let date = new Date();
        const fileName = this.portfolioSelected.name + '_' + date.getFullYear() +
          '-' + (date.getMonth() + 1) + '-' + date.getDate() + `.xls`;
        if (window.navigator.msSaveBlob) {
          window.navigator.msSaveBlob(data.body, fileName);
        } else {
          let downloadUrl = window.URL.createObjectURL(data.body);
          let downloadLink = document.createElement('a');
          downloadLink.href = downloadUrl;
          downloadLink.download = fileName;
          document.body.appendChild(downloadLink);
          downloadLink.click();
          document.body.removeChild(downloadLink);
        }
      },
      error => {
        this.loading = false;
        this.toastr.error('组合导出失败，请重试！');
        console.log('exportPortfolio error:', error);
      }
    );
  }

  changeMode() {
    if (this.isChosen === true) {
      this.isChosen = false;
      this.isRulationShow = true;
      this.portfolioSelected.isDynamic = 1;
      this.isAutoUpdate = false;
    } else {
      this.isChosen = true;
      this.isRulationShow = false;
      this.portfolioSelected.isDynamic = 0;
      this.isAutoUpdate = true;
    }
  }
  addNew() {
    this.modifyFlag = false;
    this.isSelect = true;
    this.addCompOrBondflag = true;
    this.portfolioSelected.isDynamic = 0;
    if (this.addType) {
      if (this.addType === 'auto') {
        this.ruleSelect();
        return;
      } else if (this.addType === 'static') {
        this.selSelect();
        return;
      }
    }

    if (this.isChosen === true) {
      this.selSelect();
    } else if (this.isChosen === false) {
      this.portfolioSelected.isDynamic = 1;
      this.ruleSelect();
    }
  }
  addNewP() {
    this.add = true;
    if (this.addFlag === true) {
      this.addFlag = false;
    } else if (this.addFlag === false) {
      this.addFlag = true;
    }
  }
  autoUpdate() {
    if (this.isAutoUpdate === true) {
      this.isAutoUpdate = false;
      this.portfolioSelected.isDynamic = 1;

    } else if (this.isAutoUpdate === false) {
      this.isAutoUpdate = true;
      this.portfolioSelected.isDynamic = 0;
    }
  }
  cancel() {
    this.add = false;
    this.addType = '';
    this.analyzeFlag = false;
    this.addCompOrBondflag = false;
    this.modifyFlag = true;
    this.newPortfolia = [];
    this.removedItemIds = [];
    if (this.companyListHidden && !this.bondListHidden) {
      this.getBondPortfolio();
    } else if (!this.companyListHidden && this.bondListHidden) {
      this.getCompanyPortfolio();
    }
  }
}



