import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AnalysisComponent } from './analysis.component';
import { AnalysisCompanyComponent } from './analysis-company.component';
import { AnalysisIndustryComponent } from './analysis-industry.component';
import { AnalysisArchivesComponent } from './analysis-archives.component';

export const analysisRoutes: Routes = [
  {
    path: 'analysis',
    component: AnalysisComponent,
    children: [
      {
        path: '',
        component: AnalysisCompanyComponent
      },
      {
        path: 'company',
        component: AnalysisCompanyComponent
      },
      {
        path: 'industry',
        component: AnalysisIndustryComponent
      },
      {
        path: 'archives',
        component: AnalysisArchivesComponent
      }
    ]
  }
];





