import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { SharedModule } from '../shared/shared.module';
import { NgUploaderModule } from 'ngx-uploader';

import { AdminComponent } from './admin.component';
import { AdminUserComponent } from './user/admin-user.component';
import { AdminInfoComponent } from './user-info/user-info.component';
import { UserSystemComponent } from './user-system/user-system.component';
import { AdminService } from './admin.service';
import { ExposureOwnerComponent } from './exposure-owner/exposure-owner.component';
import { CheckUserDirective } from './check-user.directive';


@NgModule({
  imports: [
    CommonModule,
    RouterModule,
    FormsModule,
    SharedModule,
    NgUploaderModule
  ],
  exports: [CheckUserDirective],
  declarations: [AdminComponent, AdminUserComponent, AdminInfoComponent, UserSystemComponent, ExposureOwnerComponent, CheckUserDirective],
  providers: [AdminService],
})
export class AdminModule { }



