import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { CompanyRatingRecordChild } from 'app/task/entities';
@Injectable()
export class RatingService {
  constructor(private http: HttpClient) { }

  getMainRatingList(owner: string, portfolios: number) {
    return this.http.get(`/ratings/portfolios/${owner}/${portfolios}`);
  }

  getCompanyRatingHistory(companyId: number) {
    return this.http.get<any>(`/ratings/records/company/${companyId}`);
  }

  getCompanyRatingOuterHistory(companyId: number) {
    return this.http.get<any>(`/companies/${companyId}/creditRatingInfos`);
  }

  initialRating(obj) {
    return this.http.post<any>('/ratings/company/measure/', obj);
  }

  finalRating(obj) {
    return this.http.put('/ratings/', obj);
  }

  getHistory(companyId: number) {
    return this.http.get('/ratings/companies/' + companyId);
  }

  getYear(companyId: number) {
    return this.http.get('/companies/' + companyId + '/financeYear/');
  }

  getModels(exposureId: number) {
    return this.http.get('/exposures/' + exposureId + '/models');
  }

  getFactors(reportDat: string, companyId, exposureId, modelId, isNew) {
    return this.http.get('/ratingModels/' + modelId + '/companyFactor/',
      {params: {'companyId': companyId, 'reportDate': reportDat, 'exposureSid': exposureId, 'isNew': isNew}});
  }

  getWarnings(companyId: number) {
    return this.http.post<any>('/warnings/search', {'page': 0, 'size': 50, 'companyId': [companyId]});
  }

  getRank(companyId: number, year: string, exposureId: number, obj) {
    return this.http.post<any>('/ratingModels/' + year + '/ranking', obj,
    {params: {'companyId': companyId.toString(), 'exposureId': exposureId.toString()}});
  }

  getScale(modelId: number) {
    return this.http.get<any>('/ratingModels/scales');
  }

  getRatingRecordDetail(recordId: number) {
    return this.http.get<CompanyRatingRecordChild>(`/ratings/${recordId}/company/ratingDetail`);
  }

  getRatingReport(ratingRecordId: number) {
    return this.http.get(`/ratings/${ratingRecordId}/attachment`, {observe: 'response', responseType: 'blob'});
  }
}



