import { Component, EventEmitter, Input, Output, OnInit, OnDestroy } from '@angular/core';
import { NgClass } from '@angular/common';
import { Router, ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-custom-select',
  templateUrl: 'custom-select.component.html',
  styleUrls: ['custom-select.component.css']
})
export class CustomSelectComponent implements OnInit, OnDestroy {
  @Input() height: string;
  @Input() list: any[];
  @Input() isSelected: boolean;
  @Input() multip: boolean;
  @Input() title: string;
  @Output() selectItem: EventEmitter<any> = new EventEmitter<any>();

  itemsHidden: boolean;
  initialList: any[];
  initialTitle: string;
  itemselected: any[];
  type: number;

  constructor() {
  }

  ngOnInit() {
    this.initialTitle = this.title;
    this.initialList = this.list;
    this.itemsHidden = true;
    this.itemselected = [];
    this.type = 1;
  }

  ngOnDestroy() {
  }

  select(item) {
    this.itemselected.push(item);
    let type = this.type;
    if (!this.multip) {
      this.title = item.text;
      this.itemsHidden = true;
      this.isSelected = true;
    } else {
      this.type = this.type + 1;
    }
    let data = {
      id: item.id,
      text: item.text,
      type: type
    };
    this.selectItem.emit(data);
  }

  blur() {
    this.itemsHidden = true;
    this.itemselected = [];
    if (this.multip) {
      this.type = 1;
      this.list = this.initialList;
    }
  }

  setSelected() {
    this.itemsHidden = true;
    this.type = 1;
    this.list = this.initialList;
    this.title = null;
    for (let item of this.itemselected) {
      if (!this.title) {
        this.title = item.text;
      } else {
        this.title = this.title + '--' + item.text;
      }
    }
    this.isSelected = true;
    this.itemselected = [];
    console.log('reset type:', this.type);
  }

  reset() {
    this.title = this.initialTitle;
    this.itemselected = [];
    this.isSelected = false;
  }
}



