import { Component, OnInit } from '@angular/core';

import { ToastsManager } from 'ng2-toastr/ng2-toastr';

import { ModelsService } from '../models.service';
import { UserService } from '../../user/user.service';

@Component({
  selector: 'app-scale-config',
  templateUrl: './scale-config.component.html',
  styleUrls: ['./scale-config.component.css']
})
export class ScaleConfigComponent implements OnInit {

  scales;
  allScales;
  scaleTypes;

  disableSave;
  hasEditPrivilege = false;

  constructor(private modelsService: ModelsService, private toastr: ToastsManager,
    private userService: UserService) {

    this.scaleTypes = modelsService.scaleTypes;
    this.hasEditPrivilege = userService.hasPrivilege(userService.modelEdit);
  }

  ngOnInit() {
    this.getAllScales();
  }

  getAllScales() {
    this.modelsService.getScale().subscribe(data => {
      this.allScales = data;
      this.filtedScales();
      this.convertNumber();
    },
      error => console.log('getScale error:', error));
  }

  private filtedScales() {
    this.scales = this.allScales.filter(it => !it.isDelete);
  }

  // 转换科学计数为正常数字
  private convertNumber() {
    if (this.scales && this.scales.length > 0) {
      this.scales.forEach(element => {
        const maxNum = this.convert(element.maxValue);
        element.maxValue = maxNum;
        element.maxValueDis = (maxNum * 100).toFixed(3) + '%';
        const midNum = this.convert(element.midPd);
        element.midPd = midNum;
        element.midPdDis = (midNum * 100).toFixed(3) + '%';
        const minNum = this.convert(element.minValue);
        element.minValue = minNum;
        element.minValueDis = (minNum * 100).toFixed(3) + '%';
      });
    }
  }

  // 转换方法
  private convert(tempValueStr) {
    const numStr = tempValueStr.toString();
    if ((numStr.indexOf('E') !== -1) || (numStr.indexOf('e') !== -1)) {
      const regExp = new RegExp('^(((\\d+.?\\d+)|(\\d+))[Ee]{1}((-(\\d+))|(\\d+)))$', 'ig');
      let result = regExp.exec(numStr);
      let resultValue;
      let power;
      if (result && result.length > 0) {
        resultValue = result[2];
        power = parseInt(result[5], 10);

        if (power > 0) {
          tempValueStr = Number(tempValueStr);
        } else {
          let powerAbs = Math.abs(power);
          resultValue = resultValue.split('.');
          if (resultValue.length === 2) {
            powerAbs += resultValue[1].length;
          }
          tempValueStr = parseFloat(numStr).toFixed(powerAbs);
        }
      }
    }

    return tempValueStr;
  }

  add() {
    this.allScales.push({ isDelete: 0 });
    this.filtedScales();
  }

  remove(item) {
    // this.scales.splice(this.scales.indexOf(item), 1);
    item.isDelete = 1;
    this.filtedScales();
  }

  private valid() {
    let scales = this.allScales;
    let empty = (v) => v == null || v.length === 0;
    let preMax = null;
    for (let i = 0; i < scales.length; i++) {
      if (empty(scales[i].rating) || empty(scales[i].midPd) || empty(scales[i].minValue) || empty(scales[i].maxValue)) {
        this.toastr.error('所有项为必填项,格式必须是数字', '验证失败');
        return false;
      }
      if ((!empty(scales[i].midPd) && /^\d{1,16}(\.\d{1,16})?$/.test(scales[i].midPd) === false) ||
        (!empty(scales[i].minValue) && /^\d{1,16}(\.\d{1,16})?$/.test(scales[i].minValue) === false) ||
        (!empty(scales[i].maxValue) && /^\d{1,16}(\.\d{1,16})?$/.test(scales[i].maxValue) === false)
      ) {
        this.toastr.error('违约概率中位数、违约概率最小值、违约概率最大值只能输入数值，且长度控制在16（小数点前）+16（小数点后）个数值内,只能输入正数；', '验证失败');
        return false;
      }
      if (i !== 0 && scales[i].minValue !== preMax) {
        this.toastr.error('最小值和前一最大值要相等', '验证失败');
        return false;
      }
      if (empty(scales[i].minValue) || empty(scales[i].maxValue) ||
        !(scales[i].minValue <= scales[i].midPd && scales[i].midPd <= scales[i].maxValue)) {
        this.toastr.error('违约概率必须 最小值≤中位数≤最大值', '验证失败');
        return false;
      }
      if (scales[i].minValue < 0 || scales[i].minValue > 1 ||
        scales[i].maxValue < 0 || scales[i].maxValue > 1 ||
        scales[i].midPd < 0 || scales[i].midPd > 1
      ) {
        this.toastr.error('违约概率必须不小于0，并且不大于1', '验证失败');
        return false;
      }
      preMax = scales[i].maxValue;
    }
    return true;
  }

  private convertShowToNumber(numberStr) {
    let precentRegulation = /^([1-9]{1}[0-9]{0,1}|0|100)(\.\d{1,3}){0,1}%$/;
    let doubleRegulation = /^\d{1,16}(\.\d{1,16})?$/;
    if (precentRegulation.test(numberStr)) {
      return (parseFloat(numberStr.substr(0, numberStr.length - 1)) / 100).toFixed(6);
    } else if (doubleRegulation.test(numberStr)) {
      return parseFloat(numberStr).toFixed(6);
    } else {
      return null;
    }
  }

  modelChange(item, type) {
    if (type === 1) {
      item.midPd = this.convertShowToNumber(item.midPdDis);
    } else if (type === 2) {
      item.minValue = this.convertShowToNumber(item.minValueDis);
    } else if (type === 3) {
      item.maxValue = this.convertShowToNumber(item.maxValueDis);
    }
  }

  save() {
    if (!this.valid()) { return; }
    this.disableSave = true;
    // this.allScales.forEach(it => it.type = 'GENERAL_MS');
    this.modelsService.saveScales(this.allScales).subscribe(data => {
      this.toastr.success('保存成功').then(it => this.disableSave = false);
      this.getAllScales();
    },
      error => {
        this.toastr.error('保存失败').then(it => this.disableSave = false);
      }
    );
  }

}



