import { Component, OnInit, Input } from '@angular/core';
import { ModelsService } from '../../models/models.service';

@Component({
  selector: 'app-rating-view',
  template: `<span class="label-rating{{small?'-sm':''}}"
  [ngClass]="{'rating-a': ratingLevel==1,'rating-b':ratingLevel==2,'rating-c':ratingLevel==3,'rating-d': ratingLevel==0}">
  {{rating?rating:'无评级'}}</span>`
})
export class RatingViewComponent implements OnInit {
  ratingLevel: number;
  private _rating: string;

  @Input()
  small = false;

  constructor(private modelService: ModelsService) { }

  ngOnInit() { }

  @Input()
  set rating(r: string) {
    this.ratingLevel = 0;
    if (r && r !== '无评级') {
      if (typeof r !== 'string') { console.error('rating should be a string but it is ' + r); }
      this.ratingLevel = this.modelService.getLevel(r);
    }
    this._rating = r;
  }

  get rating(): string {
    return this._rating;
  }
}



