import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { NgIdleModule } from '@ng-idle/core';

import { rootRouting, appRoutingProviders } from './root.routing';

import { HomeComponent } from './home/home.component';
import { SharedModule } from '../shared/shared.module';
import { DashboardComponent } from './dashboard/dashboard.component';
import { NotFoundComponent } from './notfound/notfound.component';
import { OverviewModule } from '../overview/overview.module';
import { CompanyModule } from '../company/company.module';
import { AdminModule } from '../admin/admin.module';
import { ProductModule } from '../product/product.module';
import { PortfolioModule } from '../portfolio/portfolio.module';
import { AnalysisModule } from '../analysis/analysis.module';
import { ModelsModule } from '../models/models.module';
import { CompanyService } from '../company/company.service';
import { HomeService } from './home/home.service';
import { AdminService } from '../admin/admin.service';
import { TaskModule } from '../task/task.module';
import { LogModule } from '../log/log.module';
import { MasterScaleResolver } from '../root/master-scale-resolver';

@NgModule({
  imports: [
    SharedModule,
    FormsModule,
    CompanyModule,
    ProductModule,
    PortfolioModule,
    OverviewModule,
    ModelsModule,
    AnalysisModule,
    AdminModule,
    NgIdleModule.forRoot(),
    TaskModule,
    LogModule,
    rootRouting
  ],
  declarations: [HomeComponent, DashboardComponent, NotFoundComponent],
  providers: [
    appRoutingProviders, HomeService,
    CompanyService, AdminService,
    MasterScaleResolver
  ],
})
export class RootModule { }



