import {
  Component,
  HostListener,
  OnDestroy,
  OnInit,
  ViewChild
} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { MsgService } from 'app/core/msg.service';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';
import { Subscription } from 'rxjs/Subscription';

import { AnalysisItemCompany } from '../../analysis/analysis';
import { AnalysisService } from '../../analysis/analysis.service';
import { ModelsService } from '../../models/models.service';
import { PortfolioService } from '../../portfolio/portfolio.service';
import { RuleService } from '../../portfolio/rule.service';
import { CompanySearchParams, PagedResult, Portfolio } from '../../shared';
import { RegulationCondition } from '../../shared/entities/portfolio/regulation';
import { UserService } from '../../user/user.service';
import { CompanyService } from '../company.service';
import { CompanySearchConditionShow, CompanyVo } from '../entities/company-vo';

const MAX_ANALYSIS_COUNT = 20; // 最多能勾选分析的数量

@Component({
  selector: 'app-company-search',
  templateUrl: 'company-search.component.html',
  styleUrls: ['company-search.component.css']
})
export class CompanySearchComponent implements OnInit, OnDestroy {
  portfoliolist: any[];
  portolist: boolean;
  followCollapseStatus: boolean;
  conditionCollapseStatus: boolean;
  companySearchParams: CompanySearchParams;
  companySearchConditionShowList: CompanySearchConditionShow[];
  companySearchResult: PagedResult<CompanyVo>;
  companySearchSource: Observable<any>;
  companyFollowList: CompanyVo[];
  industriesList: any[];
  mainMarketList: any[];
  ratingList: any[];
  outerRatingList: any[];
  bondConditionList: any[];
  ratingStatusConditionList: any[];
  periodList = [
    { value: '0M,1M', text: '1个月内', status: false },
    { value: '0M,2M', text: '2个月内', status: false },
    { value: '0M,3M', text: '3个月内', status: false },
    { value: '0M,6M', text: '6个月内', status: false },
    { value: '0M,9M', text: '9个月内', status: false },
    { value: '0M,1Y', text: '1年内', status: false },
  ];
  bondIsSelect: boolean;
  itemsPerPage: number;
  currentPage: number;
  loadingStatus: boolean;
  sub: Subscription;
  companySearchSub: Subscription;
  searchSubject: Subject<any> = new Subject<any>();
  isSearchCompany: boolean;
  analysisCompanyList: CompanyVo[];
  regulationCollapseStatus: boolean;
  regulationName: string;
  regulationDesc: string;
  regulationDesc1: string;
  label: string;
  value: string;
  regulartionConditioin: RegulationCondition;
  inputSearchOrRemove: boolean;
  regulationSave = false;
  isFollow = false;
  numPages;

  constructor(
    private router: Router,
    private activatedRouter: ActivatedRoute,
    private companyService: CompanyService,
    public userService: UserService,
    private analysisService: AnalysisService,
    private portfolioService: PortfolioService,
    private toastr: ToastsManager,
    private ruleService: RuleService,
    private modelService: ModelsService,
    private msg: MsgService
  ) {}

  ngOnInit() {
    this.portfoliolist = [];
    this.portolist = false;
    this.conditionCollapseStatus = true;
    this.regulationCollapseStatus = true;
    this.currentPage = 1;
    this.itemsPerPage = 10;
    this.companyFollowList = [];
    this.companySearchResult = new PagedResult<CompanyVo>();
    this.companySearchResult.total = 0;
    this.companySearchResult.items = [];
    this.followCollapseStatus = true;
    this.regulartionConditioin = new RegulationCondition();
    this.bondIsSelect = false;
    if (window.localStorage.getItem('companySearchParams')) {
      this.companySearchParams = JSON.parse(
        window.localStorage.getItem('companySearchParams')
      );
      this.currentPage = this.companySearchParams.page + 1;
      this.companySearchConditionShowList = JSON.parse(
        window.localStorage.getItem('companySearchConditionShowList')
      );

      this.bondIsSelect = this.companySearchConditionShowList.some(
        item => item.label === '发债情况'
      );

    } else {
      this.companySearchParams = new CompanySearchParams();
      this.companySearchConditionShowList = [];
    }
    this.inputSearchOrRemove = this.companySearchParams.keyword ? false : true;
    this.companySearchParams.size = this.itemsPerPage;
    this.sub = this.activatedRouter.queryParams.subscribe(params => {
      this.searchCompanies();
    });
    this.companySearchSource = Observable.create((observer: any) => {
      // Runs on every search
      // tslint:disable-next-line:no-unused-expression
      this.companySearchParams.keyword && observer.next(this.companySearchParams.keyword);
    }).mergeMap((keyword: string) => this.companyTypeaheadSearch(keyword));

    // 获取行业
    this.industriesList = [];
    this.companyService.getExposures().subscribe(data => {
      for (let item of data) {
        this.industriesList.push({
          id: item.id,
          text: item.exposure,
          status: false
        });
      }
      let industryIndexList = ['融资平台', '房地产', '建筑', '证券', '原材料'];
      this.industriesList = this.industriesList.sort((item1, item2) => {
        let index1 = industryIndexList.indexOf(item1.text);
        let index2 = industryIndexList.indexOf(item2.text);
        index1 = index1 >= 0 ? index1 : 10;
        index2 = index2 >= 0 ? index2 : 10;
        return index1 > index2 ? 1 : -1;
      });
      this.industriesList.push({ id: 0, text: '其它', status: false });
    });

    this.mainMarketList = [
      { id: 5659, text: '上交所主板', status: false },
      { id: 5670, text: '深交所主板', status: false },
      { id: 5671, text: '深交所中小板', status: false },
      { id: 5723, text: '上交所风险警示板', status: false },
      { id: 5724, text: '深交所风险警示板', status: false },
      { id: 0, text: '其他', status: false }
    ];

    this.ratingList = this.modelService.allScales.map(it => ({
      text: it.rating,
      status: false
    }));
    this.ratingList.push({ text: '无', status: false });

    this.outerRatingList = [
      { text: 'AAA', status: false },
      { text: 'AA+', status: false },
      { text: 'AA', status: false },
      { text: 'AA-', status: false },
      { text: 'A+', status: false },
      { text: 'A', status: false },
      { text: 'A-', status: false },
      { text: 'BBB+', status: false },
      { text: 'BBB', status: false },
      { text: 'BBB-', status: false },
      { text: 'BB+', status: false },
      { text: 'BB', status: false },
      { text: 'BB-', status: false },
      { text: 'B+', status: false },
      { text: 'B', status: false },
      { text: 'B-', status: false },
      { text: 'CCC', status: false },
      { text: 'CC', status: false },
      { text: 'C', status: false }
    ];
    this.bondConditionList = [
      { id: 0, code: 'no', text: '未发债', status: false },
      { id: 1, code: 'yes', text: '已发债', status: false }
    ];
    this.ratingStatusConditionList = [
      // { id: '参考评级', text: '参考评级', status: false },
      { id: '基础评级', text: '基础评级', status: false },
      { id: '认定评级', text: '认定评级', status: false }
    ];

    // 获取关注列表
    if (window.localStorage.getItem('companyFollowList')) {
      this.companyFollowList = JSON.parse(
        window.localStorage.getItem('companyFollowList')
      );
    }
    this.isSearchCompany = false;
    this.analysisCompanyList = [];
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
    this.companySearchSub.unsubscribe();
  }

  companyTypeaheadSearch(keyword: string): Observable<any> {
    if (this.isSearchCompany || keyword.trim().length < 2) {
      // tslint:disable-next-line:no-unused-expression
      this.isSearchCompany && (this.isSearchCompany = false);
      return Observable.create((observer: any) => {
        observer.next([]);
      });
    }
    return this.companyService.getCompanyListForTypeaHead(keyword.trim());
  }

  clearFollow() {
    this.portolist = false;
    this.companyFollowList.map(data => (data.followStatus = false));
    this.companyFollowList = [];
    this.analysisCompanyList = [];
    this.companySearchResult.items.map(data => (data.followStatus = false));
    window.localStorage.removeItem('companyFollowList');
  }

  goToDetail(companyId: number) {
    this.router.navigate(['company/detail', companyId]);
  }

  disfollow(item) {
    this.portolist = false;
    item.followStatus = false;
    this.companyFollowList = this.companyFollowList.filter(
      data => data.id !== item.id
    );
    this.analysisCompanyList = this.analysisCompanyList.filter(
      data => data.id !== item.id
    );
    this.companySearchResult.items.map(data => {
      if (data.id === item.id) {
        data.followStatus = false;
      }
    });
    window.localStorage.setItem(
      'companyFollowList',
      JSON.stringify(this.companyFollowList)
    );
  }

  follow(item) {
    if (item.followStatus) {
      this.disfollow(item);
      return;
    }
    item.followStatus = true;
    this.companyFollowList.push(item);
    window.localStorage.setItem(
      'companyFollowList',
      JSON.stringify(this.companyFollowList)
    );
  }

  batchFollow(follow) {
    if (follow) {
      if (this.companySearchResult.items.some(item => !item.followStatus)) {
        for (let item of this.companySearchResult.items) {
          if (!item.followStatus) {
            item.followStatus = true;
            this.companyFollowList.push(item);
          }
        }
        window.localStorage.setItem(
          'companyFollowList',
          JSON.stringify(this.companyFollowList)
        );
      }
    } else {
      if (this.companySearchResult.items.some(item => item.followStatus)) {
        for (let item of this.companySearchResult.items) {
          if (item.followStatus) {
            item.followStatus = false;
            this.disfollow(item);
          }
        }
      }
    }
  }

  goToPortfolio(item = null) {
    if (item) {
      this.companyService.portfolio = item;
    }
    this.companyService.isFromList = true;
    let companyIdList: number[] = [];
    for (let f of this.companyFollowList) {
      companyIdList.push(f.id);
    }
    this.companyService.followCompanys = companyIdList;
    this.router.navigate(['portfolio']);
  }

  highLight(item: string) {
    if (
      this.companySearchParams.keyword &&
      this.companySearchParams.keyword.trim().length > 0
    ) {
      let startIdx = item.indexOf(this.companySearchParams.keyword);
      let len = this.companySearchParams.keyword.trim().length;
      if (startIdx >= 0) {
        item =
          item.substring(0, startIdx) +
          '<strong class="match">' +
          item.substring(startIdx, startIdx + len) +
          '</strong>' +
          item.substring(startIdx + len);
      }
    }
    return item;
  }

  goToAnalysis() {
    let companyIdList: AnalysisItemCompany[] = [];
    this.analysisCompanyList.forEach(company => {
      let item = new AnalysisItemCompany();
      item.companyId = company.id;
      item.companyName = company.name;
      item.innerRating = company.innerRating;
      item.exposureId = company.exposure && company.exposure.id;
      companyIdList.push(item);
    });
    this.analysisService.AnalysisResultAnnouncement = new BehaviorSubject<any>(
      companyIdList
    );
    this.router.navigate(['company/analysis']);
  }

  searchCompaniesAndSetStatus() {
    if (this.inputSearchOrRemove && this.companySearchParams.keyword) {
      this.inputSearchOrRemove = false;
    } else if (!this.inputSearchOrRemove) {
      this.inputSearchOrRemove = true;
      this.companySearchParams.keyword = '';
    }
    this.searchCompanies();
  }

  searchCompanies(page: number = 0) {
    this.isSearchCompany = true;
    window.scrollTo(0, 0);
    if (page === 0) {
      this.currentPage = 1;
    }
    if (this.companySearchParams.keyword == null) {
      this.companySearchParams.keyword = '';
    }
    this.companySearchParams.page = page;
    this.loadingStatus = true;
    this.companySearchParams.keyword = this.companySearchParams.keyword
      ? this.companySearchParams.keyword.trim()
      : null;
    // tslint:disable-next-line:no-unused-expression
    this.companySearchSub && this.companySearchSub.unsubscribe();
    window.localStorage.setItem(
      'companySearchConditionShowList',
      JSON.stringify(this.companySearchConditionShowList)
    );
    this.companySearchSub = this.companyService
      .searchCompanies(this.companySearchParams)
      .subscribe(
        data => {
          this.generateCompanySearchResult(data);
        },
        error => console.log('searchCompanies error:', error),
        () => {
          this.loadingStatus = false;
        }
      );
  }

  generateCompanySearchResult(companySearchResult: PagedResult<CompanyVo>) {
    this.companySearchResult = new PagedResult<CompanyVo>();
    this.companySearchResult.total = companySearchResult.total;
    let compyBasicinfos: CompanyVo[] = [];
    if (companySearchResult.items && companySearchResult.items.length > 0) {
      for (let item of companySearchResult.items) {
        // 确保当A股与H股都有时，A股顺序在前
        if (
          item.securityList &&
          item.securityList[0].securityType !== 'A股' &&
          item.securityList.length > 1
        ) {
          let transItem: any;
          transItem = item.securityList[0];
          item.securityList[0] = item.securityList[1];
          item.securityList[1] = transItem;
        }
        let compyBasicinfoItem = new CompanyVo();
        compyBasicinfoItem.generateByRaw(item);
        if (
          this.companyFollowList.length > 0 &&
          this.companyFollowList.some(followItem => followItem.id === item.id)
        ) {
          compyBasicinfoItem.followStatus = true;
        }
        compyBasicinfos.push(compyBasicinfoItem);
      }
      if (compyBasicinfos.every(it => !it.followStatus)) this.isFollow = false;
      if (compyBasicinfos.every(it => it.followStatus)) this.isFollow = true;
      this.companySearchResult.items = compyBasicinfos;
    }
  }

  pageChanged($event) {
    this.searchCompanies($event.page - 1);
  }

  searchChange() {
    this.isSearchCompany = false;
    this.inputSearchOrRemove = true;
    this.searchSubject.next(this.companySearchParams.keyword);
  }

  @HostListener('document:keypress', ['$event'])
  searchKeyPress($event) {
    if ($event.keyCode === 13) {
      this.searchCompaniesAndSetStatus();
    }
  }

  selRating(items) {
    this.companySearchParams.rating = items.map(it => it.text);
    if (items.length > 0) {
      let companySearchConditionShow = new CompanySearchConditionShow();
      companySearchConditionShow.label = '内部评级';
      companySearchConditionShow.items = items.map(it => it.text);
      this.companySearchConditionShowList.push(companySearchConditionShow);
    }
    this.searchCompanies();
  }
  selOuterRating(items) {
    this.companySearchParams.creditRating = items.map(it => it.text);
    if (items.length > 0) {
      let companySearchConditionShow = new CompanySearchConditionShow();
      companySearchConditionShow.label = '外部评级';
      companySearchConditionShow.items = items.map(it => it.text);
      this.companySearchConditionShowList.push(companySearchConditionShow);
    }
    this.searchCompanies();
  }

  selIndustry(items) {
    this.companySearchParams.bussion = items.map(it => it.id);
    if (items.length > 0) {
      let companySearchConditionShow = new CompanySearchConditionShow();
      companySearchConditionShow.label = '所属敞口';
      companySearchConditionShow.items = items.map(it => it.text);
      this.companySearchConditionShowList.push(companySearchConditionShow);
    }
    this.searchCompanies();
  }

  selMarket(items) {
    this.companySearchParams.marketNames = items.map(it => it.text);
    if (items.length > 0) {
      let companySearchConditionShow = new CompanySearchConditionShow();
      companySearchConditionShow.label = '上市情况';
      companySearchConditionShow.items = items.map(it => it.text);
      this.companySearchConditionShowList.push(companySearchConditionShow);
    }
    this.searchCompanies();
  }

  selPeriod(items) {
    this.companySearchParams.period = items.map(it => it.value);
    if (items.length > 0) {
      let companySearchConditionShow = new CompanySearchConditionShow();
      companySearchConditionShow.label = '专家点评';
      companySearchConditionShow.items = items.map(it => it.text);
      this.companySearchConditionShowList.push(companySearchConditionShow);
    }
    this.searchCompanies();
  }

  removeSearchConditon(label: string) {
    this.companySearchConditionShowList = this.companySearchConditionShowList.filter(
      item => item.label !== label
    );
    switch (label) {
      case '上市情况':
        this.companySearchParams.marketNames = [];
        break;
      case '所属敞口':
        this.companySearchParams.bussion = [];
        break;
      case '内部评级':
        this.companySearchParams.rating = [];
        break;
      case '外部评级':
        this.companySearchParams.creditRating = [];
        break;
      case '发债情况':
        this.companySearchParams.issueBonds = null;
        this.bondIsSelect = false;
        this.bondConditionList.map(item => (item.status = false));
        break;
      case '评级类型':
        this.companySearchParams.ratingType = [];
        break;
      case '专家点评':
        this.companySearchParams.period = [];
        break;
    }
    this.addDesc();
    if (this.companySearchConditionShowList.length < 1) {
      this.regulationCollapseStatus = true;
    }
    this.searchCompanies();
  }

  selBondCondition(items) {
    if (items.length > 0) {
      this.bondIsSelect = true;
      this.companySearchParams.issueBonds = items[0].id;

      let companySearchConditionShow = new CompanySearchConditionShow();
      companySearchConditionShow.label = '发债情况';
      companySearchConditionShow.items = items.map(it => it.text);
      this.companySearchConditionShowList.push(companySearchConditionShow);
      this.searchCompanies();
    }
  }

  selRatingStatusCondition(items) {
    if (items.length > 0) {
      this.companySearchParams.ratingType = items.map(it => it.id);
      let companySearchConditionShow = new CompanySearchConditionShow();
      companySearchConditionShow.label = '评级类型';
      companySearchConditionShow.items = items.map(it => it.text);
      this.companySearchConditionShowList.push(companySearchConditionShow);
      this.searchCompanies();
    }
  }

  updateAnalysisCompanyList(item, el) {
    if (this.analysisCompanyList.some(company => company.id === item.id)) {
      el.checked = false;
      this.analysisCompanyList = this.analysisCompanyList.filter(
        company => company.id !== item.id
      );
    } else {
      if (this.analysisCompanyList.length >= MAX_ANALYSIS_COUNT) {
        this.toastr.error('分析最多支持20家企业');
        el.checked = false;
        return;
      }
      el.checked = true;
      this.analysisCompanyList.push(item);
    }
  }

  saveNewRegulation(name, desc, searchParams) {
    this.regulationSave = true;
    this.regulartionConditioin = new RegulationCondition();
    this.regulartionConditioin.filterType = '企业';
    this.regulartionConditioin.name = name;
    this.regulartionConditioin.desc = desc;
    this.regulartionConditioin.rating = searchParams.rating;
    this.regulartionConditioin.creditRating = searchParams.creditRating;
    this.regulartionConditioin.ratingType = searchParams.ratingType;
    this.regulartionConditioin.exposureId = searchParams.bussion;
    this.regulartionConditioin.marketNames = searchParams.marketNames;
    this.regulartionConditioin.issueBonds = searchParams.issueBonds;
    this.ruleService
      .addRegulationByCondition(this.regulartionConditioin)
      .subscribe(
        data => {
          this.toastr.success('生成规则成功');
          this.regulationSave = false;
          //    this.getUserRoles();
        },
        error => {
          console.log('error', error);
          this.regulationSave = false;
          if (error.indexOf('规则名称已存在') >= 0) {
            this.msg.alert('该规则名称已存在');
          }
        }
      );
  }
  addDesc() {
    let tempTitle: string;
    this.regulationDesc = '';
    for (let item of this.companySearchConditionShowList) {
      tempTitle = '';
      tempTitle += item.label + ':';
      let tempContent: string;
      tempContent = '';
      for (let innerItem of item.items) {
        tempContent += innerItem + ',';
      }
      if (tempContent.length > 0) {
        tempContent = tempContent.substring(0, tempContent.length - 1);
      }

      this.regulationDesc += tempTitle + tempContent + ';';
    }
  }
  getPortoList() {
    this.portolist = !this.portolist;
    this.portfolioService
      .getPortfoliolist(0)
      .subscribe(
        data => this.generatePortfoliolist(data),
        error => console.log('getInnerRatingList error:', error)
      );
  }
  generatePortfoliolist(data) {
    this.portfoliolist = [];
    if (!data || data.length < 1) {
      return;
    }
    for (let item of data) {
      let portfolio = new Portfolio();
      portfolio.generateByRaw(item);
      this.portfoliolist.push(portfolio);
    }
  }

  clearCondition() {
    this.companySearchConditionShowList = [];
    const keyword = this.companySearchParams.keyword;
    this.regulationName = ''; // 名称
    this.regulationDesc = ''; // 描述
    this.regulationCollapseStatus = true;

    this.companySearchParams = new CompanySearchParams();
    this.companySearchParams.size = this.itemsPerPage;
    this.companySearchParams.keyword = keyword;
    this.searchCompanies();
  }
}



