import { BaseClass } from '../../shared/base-class';

export class Exposures extends BaseClass {
  exposureId: number;
  format: string;
  financeFactors: ExposureFactorsItems[];
  operationFactors: ExposureFactorsItems[];
  regionFactors: ExposureFactorsItems[];
}
export class ExposureFactorsItems extends BaseClass {
  factorCode: string;
  factorName: string;
  factorType: string;
  description: string;
  formulaCh: string;
  propertyCode: number;
  categoryCode: number;
  options: OptionVosItems[];
  elementVos: ElementVosItems[];
  applicationCode: number;
  typeValue: number;
  isDefault: boolean;
}
export class OptionVosItems extends BaseClass {
  exposureSid: number;
  factorCode: string;
  optionNumber: number;
  formulaEn: string;
  formulaCh: string;
}
export class ElementVosItems extends BaseClass {
  code: string;
  name: string;
  description: string;
  dataType: number;
  dataTypeTitle: string;
  type: string;
}



