import { BaseClass } from '../base-class';
import { PortfolioItem } from './portfolio-item';

export class Portfolio extends BaseClass {
  id: number; // 组合id
  name: string; // 组合名称
  type: string; // 组合类型
  owner: string; // 组合创建者
  remark: string; // 组合备注
  isDefault: number; // 是否是默认组合
  isDynamic: number; // 是否禁用规则 0：是，1：否
  obsoleteDate: string;
  updateDate: string;
  createDate: string;
  portfolioItems: any[];
  addedItemIds: any[];
  removedItemIds: any[];
  companies: PortfolioItem[];
  regulationId: number;
  regulationName: string;
  regulationDesc: string;
  get text() {
    return this.name;
  }
  set text(value: any) {
    this.name = value;
  }
}





