import { BaseClass } from '../base-class';
export class Warning extends BaseClass {
  id: number;
  subjectId: number;
  subjectName: string;
  noticeDate: string;
  typeId: number;
  warningTitle: string;
  warningContent: string;
  exposure: string;
  regionCode: number;
  regionName: string;
  typeName: string;
  severity: number;
  importance; // TODO: is it same with severity?
  severityAdjusted: number;
  subjectType: number;
}



