import { Component, OnInit, OnDestroy, ViewChild, ElementRef, TemplateRef, ViewContainerRef } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';


import { HomeService } from '../../../root/home/home.service';
import { BondService } from '../bond.service';
import { BondIssuer, BondCreditPledge, BondCreditWarrantor, BondCredit, BondCreditAdjustSaveParam } from '../bond';
import { Bond } from '../../../shared';
import { MsgService } from 'app/core/msg.service';
@Component({
  templateUrl: 'bond-rating.component.html',
  styleUrls: ['bond-rating.component.css']
})

export class BondRatingComponent implements OnInit, OnDestroy {
  @ViewChild('scrollWrapper') scrollWrapper: ElementRef;

  bondId: number;
  bondDetail: Bond;
  issuerList: BondIssuer[];
  bondPledgeList: BondCreditPledge[];
  bondWarrantorList: BondCreditWarrantor[];
  bondCreditResult: BondCredit;
  sub: Subscription;
  announcement = new BehaviorSubject(null);
  bondCreditShowIsReady = {
    bondWarrantorIsReady: false,
    bondPledageIsReady: false,
    bondCreditResultIsReady: false
  };
  bondCreditScaleList: any[];
  adjustReason: string;
  commitNewRateDisabled: boolean;
  readyForCredit: boolean;
  loading: boolean;
  creditSuccess: boolean;
  commitNewRateParam: BondCreditAdjustSaveParam;

  constructor(private router: Router, private activateRoute: ActivatedRoute, private bondService: BondService,
    private homeService: HomeService, private msg: MsgService) {
  }
  ngOnInit() {
    this.activateRoute.params.forEach(
      (params: Params) => {
        this.bondId = +params['bondId'];
        if (!this.bondId) {
          return;
        }
        this.getBondDetail();
        this.getBondPledges();
        this.getBondWarrantors();
        this.getBondIssuers();
        this.getBondCredit();
        this.loading = true;
      }
    );
    this.getBondCreditScale();
    this.bondCreditScaleList = [];
    this.bondDetail = new Bond();
    this.issuerList = [];
    this.bondWarrantorList = [];
    this.bondPledgeList = [];
    this.readyForCredit = true;
    this.commitNewRateDisabled = true;
    this.creditSuccess = false;
    this.bondCreditResult = new BondCredit();
    this.commitNewRateParam = new BondCreditAdjustSaveParam();
    this.sub = this.announcement.subscribe(
      () => (this.bondCreditShowIsReady.bondCreditResultIsReady) && (this.bondCreditShowIsReady.bondPledageIsReady
      ) && (this.bondCreditShowIsReady.bondWarrantorIsReady) && (this.showCreditResult())
    );
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

  private adjustReasonChange(textareaElement) {
    this.commitNewRateParam.adjustRatingReason = this.commitNewRateParam.adjustRatingReason.trim();
    textareaElement.style.height = '30px';
    let scrollHeight = textareaElement.scrollHeight;
    if (scrollHeight < 30) {
      textareaElement.style.height = '30px';
    }else if (scrollHeight < 150) {
      textareaElement.style.height = scrollHeight + 1 + 'px';
    }else {
      textareaElement.style.height = '150px';
    }
  }

  private commitNewRate() {
    this.msg.confirm('确定提交吗?').result
      .then(result => {
        this.loading = true;
        this.bondService.adjustBondCredit(this.commitNewRateParam).subscribe(
          data => {
            this.loading = false;
            this.creditSuccess = true;
          },
          error => {
            this.loading = false;
            this.msg.alert('债券评级调整失败，请重试！');
          }
        );
      });
  }

  private generateBondCredit(data) {
    this.bondCreditResult = new BondCredit();
    if (data) {
      this.bondCreditResult.generateByRaw(data);
      this.commitNewRateParam.uuid = this.bondCreditResult.uuid;
      this.commitNewRateParam.secInnerId = this.bondCreditResult.secInnerId;
      this.commitNewRateParam.modelId = 1;
      this.commitNewRateParam.rawLgdScore = this.bondCreditResult.rawLGDScore;
      this.commitNewRateParam.rawLgdGrade = this.bondCreditResult.rawLGDGrade;
      this.commitNewRateParam.rawRating = this.bondCreditResult.rawRating;
      this.commitNewRateParam.adjustRating = this.bondCreditResult.rawRating;
    }
    this.bondCreditShowIsReady.bondCreditResultIsReady = true;
    this.announcement.next(true);
  }


  private generateBondCreditScale(data) {
    this.bondCreditScaleList = [];
    if (data) {
      for (let item of data) {
        this.bondCreditScaleList.push({ id: item.id, value: item.rating });
      }
    }
  }

  private generateBondDetail(data) {
    this.bondDetail = new Bond();
    if (data) this.bondDetail.generateByRaw(data);
    if (this.readyForCredit && (!this.bondDetail.securityType ||
    (this.bondService.notAllowRatingBondTypeList.indexOf(this.bondDetail.securityType) >= 0))) {
      this.msg.alert('该债券不属于可评级的债券类型', 2000);
      this.readyForCredit = false;
    }
  }

  generateBondPledges(data) {
    this.bondPledgeList = [];
    if (data) {
      for (let item of data) {
        let bondPledge = new BondCreditPledge();
        bondPledge.generateByRaw(item);
        this.bondPledgeList.push(bondPledge);
      }
    }
    this.bondCreditShowIsReady.bondPledageIsReady = true;
    this.announcement.next(true);
  }

  generateBondWarrantors(data) {
    this.bondWarrantorList = [];
    if (data) {
      for (let item of data) {
        let bondWarrantor = new BondCreditWarrantor();
        bondWarrantor.generateByRaw(item);
        this.bondWarrantorList.push(bondWarrantor);
      }
    }
    this.bondCreditShowIsReady.bondWarrantorIsReady = true;
    this.announcement.next(true);
  }

  generateIssuer(data) {
    this.issuerList = [];
    if (!data || data.length < 1) return;
    for (let item of data) {
      let bondIssuer = new BondIssuer();
      bondIssuer.generateByRaw(item);
      this.issuerList.push(bondIssuer);
    }
    if (this.readyForCredit && this.issuerList.every(item => !item.company.innerRating)) {
      this.msg.alert('所有发行人都未评级，无法计算债券评级，请先对发行人进行评级', 2000);
      this.readyForCredit = false;
    }
  }

  private getBondCredit() {
    this.bondService.getBondCredit(this.bondId).subscribe(
      data => this.generateBondCredit(data),
      error => { this.loading = false; console.log('getBondCredit error:', error); }
    );
  }

  private getBondCreditScale() {
    this.bondService.getBondCreditScale().subscribe(
      data => this.generateBondCreditScale(data),
      error => console.log('getBondCredit error:', error)
    );
  }

  private getBondDetail() {
    this.bondService.getBondDetail(this.bondId).subscribe(
      data => this.generateBondDetail(data),
      error => console.log('getBondDetail error:', error)
    );
  }

  private getBondIssuers() {
    this.bondService.getIssuer(this.bondId).subscribe(
      data => this.generateIssuer(data),
      error => console.log('error', error)
    );
  }

  private getBondPledges() {
    this.bondService.getBondPledgeDetail(this.bondId).subscribe(
      data => this.generateBondPledges(data),
      error => console.log('error', error)
    );
  }

  private getBondWarrantors() {
    this.bondService.getBondWarrantorDetail(this.bondId).subscribe(
      data => this.generateBondWarrantors(data),
      error => console.log('error', error)
    );
  }

  private showCreditResult() {
    this.loading = false;
    if (this.bondCreditResult.pledgeResult && this.bondCreditResult.pledgeResult.itemResults) {
      for (let item of this.bondCreditResult.pledgeResult.itemResults) {
        this.bondPledgeList.map(bondPledge => (bondPledge.bondPledgeSid === item.sid) && (bondPledge.valid_value = item.mitigation_value));
      }
    }
    if (this.bondCreditResult.warrantorResult && this.bondCreditResult.warrantorResult.itemResults) {
      for (let item of this.bondCreditResult.warrantorResult.itemResults) {
        this.bondWarrantorList.map(warrantor => (warrantor.bondWarrantorSid === item.sid)
        && (warrantor.valid_value === item.mitigation_value));
      }
    }
  }

  public scrollTo(elementRef: string) {
    this.homeService.scrollInside(elementRef, this.scrollWrapper);
  }

  // 超链接
  goToBondDetail(secinnerId: number) {
    this.router.navigate(['product/bond/detail', secinnerId]);
  }


}



