import { BaseRating } from './base-rating';
export class InnerRating extends BaseRating {
  exposure: string;
  modelName: string;
  totalScore: number;
  scaledRating: string;
  scaledPd: string;
  factorDate: string;
  workflowId: number;
  id: number;
  ratingType: string;
  isCollapse: boolean;
  identifiedType: string;
  ratingStatus: string;
  basicRating: string;
  leftDays?: number;
}

export class OuterRating extends BaseRating {
  creditOrgName: string;
  currentRating: string;
  previousRating: string;
  creditRating: string;
  currentRatingDate: string;
  prevRatingDate: string;
  dataSrc: string;
  dataSrcContent: string;
  creditType: string;
  creditForward: string;
}



