import { Component, OnInit, AfterViewInit, ElementRef, Input } from '@angular/core';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';

declare let math: any;
declare let $: any;

@Component({
  selector: 'app-formula-editor',
  templateUrl: './formula-editor.component.html',
  styleUrls: ['./formula-editor.component.css']
})
export class FormulaEditorComponent implements OnInit, AfterViewInit {

  @Input()
  vars;

  nodeString: string;

  @Input()
  enFormula: string;

  cnFormula: string;

  nodes: any[];

  constructor(private elementRef: ElementRef, private toastr: ToastsManager) {
  }

  ngOnInit() {

  }

  ngAfterViewInit() {
    this.parseFormula();
  }

  private parseFormula() {
    try {
      let node = math.parse(this.enFormula);

      console.log(this.vars);
      this.nodeString = node.toString({
        handler: (n, options) => {
          if (n.isSymbolNode) {
            let v = this.vars[n.name];
            if (!v) { return n.name; }
            let sufix = '';
            if (v.subjectLastYear === n.name) {
              sufix = '上期';
            } else if (v.subjectBlastYear === n.name) {
              sufix = '上上期';
            }
            return `&nbsp;<span class="var" contenteditable="false" data-key="${n.name}">` +
            `${this.trim(v.subjectNameCh)}<span class="sufix">${sufix}</span></span>&nbsp;`;
          } else if (n.isFunctionNode) {
            let args = n.args.map(arg => arg.toString(options));
            return n.fn.toString() + '(' + args.join(', ') + ')';
          }
        }
      });
      this.elementRef.nativeElement.querySelector('div.formula').innerHTML = this.nodeString;
      this.elementRef.nativeElement.onpaste = () => setTimeout(() => this.changeEditable(), 0);
      this.attachEvent();
    } catch (error) {
      console.log(error);
    }
  }

  private trim(str: string) {
    return str.substr(str.indexOf('、') + 1);
  }

  show() {
    let oDiv = this.elementRef.nativeElement.querySelector('div.formula');
    let div = oDiv.cloneNode(true);
    let children = div.children;
    for (let i = 0; i < children.length;) {
      let text: any;
      if (children[i].hasAttribute('data-key')) {
        text = document.createTextNode(children[i].attributes['data-key'].value);
      } else {
        text = document.createTextNode(children[i].innerText);
      }
      div.replaceChild(text, children[i]);
    }
    this.enFormula = div.innerText.replace(/\u00A0/g, ''); // replace the no-break white space
    this.cnFormula = oDiv.innerText.replace(/\u00A0/g, '');
    this.changeEditable();
  }

  insert(it) {
    console.log(it);
    if (it.isLabel) { return; }
    let supported = document.execCommand('insertHTML', false,
      `&nbsp;<span class="var" contenteditable="false"
      data-key="${it.subjectNameEn}">${this.trim(it.subjectNameCh)}<span class="sufix"></span></span>&nbsp;`);
    console.log(supported);
    if (!supported) {
      this.pasteHtmlAtCaret(`&nbsp;<span class="var" contenteditable="false"
        data-key="${it.subjectNameEn}">${this.trim(it.subjectNameCh)}<span class="sufix"></span></span>&nbsp;`);
    }
    setTimeout(() => { this.changeEditable(); this.attachEvent(); }, 0);
  }

  private pasteHtmlAtCaret(html) {
    let sel, range;
    if (window.getSelection) {
      // IE9 and non-IE
      sel = window.getSelection();
      if (sel.getRangeAt && sel.rangeCount) {
        range = sel.getRangeAt(0);
        let parent = range.startContainer.parentNode;
        let formulaDiv = this.elementRef.nativeElement.querySelector('div.formula');
        while (parent !== formulaDiv) {
          console.log(parent);
          parent = parent.parentNode;
          if (!parent || parent === document.rootElement) {
            return;
          }
        }
        range.deleteContents();

        // Range.createContextualFragment() would be useful here but is
        // only relatively recently standardized and is not supported in
        // some browsers (IE9, for one)
        let el = document.createElement('div');
        el.innerHTML = html;
        let frag = document.createDocumentFragment(), node, lastNode;
        while ((node = el.firstChild)) {
          lastNode = frag.appendChild(node);
        }
        range.insertNode(frag);

        // Preserve the selection
        if (lastNode) {
          range = range.cloneRange();
          range.setStartAfter(lastNode);
          range.collapse(true);
          sel.removeAllRanges();
          sel.addRange(range);
        }
      }
    }
  }

  private changeEditable() {
    let formulaDiv = this.elementRef.nativeElement.querySelector('div.formula');
    for (let it of formulaDiv.querySelectorAll('.var')) {
      it.contentEditable = 'false';
    }
  }
  private attachEvent() {
    let dropdown = $(this.elementRef.nativeElement).find('div.dropdown');
    $('body').click(function () { dropdown.css('display', 'none'); });
    $(this.elementRef.nativeElement).keydown(function () { dropdown.css('display', 'none'); });
    let f = (p, varSpan) => {
      let key = varSpan.attr('data-key');
      let v = this.vars[key];
      if (!v[p]) {
        this.toastr.info('该科目无法调整');
      } else {
        varSpan.attr('data-key', v[p]);
        if (p === 'subjectLastYear') {
          varSpan.find('.sufix').text('上期');
        } else if (p === 'subjectBlastYear') {
          varSpan.find('.sufix').text('上上期');
        } else {
          varSpan.find('.sufix').text('');
        }
      }
      dropdown.css('display', 'none');
    };

    $(this.elementRef.nativeElement).find('span.var').off('click').click(function (event) {
      let varSpan = $(this);
      let offset = $(this).offset();
      dropdown.offset({ top: offset.top + $(this).height(), left: offset.left + $(this).width() - dropdown.width() });
      dropdown.css('display', 'block');
      dropdown.find('#current').off('click').click(function () { f('subjectNameEn', varSpan); });
      dropdown.find('#last').off('click').click(function () { f('subjectLastYear', varSpan); });
      dropdown.find('#before').off('click').click(function () { f('subjectBlastYear', varSpan); });
      event.stopPropagation();
    });
  }
}



