import { Component, OnInit, Input, ChangeDetectionStrategy } from '@angular/core';
import { Warning } from 'app/shared';
import { Router } from '@angular/router/';
import { trigger, state, transition, animate, style } from '@angular/animations/';

@Component({
  selector: 'app-warning-popup',
  templateUrl: './warning-popup.component.html',
  styleUrls: ['./warning-popup.component.css'],
  animations: [
    trigger('popupState', [
      state('out', style({ opacity: 0, display: 'none', transform: 'translateX(100%)' })),
      state('in', style({ opacity: 1, transform: 'translateX(0)' })),
      transition('out => in', [
        animate('200ms ease-in')
      ]),
      transition('in => out', [
        animate('200ms ease-out')
      ])
    ])
  ],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class WarningPopupComponent implements OnInit {

  @Input() warning: Warning;
  @Input() popupState: 'in'|'out';

  constructor(private router: Router) { }

  ngOnInit() {
  }

  gotoCompany(companyId: number) {
    this.router.navigate(['company/detail', companyId]);
  }

  gotoBond(bondId: number) {
    this.router.navigate(['product/bond/detail', bondId]);
  }

}



