import { Component, OnInit, HostListener, Inject, ViewChild, ElementRef } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { PageScrollService, PageScrollInstance } from 'ng2-page-scroll';
import { DOCUMENT } from '@angular/platform-browser';

import { CompanyService } from '../../company/company.service';
import { FeedService } from '../../core/feedAPI/feedAPI.service';
import { User } from '../../user/user';
import { UserService } from '../../user/user.service';
import { HomeService } from './home.service';
import { CompanySearchParams } from '../../shared';
import { Idle, DEFAULT_INTERRUPTSOURCES } from '@ng-idle/core';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';

@Component({
  selector: 'app-home',
  templateUrl: 'home.component.html',
  styleUrls: ['home.component.css']
})

export class HomeComponent implements OnInit {
  companySearchKeyword: string;
  companySearchSource: Observable<any>;
  searchboxHidden: boolean;
  sidebarFolded = false;
  topBarUserOptFolded = false;
  searchTypes = ['企业', '债券'];

  testlist = [];
  @ViewChild('main') mainwrapper: ElementRef;
  // private curUser:User;

  constructor(private router: Router, private companyService: CompanyService, private feedService: FeedService,
    public userService: UserService,
    @Inject(DOCUMENT) private document: any,
    private homeService: HomeService, private idle: Idle, private toastr: ToastsManager) {

    if ((idle as any).handlerRegisted !== true) {
      idle.setIdle(1800);
      idle.setTimeout(3);
      idle.setInterrupts(DEFAULT_INTERRUPTSOURCES);
      idle.onIdleStart.subscribe(data => this.toastr.info('因长时间无操作系统将自动登出'));
      idle.onTimeout.subscribe(data => this.feedService.logout());
      (idle as any).handlerRegisted = true;
    }
    idle.watch();
  }

  ngOnInit() {
    // this.curUser
    this.sidebarFolded = true;
    this.topBarUserOptFolded = true;
    /*this.companySearchSource = Observable.create((observer:any) => {
      // Runs on every search
      observer.next(this.companySearchKeyword);
    }).mergeMap((keyword: string) => this.companySearch(keyword));
    this.companyService.searchAnnouncement.subscribe(
      value => { this.searchboxHidden=value as boolean;this.companySearchKeyword=null; }
    );*/
    this.searchboxHidden = false;

    this.homeService.document = document;
    this.homeService.mainwrapper = this.mainwrapper;
  }

  private companySearchSelect($event) {
    let keyword;
    if ($event.item.name) {
      keyword = $event.item.name;
    } else keyword = $event.item.value;
    this.goToCompanySearch(keyword);
  }


  /*private companySearch(keyword:string):Observable<any>{
      return this.companyService.getCompanyListForTypeaHead(keyword);
  }*/

  private goToCompanySearch(keyword: string) {
    if (!keyword) {
      keyword = '';
    }
    this.router.navigate(['company/search'], { queryParams: { keyword: keyword } });
  }

  /*@HostListener('document:keypress', ['$event'])
  private keyboardEvent($event){
    if(parseInt($event.keyCode)==13&&!this.router.routerState.snapshot.url.includes('search?keyword=')
       &&!this.router.routerState.snapshot.url.includes('portfolio')){
      this.goToCompanySearch(this.companySearchKeyword);
    }
  }*/

  logout() {
    this.feedService.logout();
  }

  downloadHelpDocument() {
    this.homeService.downloadHelpDocument().subscribe(
      data => {
        const fileName = '帮助文档.pdf';
        if (window.navigator.msSaveBlob) {
          window.navigator.msSaveBlob(data.body, fileName);
        } else {
          let downloadUrl = window.URL.createObjectURL(data.body);
          let downloadLink = document.createElement('a');
          downloadLink.href = downloadUrl;
          downloadLink.download = fileName;
          document.body.appendChild(downloadLink);
          downloadLink.click();
          document.body.removeChild(downloadLink);
        }
      },
      error => {
        console.log('downloadAttachment error:', error);
      }
    );
  }
}



