import { BaseClass } from '../../shared/base-class';

export class FactorValue extends BaseClass {
  value: number | string;
  reportDate: string;
  selectedOptionFormulaCh: string;
}

export class QuanFactor extends BaseClass {
  exposureId: number;
  id: number;
  code: string;
  type: string;
  name: string;
  content: string;
  unit: string;
  format: string;
  formulaCn: string;
  formulaEn: string;
  propertyCode: number;
  categoryCode: number;
  applicationCode: number;
  factorValues: FactorValue[];

  generateByRaw(raw: any) {
    super.generateByRaw(raw);
    this.factorValues = [];
    if (raw.factorValues) {
      for (let item of raw.factorValues) {
        let factorValue = new FactorValue();
        factorValue.generateByRaw(item);
        this.factorValues.push(factorValue);
      }
    }
  }
}

export class IndustryQuanStatistic extends BaseClass {
  code: string;
  date: string;
  name: string;
  unit: string;
  format: string;
  avgValue: number;
  minValue: number;
  maxValue: number;
  midValue: number;
}



