import { BaseClass } from '../base-class';
export class Regulation extends BaseClass {
  id: number;
  name: string;
  desc: string;
}
export class RegulationCondition extends BaseClass {
  name: string;
  filterType: string; // 	0：企业规则，1：债券规则
  desc: string;	// 规则描述
  exposureId: any[]; // 企业/债券所属敞口
  marketNames: any[];	// 上市情况/交易市场
  tradeMarket: any[];
  issueBonds: number;	// 发债情况 1：有发行债券，0：无发行债券
  rating: any[];	// 内部评级
  creditRating: any[];	// 外部评级
  ratingType: any[];	// 评级类型 参考评级、基础评级、认定评级
  couponRate: string[];	// 票面利率 格式：2,3.5 前闭后开，用逗号隔开
  bondType: any[];	// 债券类型
  issueType: any[];	// 债券发行方式 公募发行、私募发行
  issueVolum: string; // 债券发行规模
  type: any[];
}



