import { Component, OnInit, ViewChild, TemplateRef, trigger, state, style, transition, animate } from '@angular/core';
import { Router } from '@angular/router';
import { nvD3 } from '../shared';
import { RateComparitionResult, DefaultPortfolioRating, ContrastItems, ContrastCompany } from './rate-comparition';
import { WarningService } from './warning.service';
import { Portfolio, PortfolioItem, WarningQueryParam, PagedWarning, Warning, Task, Workflow, Ultility, TaskSearchParams } from '../shared';
import { PortfolioService } from '../portfolio/portfolio.service';
import { TaskService } from '../task/task.service';
import { UserService } from '../user/user.service';
import { ModelsService } from '../models/models.service';
declare let d3: any;

@Component({
  selector: 'app-dashboard',
  templateUrl: 'overview-dashboard.component.html',
  styleUrls: ['overview-dashboard.component.css']
})
export class OverviewDashboardComponent implements OnInit {
  innerRateOptionList: string[];
  outerRateOptionList: string[];
  rateComparitionResultList: RateComparitionResult[];
  warningList: Warning[];
  defaltPofolioName: string;

  portfolioList: any[];
  portfolioItems: PortfolioItem[] = [];
  defaultPortfolio: Portfolio;

  warningLoading = true;
  tooltipEnable: boolean;
  tooltipIsOpen: boolean;
  tooltipTrigger: string[];
  industryChartOptions;
  industryChartData;
  ratingChartOptions;
  ratingChartData;

  goodCount: number;
  normalCount: number;
  badCount: number;
  naCount: number;
  industryCount: number;
  ratingContrastListHidden = true;
  ratingContrastList: ContrastCompany[];
  warningDetailState: string;
  activeWarning: Warning;

  closeResult: string;
  taskList: Workflow[];

  taskSearchParams: TaskSearchParams;

  @ViewChild(nvD3) ratingChart: nvD3;
  @ViewChild(nvD3) industryChart: nvD3;

  @ViewChild('cellLinkTemplate') cellLinkTemplate: TemplateRef<any>;
  @ViewChild('cellRatingTemplate') cellRatingTemplate: TemplateRef<any>;
  @ViewChild('cellButtonTemplate') cellButtonTemplate: TemplateRef<any>;

  constructor(private router: Router, private warningService: WarningService,
    private portfolioService: PortfolioService, private taskService: TaskService,
    private modelsService: ModelsService, private userService: UserService) {
    if (!userService.hasPrivilege(userService.companyView)
      && !userService.hasPrivilege(userService.bondView)) {
      if (userService.hasPrivilege(userService.modelView)) {
        router.navigate(['models']);
      } else if (userService.hasPrivilege(userService.userView)) {
        router.navigate(['admin']);
      }
    }
  }

  ngOnInit() {
    this.taskSearchParams = new TaskSearchParams();
    this.tooltipEnable = true;
    this.tooltipIsOpen = false;
    this.warningDetailState = 'out';
    this.activeWarning = new Warning();

    this.initRateComparitionResult();
    this.warningList = [];
    this.getWarnings();
    this.getDefaultPortfolioRatingComparition();
    this.getDefaultPortfolioItems();
    this.getTaskList();
    this.taskList = [];
  }

  initIndustryChart() {
    let map: any = {};
    for (let it of this.portfolioItems) {
      if (!map[it.exposure]) {
        map[it.exposure] = 1;
      } else {
        map[it.exposure] += 1;
      }
    }
    this.industryChartData = [
      {
        key: 'Cumulative Return',
        values: []
      }
    ];
    this.industryCount = 0;
    let totalCount = 0;
    let maxCount = 0;
    for (let it in map) {
      if (map.hasOwnProperty(it)) {
        this.industryChartData[0].values.push({ label: it === 'null' ? '其他' : it, value: map[it] });
        this.industryCount++;
        let cnt = map[it];
        totalCount += cnt;
        if (cnt > maxCount) {
          maxCount = cnt;
        }
      }
    }

    let valueCustom = function () {
      let text = d3.selectAll('#industryChart .nv-bar.positive text');
      text.html(function (d) {
        return '<tspan dy="-1em" font-size="12px" fill="#314970">' + d3.format('.0f')(d.value) + '</tspan>'
          + '<tspan dx="-2em" dy="1em" fill="#1875C7" font-size="12px">'
          + d3.format('.1f')(d.value * 100 / totalCount) + '</tspan><tspan fill="#1875C7" font-size="11px">%</tspan>';
      });
    };

    window.onresize = function () { setTimeout(valueCustom, 500); };

    this.industryChartOptions = {
      chart: {
        type: 'discreteBarChart',
        noData: '默认组合下没有企业',
        height: 450,
        margin: {
          top: 20,
          right: 40,
          bottom: 90,
          left: 55
        },
        color: function (d) { return '#CEE1F1'; },
        x: function (d) { return d.label; },
        y: function (d) { return d.value; },
        showValues: true,
        valueFormat: function (d) {
        },
        duration: 500,
        xAxis: {
          rotateLabels: 30
        },
        yAxis: {
          width: 60,
          tickFormat: d3.format('.0f'),
          ticks: Math.min(5, maxCount)
        },
        dispatch: {
          renderEnd: valueCustom
        }
      }
    };
  }
  initRatingChart(data: DefaultPortfolioRating) {
    let map = {};
    if (data && data.ratingList && Array.isArray(data.ratingList) && data.ratingList.length > 0) {
      for (let it of data.ratingList) {
        let rating = it.innerRating;
        if (!rating) { rating = '无'; }
        if (!map[rating]) {
          map[rating] = 1;
        } else {
          map[rating] += 1;
        }
      }
    }
    let maxCount = 0;
    for (let it in map) {
      if (map.hasOwnProperty(it)) {
        let cnt = map[it];
        if (cnt > maxCount) {
          maxCount = cnt;
        }
      }
    }
    this.ratingChartOptions = {
      chart: {
        type: 'discreteBarChart',
        noData: '默认组合下没有企业',
        height: 450,
        margin: {
          top: 20,
          right: 40,
          bottom: 50,
          left: 55
        },
        color: function (d, i) {
          if (i < 7) return '#61B6B2';
          if (i < 13) return '#FDD33D';
          if (i < 20) return '#E9604C';
          return '#C9CACB';
        },
        x: function (d) { return d.label; },
        y: function (d) { return d.value; },
        showValues: true,
        valueFormat: d3.format('.0f'),
        duration: 500,
        yAxis: {
          tickFormat: d3.format('.0f'),
          ticks: Math.min(5, maxCount)
        }

      }
    };

    this.goodCount = this.innerRateOptionList.slice(0, 7).map(it => map[it] || 0).reduce((a, b) => a + b, 0);
    this.normalCount = this.innerRateOptionList.slice(7, 13).map(it => map[it] || 0).reduce((a, b) => a + b, 0);
    this.badCount = this.innerRateOptionList.slice(13, 20).map(it => map[it] || 0).reduce((a, b) => a + b, 0);
    this.naCount = map['无'] || 0;
    this.ratingChartData = [
      {
        key: 'Cumulative Return',
        values: this.innerRateOptionList.map(it => {
          return { label: it, value: map[it] || 0 };
        })
      }
    ];
    console.log(this.ratingChartData);
    // this.ratingChart.chart.update();
  }

  private initRateComparitionResult() {
    this.innerRateOptionList = this.modelsService.allScales.map(it => it.rating);
    this.innerRateOptionList.push('无');
    this.outerRateOptionList = [
      'AA+以上', 'AA', 'AA-以下', '无', '总计'
    ];
    this.rateComparitionResultList = [];
    let length = this.innerRateOptionList.length;
    for (let item of this.outerRateOptionList) {
      let rateComparitionResult = new RateComparitionResult();
      rateComparitionResult.level = item;
      rateComparitionResult.valueList = [];
      rateComparitionResult.contrastList = [];
      for (let i = 0; i < length; i++) {
        let contrastItems = new ContrastItems();
        contrastItems.contrastCompanyList = [];
        rateComparitionResult.contrastList.push(contrastItems);
        rateComparitionResult.valueList.push(0);
      }
      this.rateComparitionResultList.push(rateComparitionResult);
    }
  }

  generateWarningList(data: PagedWarning) {
    this.warningList = [];
    if (data && data.items && data.items.length > 0) {
      let pagedWarning = new PagedWarning();
      pagedWarning.generateByRaw(data);
      this.warningList = pagedWarning.items;
    }
    this.warningLoading = false;
  }

  generateRatingComparitionResult(data: DefaultPortfolioRating) {
    if (!data || !data.ratingList) {
      return;
    }
    this.defaltPofolioName = data.portFolioName;
    let i = 0;
    let rowMax = this.outerRateOptionList.length - 1;
    let colMax = this.innerRateOptionList.length - 1;
    for (let item of data.ratingList) {
      let companyId = item.companyId;
      let index = rowMax - 1;
      if (item.outRating && item.outRating.trim()) {
        switch (item.outRating.trim()) {
          case 'AAA+':
          case 'AAA':
          case 'Aaa':
          case 'Aa1':
          case 'AA+':
            index = 0;
            break;
          case 'AA':
          case 'AAA-':
          case 'Aa2':
            index = 1;
            break;
          default:
            index = 2;
            break;
        }
      }
      let valueIndex = colMax;
      if (item.innerRating && item.innerRating.trim()) {
        valueIndex = this.innerRateOptionList.indexOf(item.innerRating.trim());
      }
      this.rateComparitionResultList[index].valueList[valueIndex] = this.rateComparitionResultList[index].valueList[valueIndex] + 1;
      this.rateComparitionResultList[rowMax].valueList[valueIndex] = this.rateComparitionResultList[rowMax].valueList[valueIndex] + 1;
      let contrastCompany = new ContrastCompany();
      contrastCompany.id = item.companyId;
      contrastCompany.name = item.companyName;
      contrastCompany.innerRating = item.innerRating;
      contrastCompany.outRating = item.outRating;
      this.rateComparitionResultList[index].contrastList[valueIndex].contrastCompanyList.push(contrastCompany);
      this.rateComparitionResultList[rowMax].contrastList[valueIndex].contrastCompanyList.push(contrastCompany);
      i = i + 1;
    }
  }

  private getWarnings() {
    let params = new WarningQueryParam();
    params.isDefault = 1;
    params.importance = [3, 2];
    let date = new Date();
    params.startNoticeDate = (date.getFullYear() - 2) + '-' + (date.getMonth() + 1) + '-' + date.getDate();
    this.warningService.getWarnings(params).subscribe(
      data => this.generateWarningList(data),
      error => console.log('getWarningAcquiesce error:', error)
    );
  }

  private getDefaultPortfolioRatingComparition() {
    this.portfolioService.getDefaultPofolioRatingComparition().subscribe(
      data => {
        this.generateRatingComparitionResult(data);
        this.initRatingChart(data);
      },
      error => console.log('getDefaultPofolioRatingComparition', error)
    );
  }
  private getDefaultPortfolioItems() {
    this.portfolioService.getPortfolios(0).subscribe(
      data => this.generatePortfolioList(data),
      error => console.log('getPortfolioList error:', error)
    );
  }

  private generatePortfolioList(data: any[]) {
    if (Ultility.arrayIsNotEmpty(data)) {
      for (let item of data) {
        let portfolio = new Portfolio();
        portfolio.generateByRaw(item);
        portfolio.type = '企业';
        if (portfolio.isDefault && portfolio.isDefault === 1) {
          this.defaultPortfolio = new Portfolio();
          this.defaultPortfolio.generateByRaw(portfolio);
          this.getPortfolioItems(this.defaultPortfolio);
        }
      }
    }
  }

  private getPortfolioItems(portfolio: Portfolio) {
    this.portfolioService.getPortfolioItems(portfolio.id).subscribe(
      data => {
        this.generatePortfolioItems(data, portfolio);
        this.initIndustryChart();
      },
      error => console.log('error:', error)
    );
  }

  private generatePortfolioItems(data: any, portfolio: Portfolio) {
    this.portfolioItems = [];
    if (data && data.companies && Array.isArray(data.companies) && data.companies.length > 0) {
      for (let item of data.companies) {
        let portfolioItem = new PortfolioItem();
        portfolioItem.generateByRaw(item);
        this.portfolioItems.push(portfolioItem);
      }
    }
  }

  goToCompany(companyId: number) {
    this.router.navigate(['company/detail', companyId]);
  }

  goToBond(bondId: number) {
    this.router.navigate(['product/bond/detail/', bondId]);
  }

  showRatingContrastList(contrastCompanyList: ContrastCompany[]) {
    console.log('rating contrast list: ', contrastCompanyList);
    this.ratingContrastList = [];
    if (contrastCompanyList && contrastCompanyList.length > 0) {
      this.ratingContrastList = contrastCompanyList;
    }
    this.ratingContrastListHidden = false;
  }

  showWarningDetail(warning: Warning) {
    if (this.warningDetailState === 'in' && warning.id === this.activeWarning.id) {
      this.warningDetailState = 'out';
      return;
    }
    this.warningDetailState = 'out';
    setTimeout(() => {
      this.warningDetailState = 'in';
      this.activeWarning = warning;
    }, 300);
    if (warning.warningContent) {
      return;
    }
    this.warningService.getWarningDetail(warning.id, warning.typeId).subscribe(
      data => {
        warning.warningContent = data.content;
      },
      error => console.log('error:', error)
    );
  }

  private getTaskList() {
    this.taskService.getWaitForProcessTaskList(this.taskSearchParams).subscribe(
      res => {
        this.taskList = [];
        if (res && res.items && Array.isArray(res.items) && res.items.length > 0) {
          for (const item of res.items) {
            const workflow = new Workflow();
            Object.assign(workflow, item);
            this.taskList.push(workflow);
          }
        }
      },
      error => console.log('getTaskList error:', error)
    );
  }

  private processTask(item: Task) {
    switch (item.taskType) {
      case '分析':
        this.router.navigate(['task/company/rating', 'affirm', item.workflowSid]);
        break;
      case '复核':
        this.router.navigate(['task/company/rating', 'approve', item.workflowSid]);
        break;
      case '审核':
        this.router.navigate(['task/company/rating', 'examine', item.workflowSid]);
        break;
      case '预警处理':
        this.router.navigate(['task/warning', item.workflowSid, 0 ]);
        break;
      default:
        break;
    }
  }
}



