import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Headers, RequestOptions } from '@angular/http';

import { User } from './user';

@Injectable()
export class UserService {

  readonly companyView = 'compy_view';
  readonly companyAdd = 'compy_add';
  readonly companyEdit = 'compy_edit';
  readonly companyDelete = 'compy_delete';
  readonly companyRating = 'compy_rating';
  readonly companyRatingApprov = 'compy_rating_approv';
  readonly companyRatingManage = 'compy_rating_manage';
  readonly companyAnalysis = 'compy_analysis';
  readonly bondView = 'bond_view';
  readonly bondAdd = 'bond_add';
  readonly bondEdit = 'bond_edit';
  readonly bondDelete = 'bond_delete';
  readonly bondRating = 'bond_rating';
  readonly bondRatingApprov = 'bond_rating_approv';
  readonly bondRatingManage = 'bond_rating_manage';
  readonly bondAnalysis = 'bond_analysis';
  readonly modelView = 'model_view';
  readonly modelAdd = 'model_add';
  readonly modelEdit = 'model_edit';
  readonly modelDelete = 'model_delete';
  readonly userView = 'user_view';
  readonly userAdd = 'user_add';
  readonly userEdit = 'user_edit';
  readonly userDelete = 'user_delete';
  readonly system = 'system';

  public CurrentUser: User;

  constructor() {
  }
  hasPrivilege(code: string): boolean {
    if (this.CurrentUser && this.CurrentUser.privileges
      && this.CurrentUser.privileges.find) {
      return this.CurrentUser.privileges.find(it => it.code === code) != null;
    }
    return false;
  }

  canViewModel(): boolean {
    return this.hasPrivilege(this.modelView);
  }

  canViewAdmin(): boolean {
    return this.hasPrivilege(this.userView)
      || this.hasPrivilege(this.system);
  }

}




